<template>
  <div class="app-container" v-loading="loading" @click="unModifyPrice">
    <el-card class="box-card">
      <!--内容-->
      <el-tabs v-model="tabsName" @tab-click="changeTabs">
        <el-tab-pane
          v-for="(item, index) in editableTabs"
          :key="index"
          :label="item.content"
          :name="item.id"
        >
          <!--列表-->
          <div class="tab_header_bar">
            <div class="block" style="margin-left: 10px">
              <el-input clearable size="small" v-model="goodsName" placeholder="商品名称/商品编号"></el-input>
            </div>
            <div class="block ml20">
              <el-button size="small" type="primary" @click="selectList">查询</el-button>
            </div>
            <div class="block ml20">
              <el-button size="small" type="primary" @click="getgoodsList">重置</el-button>
            </div>
          </div>
          <div style="width:100%;display: flex;justify-content: flex-end;">
            <el-button @click="toLead" type="primary" size="small">导入商品</el-button>
            <el-button @click="addRule" type="primary" size="small">添加商品</el-button>
          </div>
          <div class="tab_bottom">
            <!--表格-->
            <el-table
              @selection-change="selAll"
              ref="tableData"
              :data="tableData"
              style="width: 100%"
            >
              <el-table-column type="selection" width="55"></el-table-column>
              <el-table-column prop="itemNumber" label="编号" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column label="商品图片" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <img
                    v-if="scope.row.goodsPic"
                    style="width: 60px;height: 60px;"
                    :src="photoUrl+scope.row.goodsPic"
                    class="head_pic"
                  />
                  <img
                    v-else
                    style="width: 60px;height: 60px;"
                    src="../../../assets/images/goods/noIMge.png"
                    class="head_pic"
                  />
                </template>
              </el-table-column>
              <el-table-column prop="goodsName" label="商品名称" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="isSet" label="价格" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-input
                    :key="scope.row.id"
                    style="width:50px"
                    size="mini"
                    v-show="scope.row.isSet"
                    v-model="scope.row.goodsPrice"
                    @click.native.stop
                  ></el-input>
                  <span v-show="!scope.row.isSet">￥{{scope.row.goodsPrice}}</span>
                  <span v-show="!scope.row.isSet" @click.stop="()=>{scope.row.isSet = !scope.row.isSet}">
                    <i style="color:#1087C7;margin:10px;" class="el-icon-edit"></i>
                  </span>
                  <span
                    style="font-size:12px;color:#1087C7"
                    v-show="scope.row.isSet"
                    @click.stop="modifyPrice11(scope.row)"
                  >保存</span>
                </template>
              </el-table-column>
              <el-table-column label="上下架" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-switch
                    v-model="scope.row.isShelf"
                    active-color="#1087C7"
                    inactive-color="#dcdfe6"
                    :active-value="1"
                    :inactive-value="0"
                    @change="switchChange(scope.row)"
                  ></el-switch>
                </template>
              </el-table-column>>
              <el-table-column prop="isSet2" label="库存" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-input
                    :key="scope.row.id"
                    style="width:50px"
                    size="mini"
                    v-show="scope.row.isSet2"
                    v-model="scope.row.stockNum"
                    @click.native.stop
                  ></el-input>
                  <span v-show="!scope.row.isSet2">{{scope.row.stockNum}}</span>
                  <span v-show="!scope.row.isSet2" @click.stop="()=>{scope.row.isSet2 = !scope.row.isSet2}">
                    <i style="color:#1087C7;margin:10px;" class="el-icon-edit"></i>
                  </span>
                  <span
                    style="font-size:12px;color:#1087C7"
                    v-show="scope.row.isSet2"
                    @click.stop="modifyPrice22(scope.row)"
                  >保存</span>
                </template>
              </el-table-column>
              <el-table-column prop="unit" label="计价单位" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="salesNum" label="销量" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column label="操作" width="350px">
                <template slot-scope="scope">
                  <el-button @click="lookOver(scope.row)" type="primary" size="mini">查看</el-button>
                  <el-button @click="compileGoods(scope.row)" type="primary" size="mini">编辑</el-button>
                  <el-button type="primary" size="mini" @click="deleteGoods(scope.row.id)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div style="display: flex;align-items: flex-end;justify-content: space-between;">
              <div class="block">
                <el-checkbox
                  :indeterminate="isIndeterminate"
                  @change="handleCheckAllChange"
                  v-model="quanxuan"
                ></el-checkbox>
                <el-select size="small" v-model="state" placeholder="批量操作" @change="getcao">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
                <div class="block ml20">
                  <el-button size="small" type="primary" @click="setBetch">确定</el-button>
                </div>
              </div>
              <pagination
                :total-elements="tableTotalElement"
                :change-callback="getgoodsList"
                ref="page"
              ></pagination>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <!--模态框-->
      <el-dialog title="新增商品" :visible.sync="editMemberDialogVisible" width="50%">
        <div class="justleft mt">
          <p class="title">
            <span class="red">*</span>
            <span class="weight7">商品名称</span>
          </p>
          <div>
            <el-input v-model="addGoodsList.goodsName" class="ml20" style="width:100%;" size="small" @input="getName" :style="nameBorder"></el-input>
            <p style="height: 0; position: relative; left: 20px; top: 10px; color: red;" v-if="this.name != ''">{{name}}</p>
          </div>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="red">*</span>
            <span class="weight7">条形码</span>
          </p>
          <div>
            <el-input v-model="addGoodsList.goodsCode" class="ml20" style="width:100%;" size="small" @input="getCode" :style="codeBorder"></el-input>
            <p style="height: 0; position: relative; left: 20px; top: 10px; color: red;" v-if="this.code != ''">{{code}}</p>
          </div>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">商品编号</span>
          </p>
          <el-input v-model="addGoodsList.itemNumber" class="ml20" style="width:50%;" size="small"></el-input>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">封面图片</span>
          </p>
          <el-upload
            class="avatar-uploader justcenter ml20"
            style="overflow: hidden;"
            :headers="headers"
            :action="importUrl"
            :show-file-list="false"
            name="files"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img
              style="width: auto; height: auto; max-width: 100%; max-height: 100%;"
              v-if="addGoodsList.goodsPic"
              :src="addGoodsList.goodsPic"
              class="avatar"
            />
            <img
              v-else
              style="width: 60px;height: 60px;"
              src="../../../assets/images/goods/noIMge.png"
              class="head_pic"
            />
            <!-- <i v-else class="el-icon-plus avatar-uploader-icon"></i> -->
          </el-upload>
        </div>
        <p class="center" style="margin:20px">建议尺寸1000*600像素，大小1M以内，支持jpg、png、jpeg格式</p>
        <div class="justcenter mt status">
          <p class="title">
            <span class="red">*</span>
            <span class="weight7 mr20">商品分类</span>
          </p>
          <div :style="parentBorder">
            <el-select v-model="fenleiName" placeholder="请选择" @input="selectChanged">
              <el-option
                v-for="item in fenleidata"
                :key="item.id"
                :label="item.cagegoryName"
                :value="item.id"
              ></el-option>
            </el-select>
            <p style="height: 0; position: relative; top: 5px; color: red;" v-show="this.fenleiName == ''">{{category}}</p>
          </div>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="red">*</span>
            <span class="weight7">商品价格</span>
          </p>
          <div>
            <el-input v-model="addGoodsList.goodsPrice" class="ml20" style="width:40%; margin-right: 5px" size="small" @input="getPrice" :style="priceBorder"></el-input>元
            <p style="height: 0; position: relative; left: 20px; top: 10px; color: red;" v-if="this.price != ''">{{price}}</p>
          </div>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="red">*</span>
            <span class="weight7">库存</span>
          </p>
          <div>
            <el-input v-model="addGoodsList.stockNum" class="ml20" style="width:40%;" size="small"  @input="getStock" :style="stockBorder"></el-input>
            <p style="height: 0; position: relative; left: 20px; top: 10px; color: red;" v-if="this.stock != ''">{{stock}}</p>
          </div>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="red">*</span>
            <span class="weight7">计价单位</span>
          </p>
          <div>
            <el-input v-model="addGoodsList.unit" class="ml20" style="width:40%;" size="small"  @input="getCompany" :style="companyBorder"></el-input>
            <p style="height: 0; position: relative; left: 20px; top: 10px; color: red;" v-if="this.countCompany != ''">{{countCompany}}</p>
          </div>
        </div>
        <div class="justleft mt">
          <el-checkbox class="title mt" v-model="addGoodsList.isShelf">上架</el-checkbox>
        </div>
        <!--***-->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editMemberDialogVisible = false">取 消</el-button>
          <el-button
            @click="saveGoods"
            type="primary"
            v-loading="btnLoading"
            :disabled="btnLoading"
          >保存</el-button>
        </span>
      </el-dialog>
      <el-dialog title="商品详情" :visible.sync="GoodsDialog" width="30%">
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">商品名称</span>
          </p>
          <p class="ml20" style="width:50%;">{{addGoodsList.goodsName}}</p>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">条形码</span>
          </p>
          <p class="ml20" style="width:50%;">{{addGoodsList.goodsCode}}</p>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">商品编号</span>
          </p>
          <p class="ml20" style="width:50%;">{{addGoodsList.itemNumber}}</p>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">封面图片</span>
          </p>
          <img
            v-if="addGoodsList.goodsPic"
            style="width:60px;height:60px;"
            :src="photoUrl+addGoodsList.goodsPic"
            class="avatar ml20"
          />
          <img
            v-else
            style="width: 60px;height: 60px;"
            src="../../../assets/images/goods/noIMge.png"
            class="head_pic"
          />
        </div>
        <div class="justcenter mt status">
          <p class="title">
            <span class="weight7 mr20">商品分类</span>
          </p>
          <p class="ml20" style="width:50%;">{{addGoodsList.goodsCategory}}</p>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">商品价格</span>
          </p>
          <p class="ml20" style="width:50%;">{{addGoodsList.goodsPrice}}</p>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">库存</span>
          </p>
          <p class="ml20" style="width:50%;">{{addGoodsList.stockNum}}</p>
        </div>
        <div class="justleft mt">
          <p class="title">
            <span class="weight7">上下架</span>
          </p>
          <p class="ml20" style="width:50%;">{{addGoodsList.isShelf?'已上架':'未上架'}}</p>
        </div>
        <!--***-->
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import pagination from "@/components/pagination/index";
import { url } from "@/utils/request";
import {
  goodsList,
  addGoods,
  getClassify,
  delGoods,
  delGoods2,
  getcount,
  batchSel
} from "../api/goodsManage";

export default {
  components: { pagination },
  data() {
    return {
      ids: [],
      batchisShelf: "",
      timestamp: new Date().getTime(),
      showInput: false,
      fenleiName: "",
      subjectOptProp: {
        value: "id",
        children: "children",
        label: "cagegoryName"
      },
      open: false,
      GoodsDialog: false,
      quanxuan: false,
      photoUrl: url + "/fms/upload/resource/",
      importUrl: url + "/fms/upload/files_upload/file/user",
      headers: {
        authorized: sessionStorage.token
      },
      goodsName: "",
      putaway: "",
      addGoodsList: {
        unit: "",
        goodsName: "",
        goodsCode: "",
        itemNumber: "",
        goodsPic: "",
        goodsCategory: 1,
        goodsPrice: "",
        stockNum: "",
        isShelf: 0
      },
      options: [
        {
          value: 1,
          label: "全部上架"
        },
        {
          value: 0,
          label: "全部下架"
        },
        {
          value: 3,
          label: "全部删除"
        }
      ],
      editableTabs: [
        {
          id: "1",
          content: "全部"
        },
        {
          id: "2",
          content: "已上架"
        },
        {
          id: "3",
          content: "未下架"
        }
      ],
      state: "",
      isIndeterminate: false,
      onWindowResize: null,
      loading: false, // 页面加载
      btnLoading: false, // 按钮锁定
      tabsName: "1", // 标签页
      headers: {
        authorized: sessionStorage.token
      },
      searchForm: {
        memberNum: "",
        startTime: "",
        endTime: "",
        phone: ""
      }, // 搜索条件
      tableData: [], // 表格数据
      tableTotalElement: 0, // 表格总条数
      goodsImage: "",
      fenleidata: [],
      // 模态框
      editMemberDialogVisible: false, // 编辑模态框
      TabNum: [],
      editForm: {
        id: "",
        nickname: "",
        phone: "",
        sex: "",
        birthday: ""
      },
      name: '',
      code: '',
      price: '',
      stock: '',
      countCompany: '',
      category: '',
      nameBorder: {
        border: '',
        borderRadius: ''
      },
      codeBorder:{
        border: '',
        borderRadius: ''
      },
      priceBorder:{
        border: '',
        borderRadius: ''
      },
      stockBorder:{
        border: '',
        borderRadius: ''
      },
      companyBorder:{
        border: '',
        borderRadius: ''
      },
      parentBorder: {
        border: '',
        borderRadius: ''
      },
      pageNumber: 1,
      pageSize: 10
    };
  },
  beforeDestroy() {
    if (this.onWindowResize) {
      window.removeEventListener("resize", this.onWindowResize);
    }
  },
  filters: {},
  created() {
    this.getgoodsList();
    this.getfenlei();
    this.getTabNum();
  },
  methods: {
    setBetch() {
      if (this.batchisShelf != "") {
        batchSel(this.ids, this.batchisShelf).then(response => {
          this.$message({
            message: response.msg,
            type: "success"
          });
          this.getgoodsList();
          this.getTabNum();
        }).catch(() => {});
      } else {
        // 删除
        this.$confirm("确定要删除所选商品吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(()=>{
          delGoods2(this.ids).then(response => {
            this.$message({
              message: response.msg,
              type: "success"
            });
            this.getgoodsList();
            this.getTabNum();
          }).catch(() => {});
        }).catch(()=>{});
      };
    },
    getcao(val) {
      if (val === 1) {
        this.batchisShelf = 1;
      } else if (val === 0) {
        this.batchisShelf = 0;
      } else {
      }
    },
    selAll(selection, index) {
      selection.forEach(item => {
        let newId = this.ids.indexOf(item.id);
        if (newId === -1) {
          this.ids.push(item.id);
        }
      });
      if (selection.length === this.tableData.length) {
        // 全选状态
        this.quanxuan = true;
        this.isIndeterminate = false;
      } else {
        this.quanxuan = false;
        if (selection.length > 0) {
          // 部分选中状态
          this.isIndeterminate = true;
        } else {
          // 未选中状态
          this.isIndeterminate = false;
        }
      }
    },
    handleCheckAllChange() {
      this.$refs.tableData[0].toggleAllSelection();
      this.isIndeterminate = !this.isIndeterminate;
    },
    toLead() {
      this.$router.push({ path: "/goodsManage/views/importRecords" });
    },
    modifyPrice1(row) {
      row.isSet = !row.isSet;
    },
    modifyPrice11(row) {
      row.isSet = !row.isSet;
      this.addGoodsList = row;
      if (!row.isSet) {
        addGoods(this.addGoodsList).then((response)=>{
          this.$message({
            message: response.msg,
            type: "success"
          });
          goodsList(this.goodsName, this.putaway, this.pageNumber, this.pageSize).then((response)=>{
            this.getTabNum();
          }).catch((response)=>{});
        }).catch((response)=>{});
      }
    },
    modifyPrice2(row) {
      row.isSet2 = !row.isSet2;
    },
    modifyPrice22(row) {
      row.isSet2 = !row.isSet2;
      this.addGoodsList = row;
      if (!row.isSet2) {
        addGoods(this.addGoodsList).then((response)=>{
          this.$message({
            message: response.msg,
            type: "success"
          });
          goodsList(this.goodsName, this.putaway, this.pageNumber, this.pageSize).then((response)=>{
            this.getTabNum();
          }).catch((response)=>{});
        }).catch((response)=>{});
      };
    },
    unModifyPrice() {
      this.tableData.forEach(item => {
        item.isSet = false;
        item.isSet2 = false;
      });
    },
    getTabNum() {
      getcount()
        .then(response => {
          // console.log(response)
          this.TabNum = response.obj;
          this.editableTabs.forEach(item => {
            if (item.id === "1") {
              // console.log();
              item.content = "全部" + "(" + this.TabNum.all + ")";
            }
            if (item.id === "2") {
              item.content = "已上架" + "(" + this.TabNum.isShelf + ")";
            }
            if (item.id === "3") {
              item.content = "未上架" + "(" + this.TabNum.noShelf + ")";
            }
          });
        })
        .catch(() => {});
    },
    //上下架商品
    switchChange(row) {
      this.addGoodsList = row;
      this.addGoodsList.goodsPic = row.goodsPic;
      // this.saveGoods();
      addGoods(this.addGoodsList).then((response)=>{
          this.$message({
            message: response.msg,
            type: "success"
          });
        }).catch((response)=>{
          this.$message({
              message: response.msg,
              type: "success"
            });
        });
      if(row.isShelf == 0){
        goodsList(this.goodsName, this.putaway, this.pageNumber, this.pageSize).then((response)=>{
          this.getTabNum();
        }).catch((response)=>{});
      }else{
        goodsList(this.goodsName, this.putaway, this.pageNumber, this.pageSize).then((response)=>{
          this.getTabNum();
        }).catch((response)=>{});
      };
    },
    deleteGoods(id) {
      this.$confirm("确定要删除该商品吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        // 删除
        delGoods(id).then(response => {
          this.$message({
            message: response.msg,
            type: "success"
          });
          this.getgoodsList();
          this.getTabNum();
        }).catch(() => {});
      }).catch(() => {});
    },
    getfenlei() {
      getClassify()
      .then(response => {
        this.fenleidata = response.obj;
      })
      .catch(() => {});
    },
    lookOver(row) {
      this.addGoodsList = row;
      this.GoodsDialog = true;
    },
    compileGoods(row) {
      // console.log(row);
      this.addGoodsList = row;
      this.editMemberDialogVisible = true;
      if (this.addGoodsList.goodsPic) {
        let newImage = this.addGoodsList.goodsPic.indexOf(this.photoUrl);
        if (newImage === -1) {
          this.addGoodsList.goodsPic =
            this.photoUrl + this.addGoodsList.goodsPic;
        } else {
          this.addGoodsList.goodsPic = this.addGoodsList.goodsPic;
        };
      };
      this.fenleiName = row.goodsCategory;
      getClassify().then((response)=>{}).catch(()=>{});
    },
    // 查询
    selectList() {
      this.getgoodsList();
      this.goodsName = "";
    },
    //保存
    saveGoods() {
      var name = this.addGoodsList.goodsName;
      var code = this.addGoodsList.goodsCode;
      var price = this.addGoodsList.goodsPrice;
      var stock = this.addGoodsList.stockNum;
      var company = this.addGoodsList.unit;
      var classification = this.fenleiName;
      if(name != '' && code != '' && price != '' && stock != '' && company != undefined && classification != ''){
        this.addGoodsList.goodsPic = this.goodsImage;
        if (!this.addGoodsList.isShelf) {
          this.addGoodsList.isShelf = 0; //未上架
        } else {
          this.addGoodsList.isShelf = 1;
        };
        addGoods(this.addGoodsList)
        .then(response => {
          this.$message({
            message: response.msg,
            type: "success"
          });
          this.editMemberDialogVisible = false;
          this.getTabNum();
          this.getgoodsList();
        })
        .catch(() => {});
      }else{
        if(name == ''){
          this.name = '请输入商品名称';
          this.nameBorder.borderRadius = '5px';
          this.nameBorder.border = '1px solid red';
        }
        if(code == ''){
          this.code = '请输入条形码';
          this.codeBorder.borderRadius = '5px';
          this.codeBorder.border = '1px solid red';
        }
        if(price == ''){
          this.price = '请输入商品价格';
          this.priceBorder.borderRadius = '5px';
          this.priceBorder.border = '1px solid red';
        }
        if(stock == ''){
          this.stock = '请输入商品库存';
          this.stockBorder.borderRadius = '5px';
          this.stockBorder.border = '1px solid red';
        }
        if(company == undefined){
          this.countCompany = '请输入计价单位';
          this.companyBorder.borderRadius = '5px';
          this.companyBorder.border = '1px solid red';
        }
        if(classification == ''){
          this.category = '请选择分类'
          this.parentBorder.borderRadius = '5px';
          this.parentBorder.border = '1px solid red';
        }
      }
    },
    //获取列表
    getgoodsList(pageNumber, pageSize) {
      goodsList(this.goodsName, this.putaway, pageNumber, pageSize)
        .then(response => {
          let data = response.obj;
          data.content = data.content.map(i => {
            i.isSet = false;
            i.isSet2 = false;
            return i;
          });
          this.tableData = data.content;
          this.tableTotalElement = data.totalElements;
          // console.log(this.tableData, "tableData2");
        })
        .catch(() => {});
    },
    handleAvatarSuccess(res, file) {
      this.goodsImage = res.obj;
      this.addGoodsList.goodsPic = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    /**
     * 切换标签页
     */
    changeTabs: function(tab, event) {
      // console.log(tab.name, "tab");
      if (tab.name === "1") {
        this.putaway = "";
        this.getgoodsList();
      }
      if (tab.name === "2") {
        this.putaway = 1;
        this.getgoodsList();
      }
      if (tab.name === "3") {
        this.putaway = 0;
        this.getgoodsList();
      }
    },
    //   添加商品
    addRule() {
      this.addGoodsList = {
        goodsName: "",
        goodsCode: "",
        itemNumber: "",
        goodsPic: "",
        goodsCategory: 1,
        goodsPrice: "",
        stockNum: "",
        isShelf: 0,
        industryCategory: 0
      };
      this.fenleiName = "";
      this.editMemberDialogVisible = true;
      this.name = '';
      this.nameBorder.borderRadius = '';
      this.nameBorder.border = '';
      this.code = '';
      this.codeBorder.borderRadius = '';
      this.codeBorder.border = '';
      this.price = '';
      this.priceBorder.borderRadius = '';
      this.priceBorder.border = '';
      this.stock = '';
      this.stockBorder.borderRadius = '';
      this.stockBorder.border = '';
      this.countCompany = '';
      this.companyBorder.borderRadius = '';
      this.companyBorder.border = '';
      this.category = '';
      this.parentBorder.borderRadius = '';
      this.parentBorder.border = '';
    },
    getName(e){
      if(e == ''){
        this.name = '请输入商品名称';
        this.nameBorder.borderRadius = '5px';
        this.nameBorder.border = '1px solid red';
      }else{
        this.name = '';
        this.nameBorder.borderRadius = '';
        this.nameBorder.border = '';
      }
    },
    getCode(e){
      if(e == ''){
        this.code = '请输入条形码';
        this.codeBorder.borderRadius = '5px';
        this.codeBorder.border = '1px solid red';
      }else{
        this.code = '';
        this.codeBorder.borderRadius = '';
        this.codeBorder.border = '';
      }
    },
    getPrice(e){
      if(e == ''){
        this.price = '请输入商品价格';
        this.priceBorder.borderRadius = '5px';
        this.priceBorder.border = '1px solid red';
      }else{
        this.price = '';
        this.priceBorder.borderRadius = '';
        this.priceBorder.border = '';
      }
    },
    getStock(e){
      if(e == ''){
        this.stock = '请输入商品库存';
        this.stockBorder.borderRadius = '5px';
        this.stockBorder.border = '1px solid red';
      }else{
        this.stock = '';
        this.stockBorder.borderRadius = '';
        this.stockBorder.border = '';
      }
    },
    getCompany(e){
      if(e == ''){
        this.countCompany = '请输入计价单位';
        this.companyBorder.borderRadius = '5px';
        this.companyBorder.border = '1px solid red';
      }else{
        this.countCompany = '';
        this.companyBorder.borderRadius = '';
        this.companyBorder.border = '';
      }
    },
    selectChanged(e){
      this.addGoodsList.goodsCategory = e;
      if(e == ''){
        this.category = '请选择分类';
        this.parentBorder.borderRadius = '5px';
        this.parentBorder.border = '1px solid red';
      }else{
        this.category = '';
        this.parentBorder.borderRadius = '';
        this.parentBorder.border = '';
      }
    }
  }
};
</script>

<style scoped>
.avatar-uploader {
  width: 80px;
  height: 80px;
  border: 1px dashed #ccc;
}
.title {
  width: 120px;
  text-align: right;
}
/*public*/
.status {
  display: flex;
  justify-content: flex-start;
  padding: 0 20px;
}
.nodata {
  position: relative;
  left: 0;
  top: -20px;
  width: 100%;
  height: 100%;
  background-color: #3a3a3a;
}

.block {
  display: inline-block;
  vertical-align: middle;
}

/*private*/
.tab_header_bar {
  /* margin-bottom: 20px; */
}

.tab_bottom {
}

/*卡片*/
.card_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}

.card_item {
  padding: 0;
  width: 15%;
  max-width: 100%;
  margin: 20px 1% 0 1%;
}

.tools_box {
  padding: 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.tools_img {
  width: 30%;
  text-align: left;
}

.tools_text {
  width: 70%;
  text-align: right;
}

.tools_text p {
  margin: 0;
  padding: 0;
  cursor: default;
}

.tools_text p:nth-child(1) {
  font-size: 12px;
  color: #888;
}

.tools_text p:nth-child(2) {
  padding: 5px 0 0 0;
  font-size: 20px;
  color: #3f3f3f;
}

/*图表*/
.chart_img {
  width: 100%;
  height: 400px;
}

.lineTwo_chart_img {
  width: 50%;
  height: 400px;
}

.lineTwo_chart_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.analysisBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.analysis_table {
  border: 1px solid #cdcdcd;
  font-size: 14px;
}

.analysis_table thead tr {
  font-weight: bold;
  height: 24px;
  line-height: 28px;
  text-align: left;
}

.analysis_table tbody tr td {
  color: #718195;
}

.gender_bar_box {
}

.gender_bar_item {
  margin: 0 auto;
  width: 50%;
}

.bar_content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bar_title p {
  padding: 0;
  font-size: 16px;
  text-align: center;
}

.bar_icon_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.bar_icon {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.icon_img {
}

.bar_proportion {
  height: 20px;
  width: 100%;
  background-color: #ffac21;
}

.girl_mark {
  position: relative;
  top: 0;
  left: 0;
  height: 20px;
  background-color: #81be94;
}
span {
  font-size: 14px;
}
</style>



