<template>
  <div class="app-container" v-loading="loading">
    <!--内容-->
    <!--列表-->
    <div style="width:100%;display: flex;justify-content: flex-end;">
      <el-button @click="addjunior" type="primary" size="small">添加</el-button>
    </div>
    <div class="tab_bottom">
      <!--表格-->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="sort" label="排序" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="cagegoryName" label="分类名称" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="goodsNum" label="商品数量" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{scope.row.goodsNum}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="text" size="mini" @click="editaddjunior(scope.row)">编辑</el-button>
            <el-button type="text" size="mini" @click="deleteClassify(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <pagination
        :total-elements="tableTotalElement"
        :change-callback="getcategoryList"
        ref="page"
      ></pagination>
    </div>

    <!--模态框-->
    <el-dialog title="添加分类" :visible.sync="DialogVisible" width="30%">
      <div class="justleft mt">
        <p class="title">
          <span class="red">*</span>
          <span class="weight7">分类名称</span>
        </p>
        <div style="width:50%; margin-left: 20px;">
          <el-input v-model="fenleiList.cagegoryName" class="ml20" style="width:100%; margin: 0;" size="small" @input="getName" :style="nameBorder"></el-input>
          <div style="height: 0; position: relative; top: 6px; color: red;" v-if="this.name != ''">{{name}}</div>
        </div>
        <span class="ml20" style="line-height: 34px;">20字</span>
      </div>
      <div class="justleft mt">
        <p class="title">
          <span class="red">*</span>
          <span class="weight7">排序</span>
        </p>
        <el-input v-model="fenleiList.sort" class="ml20" style="width:25%;" size="small" @input="getSort" :style="sortBorder"></el-input>
      </div>
      <p style="padding-left: 97px;margin: 20px;">1以上整数越小越靠前</p>
      <p style="height: 0; position: relative; left: 120px; top: -10px; color: red;" v-if="this.sortNum != ''">{{sortNum}}</p>
      <!--***-->
      <span slot="footer" class="dialog-footer">
        <el-button @click="DialogVisible = false; empty()" size="mini">取 消</el-button>
        <el-button
          @click="addfenlei"
          type="primary"
          v-loading="btnLoading"
          :disabled="btnLoading"
          size="mini"
        >保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import pagination from '@/components/pagination/index'
import { url } from '@/utils/request'
import { addclassify, categoryList, declassify } from '../api/category'

export default {
  name: 'memberList',
  components: { pagination },
  data () {
    return {
      parentName: '',
      open: false,
      options: [
        {
          value: 1,
          label: '开启'
        },
        {
          value: 2,
          label: '禁用'
        }
      ],
      value3: '',
      editMemberClose: false,
      state: '',
      onWindowResize: null,
      loading: false, // 页面加载
      btnLoading: false, // 按钮锁定
      tabsName: '1', // 标签页
      importUrl: url + '/member/member/member_import',
      headers: {
        authorized: sessionStorage.token
      },
      searchForm: {
        memberNum: '',
        startTime: '',
        endTime: '',
        phone: ''
      }, // 搜索条件
      tableData: [], // 表格数据
      tableData2: [], // 表格数据
      tableTotalElement: 0, // 表格总条数
      tableTotalElement2: 0,
      fenleiList: {
        cagegoryName: '',
        sort: '',
        parentId: 0
      },
      // 模态框
      DialogVisible: false, // 编辑模态框
      DialogVisible2: false, // 编辑模态框
      editForm: {
        id: '',
        nickname: '',
        phone: '',
        sex: '',
        birthday: ''
      },
      dayRange: '0', // 日期范围
      analysisSearchForm: {
        startTime: '',
        endTime: ''
      }, // 会员分析搜索条件
      analysisCardData: {
        allMember: 0,
        newMember: 0
      },
      genderProportion: {
        newMemberTotal: 0,
        newMemberGirl: 0,
        newMemberBoy: 0,
        nBp: 0,
        nGp: 0,
        totalMemberTotal: 0,
        totalMemberGirl: 0,
        totalMemberBoy: 0,
        tBp: 0,
        tGp: 0
      },
      parentId: 0,
      lineOneChartTab: '1',
      lineOneDataNull: true, // 上图表无数据
      lineTwoDataNull: true, // 下图表无数据

      // 会员活跃度分析
      activityData: [{ name: '暂无数据', num: '暂无数据' }], // 下图表数据
      activityDataTotal: 0,
      addDialogVisible: false,
      xiaId: '',
      number: 1,
      pageSize: 10,
      name: '',
      sortNum: '',
      // parent: '',
      nameBorder: {
        border: '',
        borderRadius: ''
      },
      sortBorder: {
        border: '',
        borderRadius: ''
      }
    }
  },
  beforeDestroy () {
    if (this.onWindowResize) {
      window.removeEventListener('resize', this.onWindowResize)
    }
  },
  created () {
    this.getcategoryList();
    if(this.DialogVisible == false || this.addDialogVisible == false){
      this.name = '';
      this.nameBorder.borderRadius = '';
      this.nameBorder.border = '';
      this.sortNum = '';
      this.sortBorder.borderRadius = '';
      this.sortBorder.border = '';
    }
  },
  filters: {},
  methods: {
    deleteClassify (row) {
      console.log(row);
      if(row.goodsNum > 0){
        declassify(row.id)
          .then(response => {
            this.$message({
              message: response.msg,
              type: 'success'
            })
          this.getcategoryList()
          this.xiaList()
        })
        .catch(() => {})
      }else{

      }
      
    },
    selectName (val) {
      console.log(val, 'val')
      this.fenleiList.parentId = val
    },
    // 新增分类
    addfenlei () {
      if(this.fenleiList.cagegoryName != '' && this.fenleiList.sort != ''){
        addclassify(this.fenleiList)
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.DialogVisible = false
          this.addDialogVisible = false
          this.getcategoryList()
          this.xiaList()
        })
        .catch((res) => {
          console.log('失败',res)
        })
      }else{
        if(this.fenleiList.cagegoryName == ''){
          this.name = '请输入分类名称';
          this.nameBorder.borderRadius = '5px';
          this.nameBorder.border = '1px solid red';
        }
        if(this.fenleiList.sort == ''){
          this.sortNum = '请输入排序';
          this.sortBorder.borderRadius = '5px';
          this.sortBorder.border = '1px solid red';
        }
      }
    },
    // 列表
    getcategoryList (pageNumber, pageSize) {
      categoryList(0, pageNumber, pageSize)
        .then(response => {
          let data = response.obj
          this.tableData = data.content
          this.tableTotalElement = data.totalElements
        })
        .catch(() => {})
    },
    // 查看
    seJunior (row) {
      // console.log(row,'row');
      this.xiaId = row.id
      this.se_parentId = row.id
      this.xiaList()
      this.DialogVisible2 = true
    },
    // 下级列表
    xiaList () {
      categoryList(this.se_parentId)
        .then(response => {
          let data = response.obj
          this.tableData2 = data.content
          this.tableTotalElement2 = data.totalElements
        })
        .catch(() => {})
    },
    editaddjunior (row) {
      this.fenleiList = row
      this.DialogVisible = true
    },
    //   添加
    addjunior () {
      this.fenleiList = { cagegoryName: '', sort: '', parentId: 0, industryCategory: 1};
      this.DialogVisible = true;
      // this.parent = '';
      // this.parentBorder.borderRadius = '';
      // this.parentBorder.border = '';
      this.name = '';
      this.nameBorder.borderRadius = '';
      this.nameBorder.border = '';
      this.sortNum = '';
      this.sortBorder.borderRadius = '';
      this.sortBorder.border = '';
    },
    getName(e){
      if(e == ''){
        this.name = '请输入分类名称';
        this.nameBorder.borderRadius = '5px';
        this.nameBorder.border = '1px solid red';
      }else{
        this.name = '';
        this.nameBorder.borderRadius = '';
        this.nameBorder.border = '';
      }
    },
    getSort(e){
      if(e == ''){
        this.sortNum = '请输入排序';
        this.sortBorder.borderRadius = '5px';
        this.sortBorder.border = '1px solid red';
      }else{
        this.sortNum = '';
        this.sortBorder.borderRadius = '';
        this.sortBorder.border = '';
      }
    },
    empty(){
      this.name = '';
      this.nameBorder.borderRadius = '';
      this.nameBorder.border = '';
      this.sortNum = '';
      this.sortBorder.borderRadius = '';
      this.sortBorder.border = '';
      // this.parent = '';
      // this.parentBorder.borderRadius = '';
      // this.parentBorder.border = '';
    }
  }
}
</script>

<style scoped>
.title {
  width: 100px;
  text-align: right;
}
/*public*/
.status {
  display: flex;
  justify-content: flex-start;
  padding: 0 20px;
}
.nodata {
  position: relative;
  left: 0;
  top: -20px;
  width: 100%;
  height: 100%;
  background-color: #3a3a3a;
}

.block {
  display: inline-block;
  vertical-align: middle;
}

/*private*/
.tab_header_bar {
  /* margin-bottom: 20px; */
}

/*卡片*/
.card_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}

.card_item {
  padding: 0;
  width: 15%;
  max-width: 100%;
  margin: 20px 1% 0 1%;
}

.tools_box {
  padding: 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.tools_img {
  width: 30%;
  text-align: left;
}

.tools_text {
  width: 70%;
  text-align: right;
}

.tools_text p {
  margin: 0;
  padding: 0;
  cursor: default;
}

.tools_text p:nth-child(1) {
  font-size: 12px;
  color: #888;
}

.tools_text p:nth-child(2) {
  padding: 5px 0 0 0;
  font-size: 20px;
  color: #3f3f3f;
}

/*图表*/
.chart_img {
  width: 100%;
  height: 400px;
}

.lineTwo_chart_img {
  width: 50%;
  height: 400px;
}

.lineTwo_chart_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.analysisBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.analysis_table {
  border: 1px solid #cdcdcd;
  font-size: 14px;
}

.analysis_table thead tr {
  font-weight: bold;
  height: 24px;
  line-height: 28px;
  text-align: left;
}

.analysis_table tbody tr td {
  color: #718195;
}

.gender_bar_item {
  margin: 0 auto;
  width: 50%;
}

.bar_content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bar_title p {
  padding: 0;
  font-size: 16px;
  text-align: center;
}

.bar_icon_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.bar_icon {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.bar_proportion {
  height: 20px;
  width: 100%;
  background-color: #ffac21;
}

.girl_mark {
  position: relative;
  top: 0;
  left: 0;
  height: 20px;
  background-color: #81be94;
}
span {
  font-size: 14px;
}
</style>



