<template>
  <div class="app-container"
       v-loading="loading">
    <el-card class="box-card">
      <!--标签页-->
      <el-tabs v-model="tabsNum">
        <el-tab-pane label="积分规则"
                     key="0"
                     name="0">
          <div>
            <el-card class="box-card">
              <div slot="header"
                   class="clearfix">
                <el-tooltip class="item"
                            effect="light"
                            content="积分可通过消费或赠送获得，累计后可兑换商品/服务等。"
                            placement="right">
                  <span class="card_title">
                    积分规则
                    <i class="el-icon-question"></i>
                  </span>
                </el-tooltip>
              </div>
              <div class="point_rule_box flex">
                <div class="flex">
                  <p>每消费：</p>
                  <el-input size="small"
                            class="rule_text"
                            v-model="integralData.consumptionAmount">
                  </el-input>
                  元
                </div>
                <div class="flex">
                  <p>赠送：</p>
                  <el-input size="small"
                            class="rule_text"
                            v-model="integralData.credits">
                  </el-input>
                  积分
                </div>
              </div>
            </el-card>

            <el-card class="box-card"
                     style="margin-top: 20px">
              <div slot="header"
                   class="clearfix">
                <span class="card_title">其他设置</span>
              </div>
              <div class="point_rule_box">
                <template>
                  <el-form :inline="true"
                           class="point_rule_inline">
                    <el-form-item label="储值消费是否积分："
                                  label-width="200px">
                      <el-radio v-model="integralData.isTrue"
                                :label="0">不积分
                      </el-radio>
                      <el-radio v-model="integralData.isTrue"
                                :label="1">积分
                      </el-radio>
                    </el-form-item>
                  </el-form>
                </template>
              </div>
            </el-card>
          </div>
          <div style="margin-top: 20px;text-align: center">
            <!--尝试过渡-->
            <transition name="fade"
                        mode="out-in">
              <div key="mode1">
                <el-button type="primary"
                           size="small"
                           @click="savePointsRuler">修 改
                </el-button>
              </div>
            </transition>
          </div>
          <!--************************************-->
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>
<script>
import { findPointsRuler, savePointsRuler } from '../api/pointsRule'

export default {
  name: 'pointsRule',
  data () {
    return {
      integralData: {
        consumptionAmount: 0,
        credits: 0,
        isTrue: 0,
        id: ''
      },
      loading: false,
      tabsNum: 0,
      pointeRuleid: ''
    }
  },

  created () {
    this.findPointsRuler()
  },
  methods: {
    savePointsRuler () {
      this.integralData.id = this.pointeRuleid
      savePointsRuler(this.integralData)
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.findPointsRuler()
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    findPointsRuler () {
      findPointsRuler()
        .then(response => {
          this.pointeRuleid = response.obj.id // 赋值id
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    }
  }
}
</script>

<style scoped>
.el-input {
  width: 30%;
  margin: 0 10px;
}
.flex {
  display: flex;
  align-items: center;
  /* margin-right: 10px; */
}
/*public*/
.block {
  display: inline-block;
  vertical-align: middle;
}
.card_title {
  font-size: 14px;
}

.point_rule_box {
  padding: 0;
  justify-content: flex-start;
}

.point_rule_inline {
  padding: 0;
}

.point_rule_inline .point_rule_inline {
  margin-bottom: 0 !important;
}

.rule_text {
  color: #23c722;
}

.item_name {
  margin-left: 8px;
}

.item-img-name {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.item-img-name img {
  height: 60px;
  width: auto;
}

.item-img-name img {
  border-radius: 4px;
}

.item-img-name span:hover {
  color: #409eff;
}
</style>



