<template>
  <div class="app-container"
       v-loading="loading">
    <div class="filter-container">
      <el-input style="width: 200px"
                class="filter-item"
                placeholder="设备号/门店/商家/上"
                v-model="queryList.keyWords"
                @keyup.enter.native="search"
                size="medium">
      </el-input>
      <el-select style="width:200px;margin-right:15px"
                 v-model="queryList.type"
                 placeholder="请选择"
                 size="medium">
        <el-option v-for="item in deviceTypeOption"
                   :key="item.value"
                   :label="item.name"
                   :value="item.value">
        </el-option>
      </el-select>
      <el-button type="primary"
                 class="filter-item"
                 size="medium"
                 @click="search">查询
      </el-button>
    </div>
    <el-table :data="tableData"
              style="width: 100%">
      <el-table-column prop="deviced"
                       label="设备号">
      </el-table-column>
      <el-table-column prop="deviceType"
                       label="设备类型">
      </el-table-column>
      <el-table-column prop="orderCount"
                       label="交易成功笔数">
      </el-table-column>
      <el-table-column prop="actPayPrice"
                       label="交易金额">
      </el-table-column>
      <el-table-column prop="storeName"
                       label="所属门店">
      </el-table-column>
      <el-table-column prop="mode"
                       label="收银模式">
        <template slot-scope="scope">
          <el-button type="text"
                     v-if="scope.row.mode==1"
                     @click="openModeDialog(scope.row)">收银
          </el-button>
          <el-button type="text"
                     v-if="scope.row.mode==2"
                     @click="openModeDialog(scope.row)">收银+押金
          </el-button>
          <el-button type="text"
                     v-if="scope.row.mode==3"
                     @click="openModeDialog(scope.row)">收银+结算
          </el-button>
          <el-button type="text"
                     v-if="scope.row.mode==4"
                     @click="openModeDialog(scope.row)">收银+押金+结算
          </el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text"
                     @click="waterList(scope.row)">流水清单</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination :total-elements="totalElements"
                :change-callback="getDeviceList"
                ref="page">
    </pagination>
    <el-dialog title="收银模式配置"
               :visible.sync="createDialog"
               :before-close="closemodeDialog"
               width="40%"
               class="dialog">
      <el-form class="form">
        <el-form-item label="当前收银模式：">
          <div v-if="shebei==1">收银</div>
          <div v-if="shebei==2">收银+押金</div>
          <div v-if="shebei==3">收银+结算</div>
          <div v-if="shebei==4">收银+押金+结算</div>
        </el-form-item>
        <el-form-item label="修改收银模式："
                      class="form-item">
          <div class="form-radio">
            <el-radio-group class="radio-group"
                            v-model="radio">
              <el-radio style="padding:15px"
                        :label="1">收银</el-radio>
              <el-radio style="padding:15px"
                        :label="2">收银+押金</el-radio>
              <el-radio style="padding:15px"
                        :label="3">收银+结算</el-radio>
              <el-radio style="padding:15px"
                        :label="4">收银+押金+结算</el-radio>
            </el-radio-group>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="closemodeDialog">取消</el-button>
        <el-button type="primary"
                   @click="editModeSubmit">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getDevice, geteditModeSubmit } from '../api/device'

import pagination from '@/components/pagination/index'
export default {
  name: 'deviceList',
  components: { pagination },
  data () {
    return {
      id: '',
      shebei: [],
      radio: 1,
      // 模态框，修改模态框
      createDialog: false,
      loading: false,
      totalElements: 0, // 数据条数
      queryList: {
        keyWords: '',
        type: ''
      }, // 搜索数据
      tableData: [],
      deviceTypeOption: [{ value: 1, name: '支付宝' }, { value: 2, name: '微信' }]
    }
  },
  mounted () {
    this.getDeviceList()
  },
  methods: {
    search () {
      this.$refs.page.search()
    },
    // 打开收银模式模态框
    openModeDialog (row) {
      // console.log(row, "row")
      this.shebei = row.mode
      this.radio = row.mode
      this.id = row.id
      this.createDialog = true
    },
    // 取消关闭收银模式模态框
    closemodeDialog: function () {
      this.createDialog = false
    },
    // 提交修改收银模式，并关闭模态框
    editModeSubmit: function () {
      // console.log('123')
      this.loading = true
      this.createDialog = false
      geteditModeSubmit(this.radio, this.id).then(response => {
        this.getDeviceList()
        setTimeout(() => {
          this.loading = false
        }, 500)
      }).catch(() => {
        this.loading = false
      })
    },
    // 导出的方法
    exportExcel () {
      this.$confirm('是否导出设备列表？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const tHeaders = ['设备号', '设备类型', '交易成功笔数', '交易金额', '所属门店', '收银模式']
        const filterVals = ['deviced', 'deviceType', 'orderCount', 'actPayPrice', 'storeName', 'mode']
        const datas = this.tableData
        const jsons = this.formatJson(filterVals, datas)
        getExcel(tHeaders, jsons)
      })
    },
    formatJson (filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
    // 获取设备列表
    getDeviceList (number, pageSize) {
      this.loading = true
      getDevice(number, pageSize, this.queryList).then(response => {
        let data = response.obj
        this.totalElements = data.totalElements
        data.content.forEach(element => {
          switch (element.deviceType) {
            case 1:
              element.deviceType = '支付宝'
              break
            case 2:
              element.deviceType = '微信'
              break
          }
        })
        this.tableData = data.content
        setTimeout(() => {
          this.loading = false
        }, 500)
      }).catch(() => {
        this.loading = false
      })
    },
    // 跳转到流水清单
    waterList (data) {
      let id = data.id
      this.$router.push({ path: '/deviceManage/views/deviceDetail', query: { id: id } })
    }
  }
}
</script>

<style scoped>
.filter-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 20px 0;
}
body .el-table th.gutter {
  display: table-cell !important;
}
.el-select {
  margin-right: 5px;
}
.form {
  display: block;
}
.radio-group {
  display: flex;
  flex-direction: column;
}
</style>



