import request from '@/utils/request'
import qs from 'qs'

//获取表格数据
export function getTableList (pageNumber, pageSize, searchData) {
  return request({
    url: '/merchant/store/list_rewrite',
    method: 'get',
    params: {
      pageNumber,
      pageSize,
      name: searchData.name,
      status: searchData.status,
    }
  })
}

// 登录账号是商户还是店长
export function storeDetail (id) {
  return request({
    url: '/merchant/store/find_one',
    method: 'get',
    params: {
      id
    }
  })
}

// 添加门店
export function addStore (form) {
  return request({
    url: '/merchant/store/save_rewrite',
    method: 'post',
    data: qs.stringify(
      form
    )
  })
}

// 修改门店
export function editStore (form) {
  return request({
    url: '/merchant/store/update',
    method: 'post',
    data: qs.stringify(
      form
    )
  })
}

// 获取云喇叭配置信息--列表
export function hornList (params) {
  return request({
    url: '/merchant/store/voice',
    method: 'GET',
    params: params
  })
}

// 获取云喇叭配置信息--新增
export function hornListAdd (params) {
  return request({
    url: '/merchant/store/voice',
    method: 'POST',
    data: params,
    jsonHeader: true
  })
}

// 获取云喇叭配置信息--调试
export function hornListTest (params) {
  return request({
    url: '/merchant/store/voice_test',
    method: 'GET',
    params: params
  })
}

// 获取云喇叭配置信息--调试
export function hornListDel (params) {
  return request({
    url: `/merchant/store/voice/${params}`,
    method: 'DELETE'
  })
}

//设置行业
export function Industry (form) {
  console.log('进来了',form)
  return request({
    url: '/merchant/store/category',
    method: 'post',
    data: form,
    jsonHeader: true
  })
}


