export function getExcel(tHeaders, jsons){
    require.ensure([], () => {
        const { export_json_to_excel } = require('@/excel/Export2Excel')
        const tHeader = tHeaders
        const data = jsons
        export_json_to_excel(tHeader, data, '用户列表')
    })
}


