import request from '@/utils/request'
import qs from 'qs'

// 今日交易概况
export function tradingSituation(startTime,endTime) {
  return request({
    url: '/order/count/order_count_day_new',
    method: 'get',
    params: {
      startTime,endTime
    }
  })
}
// 今日交易概况图
export function tradingSituationMap(type) {
  return request({
    url: '/order/count/dealStatistics',
    method: 'get',
    params: {
     type
    }
  })
}
// 交易数据
export function tradeData(startTime,endTime) {
  return request({
    url: '/order/count/order_count_period',
    method: 'get',
    params: {
      startTime,endTime
    }
  })
}
// 交易数据图
export function tradeDataMap(startTime,endTime) {
    return request({
      url: '/order/count/dealDetailStatistics',
      method: 'get',
      params: {
        startTime,endTime
      }
    })
  }

// 支付通道
export function payAisle(startTime,endTime,type ) {
    return request({
      url: '/order/count/pay_channel',
      method: 'get',
      params: {
        startTime,endTime,
        type
      }
    })
  }


