<template>
  <div class="newCommission">
    <el-card class="card-box">
      <div class="tab_header_bar">
        <div class="block">
          <el-select size="mini"
                     v-model="value"
                     placeholder="请选择">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      合计：消费金额：5466元 返佣金额 12.5元
      <!-- 表格 -->
      <el-table :data="tableData"
                style="width: 100%">
        <el-table-column prop="orderNo"
                         label="用户名"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="payTime"
                         label="手机号"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="time"
                         label="交易时间"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="pay"
                         sortable
                         label="消费金额"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="pay"
                         sortable
                         label="返佣金额"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column label="状态"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            已结算
          </template>
        </el-table-column>
      </el-table>
      <!-- <pagination :total-elements="tableTotalElements" :change-callback="getOrderList" ref="page"></pagination> -->
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      name: '',
      startTime: '',
      endTime: '',
      options: [
        {
          value: '选项5',
          label: '北京烤鸭'
        }
      ],
      value: '',
      tableData: [
        {
          time: '2019-10-19'
        }
      ],
      tableTotalElements: 0
    }
  }
}
</script>
<style scoped>
.newCommission {
  padding: 30px;
}
.tab_header_bar {
  margin-bottom: 10px;
}

.block {
  display: inline-block;
  vertical-align: middle;
}
</style>



