import request from '@/utils/request'
import qs from 'qs'

// 获取会员卡详情
export function memberDetail () {
  return request({
    url: '/member/sem/cardTemplate/max',
    method: 'POST',
    params: {

    }
  })
}

// 修改会员阿卡详情
export function UpdateDetail (memberDetailData) {
  return request({
    url: '/member/sem/cardTemplate/save',
    method: 'post',
    jsonHeader: true,
    data: memberDetailData
  })
}

// 上传
export function uploadIMG (file, userID) {
  return request({
    url: '/member/sem/upload/imageOrVideo',
    method: 'POST',
    params: {
      file, userID
    }
  })
}
// 保存模板
export function setTemplateForm (templateId) {
  return request({
    url: '/member/sem/cardTemplate/setTemplateForm',
    method: 'POST',
    params: {
      templateId
    }
  })
}
// 获取发卡链接
export function applyActivateUrl (templateId) {
  return request({
    url: '/member/sem/cardTemplate/applyActivateUrl',
    method: 'POST',
    params: {
      templateId
    }
  })
}
// 添加优惠券
export function coupon (couponData) {
  return request({
    url: '/member/coupon/save_type_coupon',
    method: 'post',
    data: qs.stringify(
      couponData
    )
  })
}
// 门店列表
export function shopTable (name) {
  console.log(name, 'name')
  return request({
    url: '/member/sem/coupon/findStore',
    method: 'get',
    params: {
      name
    }
  })
}

// 获取会员支付设置详情
export function MembersPay () {
  return request({
    url: '/member/sem/payRule/findOne',
    method: 'get',
    params: {

    }
  })
}
// 修改
export function saveMembersPay (payData) {
  return request({
    url: '/member/sem/payRule/save',
    method: 'post',
    jsonHeader: true,
    data: payData
  })
}
// 添加会员等级
export function addMemberGrade (couponData) {
  return request({
    url: '/member/member/level/save_single',
    method: 'post',
    jsonHeader: true,
    data: couponData
  })
}
// 获取会员等级
export function MemberGrade () {
  return request({
    url: '/member/member/level/list',
    method: 'get',
    params: {
    }
  })
}
// 删除会员等级
export function delMemberGrade (ids) {
  return request({
    url: '/member/member/level/delete',
    method: 'post',
    jsonHeader: true,
    params: {
      ids
    }
  })
}



