<template>
  <div class="comSales">
    <p class="icon">
      <i class="el-icon-goods"></i>商品销量
    </p>
    <div class="mt">
      <el-radio-group class="mr20"
                      v-model="searchDays"
                      size="small"
                      @change="changeDays($event)">
        <el-radio-button :label="0">昨日</el-radio-button>
        <el-radio-button :label="1">近7日</el-radio-button>
        <el-radio-button :label="2">近30日</el-radio-button>
      </el-radio-group>
      <el-date-picker @change="getstartTime"
                      v-model="startTime"
                      type="date"
                      format="yyyy-MM-dd"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      size="small">
      </el-date-picker>
      <span>--</span>
      <el-date-picker @change="getendTime"
                      v-model="endTime"
                      type="date"
                      format="yyyy-MM-dd"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      size="small">
      </el-date-picker>
      <el-button size="small"
                 type="primary"
                 style="margin-left:20px">导出表格
      </el-button>
    </div>
    <div class="mt flex"
         style="justify-content: space-between;">
      <div class="flex"
           style="color:#717171;">
        <p>
          销量
          <i @click="getSort1"
             :class="isSort1?'el-icon-caret-bottom':'el-icon-caret-top'"></i>
        </p>
        <p>
          销售额
          <i @click="getSort2"
             :class="isSort2?'el-icon-caret-bottom':'el-icon-caret-top'"></i>
        </p>
        <p>
          带来交易量
          <i @click="getSort3"
             :class="isSort3?'el-icon-caret-bottom':'el-icon-caret-top'"></i>
        </p>
      </div>
      <p style="color:#9B9999;">
        指标说明
        <el-tooltip placement="bottom">
          <div slot="content"
               class="tooltipContent">
            <p>
              <span>销量</span>已选时间段内有效交易中，该商品的销售数量
            </p>
            <p>
              <span>销量占比</span>已选时间段内有效交易中，该商品销量与全部商品销量的比值
            </p>
            <p>
              <span>商品销售额</span>已选时间段内有效交易中，该商品的实际销售总金额
            </p>
            <p>
              <span>商品销售额占比</span>已选时间段内有效交易中，该商品销售额与全部商品销售额的比值
            </p>
            <p>
              <span>带来交易量</span>已选时间段内有效交易中，包含该商品的交易笔数
            </p>
            <p>
              <span>带来交易量占比</span> 已选时间段内有效交易中，包含该商品的交易笔数占全部交易笔数的比值
            </p>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </p>
    </div>
    <!-- 表格 -->
    <el-table ref="tableData"
              :data="tableData"
              style="width: 100%;margin-left:10px"
              @expand-change="getsalesMap">
      <el-table-column label="排名"
                       type="index"
                       width="90px"
                       align="center">
      </el-table-column>
      <el-table-column label="商品名称"
                       prop="goodsName"
                       align="center">
      </el-table-column>
      <el-table-column label="商品图片"
                       prop="goodsImg"
                       align="center">
        <template slot-scope="scope">
          <img v-if="scope.row.goodsImg"
               style="width:60px;height:60px;"
               :src="photoUrl+scope.row.goodsImg" />
          <img v-else
               style="width: 60px;height: 60px;"
               src="../../../assets/images/goods/noIMge.png"
               class="head_pic" />
        </template>
      </el-table-column>
      <el-table-column label="销售额"
                       prop="salesNum"
                       align="center">
        <template slot-scope="scope">
          <p>￥{{scope.row.salesNum}}</p>
          <p style="color:#AEABAB">占比{{Math.round((scope.row.salesNum/goodsPriceScale) * 100)}}%</p>
        </template>
      </el-table-column>
      <el-table-column label="销量（份）"
                       prop="salesCountNum"
                       align="center">
        <template slot-scope="scope">
          <p>￥{{scope.row.salesCountNum}}</p>
          <p style="color:#AEABAB">占比{{Math.round((scope.row.salesCountNum/salesCount) * 100)}}%</p>
        </template>
      </el-table-column>
      <el-table-column label="带来交易量（笔）"
                       prop="tradingVolume"
                       align="center">
        <template slot-scope="scope">
          <p>￥{{scope.row.tradingVolume}}</p>
          <p style="color:#AEABAB">占比{{Math.round((scope.row.tradingVolume/tradingVolumeScale) * 100)}}%</p>
        </template>
      </el-table-column>
      <el-table-column label="更多"
                       type="expand"
                       width="100"
                       align="center">
        <template slot-scope="scope">
          <!-- 表格下拉内容 -->
          <div class="flex">
            <el-radio-group class="mr20"
                            v-model="scope.row.searchDays2"
                            size="small"
                            @change="changeDays2($event,scope.row)">
              <el-radio-button :label="0">昨日</el-radio-button>
              <el-radio-button :label="1">近7日</el-radio-button>
              <el-radio-button :label="2">近30日</el-radio-button>
            </el-radio-group>
            <el-date-picker @change="getstartTime2($event,scope.row)"
                            v-model="scope.row.startTime2"
                            type="date"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            size="small">
            </el-date-picker>
            <span>--</span>
            <el-date-picker @change="getendTime2($event,scope.row)"
                            v-model="scope.row.endTime2"
                            type="date"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            size="small">
            </el-date-picker>
            <div class="modelK bgfense">
              <p>销售额</p>
              <p>￥{{scope.row.map1}}</p>
            </div>
            <div class="modelK bglanse">
              <p>销量</p>
              <p>{{scope.row.map2}}份</p>
            </div>
            <div class="modelK bghuangse">
              <p>带来交易量</p>
              <p>{{scope.row.map3}}笔</p>
            </div>
            <div class="dateType">
              <p @click="getisType(scope.row)"
                 :class="scope.row.isType?'bg_p':''">按天</p>
              <p @click="getisType(scope.row)"
                 :class="!scope.row.isType?'bg_p':''">按月</p>
            </div>
          </div>
          <!-- 折线图 -->
          <div ref="myChartId"
               :style="{width: '100%', height: '300px'}"></div>
        </template>
      </el-table-column>
    </el-table>
    <pagination :total-elements="totalElement"
                :change-callback="getsalesList"
                ref="page">
    </pagination>
  </div>
</template>
<script>
import pagination from "@/components/pagination/index";
import { url } from "@/utils/request";
import { salesList, salesMap } from "../api/commoditySales";
export default {
  components: { pagination },
  data() {
    return {
      searchDays: 1,
      searchDays2: 1,
      startTime: "",
      endTime: "",
      startTime2: "",
      endTime2: "",
      isType: true,
      tableData: [],
      photoUrl: url + "/fms/upload/resource/",
      goodsPriceScale: 0,
      salesCount: 0,
      tradingVolumeScale: 0,
      salesNumType: "", //销售类型
      salesCountNumType: "", //销售（份）类型
      tradingVolumeType: "",
      isSort1: true,
      isSort2: true,
      isSort3: true,
      totalElement: 0,
      goodsName: "",
      type: 0,
      tableMap: [],
      dateTimeList: [],
      salesCountNum: [],
      map1: 0,
      map2: 0,
      map3: 0
    };
  },
  mounted() { },
  created() {
    this.startTime = this.getDay(-7);
    // this.startTime2 = this.getDay(-7);
    this.endTime = this.getDay(0);
    // this.endTime2 = this.getDay(0);
    this.getsalesList(1, 10);
  },
  methods: {
    changeDays(val) {
      switch (val) {
        case 0:
          this.startTime = this.getDay(-1);
          this.endTime = this.getDay(0);
          break;
        case 1:
          this.startTime = this.getDay(-7);
          this.endTime = this.getDay(0);
          break;
        case 2:
          this.startTime = this.getDay(-30);
          this.endTime = this.getDay(0);
          break;
      }
      this.getsalesList(1, 10);
    },
    changeDays2(val, row) {
      switch (val) {
        case 0:
          row.startTime2 = this.getDay(-1);
          row.endTime2 = this.getDay(0);
          break;
        case 1:
          row.startTime2 = this.getDay(-7);
          row.endTime2 = this.getDay(0);
          break;
        case 2:
          row.startTime2 = this.getDay(-30);
          row.endTime2 = this.getDay(0);
          break;
      }
      this.getsalesMap(row);
    },
    getSort1() {
      this.isSort1 = !this.isSort1;
      if (this.isSort1) {
        this.salesNumType = 0;
      } else {
        this.salesNumType = 1;
      }
      this.getsalesList();
    },
    getSort2() {
      this.isSort2 = !this.isSort2;
      if (this.isSort2) {
        this.salesCountNumType = 0;
      } else {
        this.salesCountNumType = 1;
      }
      this.getsalesList();
    },
    getSort3() {
      this.isSort3 = !this.isSort3;
      if (this.isSort3) {
        this.tradingVolumeType = 0;
      } else {
        this.tradingVolumeType = 1;
      }
      this.getsalesList();
    },
    getisType(row) {
      row.isType = !row.isType;
      if (row.isType) {
        this.type = 0;
      } else {
        this.type = 1;
      }
      this.getsalesMap(row);
    },
    //获取图表列表
    getsalesMap(row) {
      if (row) {
        this.goodsName = row.goodsName;
        this.startTime2 = row.startTime2;
        this.endTime2 = row.endTime2;
      }
      salesMap(this.goodsName, this.startTime2, this.endTime2, this.type)
        .then(response => {
          let dataMap = response.obj.list;
          row.map1 = response.obj.goodsPriceScale; //goodsPriceScale销售额
          row.map2 = response.obj.salesCount; //salesCount销量
          row.map3 = response.obj.total; //total带来交易量
          this.dateTimeList = [];
          this.salesCountNum = [];

          dataMap.forEach(item => {
            this.dateTimeList.push(item.dateTime);
            this.salesCountNum.push(item.salesCountNum);
          });
          this.drawLine();
        })
        .catch(() => { });
    },
    //获取列表
    getsalesList(pageNumber, pageSize) {
      salesList(
        this.startTime,
        this.endTime,
        this.salesNumType,
        this.salesCountNumType,
        this.tradingVolumeType,
        pageNumber,
        pageSize
      )
        .then(response => {
          this.tableData = response.obj.list.map(item => {
            return {
              map1: 0,
              map2: 0,
              map3: 0,
              searchDays2: 1,
              isType: true,
              startTime2: this.getDay(-7),
              endTime2: this.getDay(0),
              ...item
            };
          });
          this.totalElement = response.obj.totalElement;

          this.goodsPriceScale = response.obj.goodsPriceScale; //总销售额
          this.salesCount = response.obj.salesCount; //总销售份
          this.tradingVolumeScale = response.obj.tradingVolumeScale; //总交易笔
        })
        .catch(() => { });
    },
    drawLine() {
      this.$refs.tableData.$nextTick(() => {
        let myChart = this.$echarts.init(this.$refs.myChartId);
        // 绘制图表
        myChart.setOption({
          //提示框
          tooltip: {
            trigger: "axis",
            position: "top",
            axisPointer: {
              type: "cross",
              label: {
                backgroundColor: "#6a7985"
              }
            }
          },
          legend: {
            data: ["省", "市", "区"]
          },
          toolbox: {
            feature: {
              saveAsImage: {}
            }
          },
          grid: {
            left: "3%",
            right: "4%",
            bottom: "3%",
            containLabel: true
          },
          xAxis: {
            type: "category",
            boundaryGap: false,
            data: this.dateTimeList
          },
          yAxis: {
            type: "value",
            show: false
          },
          series: [
            {
              data: this.salesCountNum,
              type: "line",
              areaStyle: {}
            }
          ],
          color: ["#8db9be"]
        });
      });
    },
    getstartTime(val) {
      this.startTime = val;
      this.getsalesList();
    },
    getendTime(val) {
      this.endTime = val;
      this.getsalesList();
    },
    getstartTime2(val, row) {
      row.startTime2 = val;
      this.getsalesMap(row);
    },
    getendTime2(val, row) {
      row.endTime2 = val;
      this.getsalesMap(row);
    },
    getDay(day) {
      var today = new Date();
      var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      var tHours = today.getHours();
      var tMinutes = today.getMinutes();
      var tSeconds = today.getSeconds();
      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return (
        tYear +
        "-" +
        tMonth +
        "-" +
        tDate +
        " " +
        tHours +
        ":" +
        tMinutes +
        ":" +
        tSeconds
      );
    },
    doHandleMonth(month) {
      var m = month;
      if (month.toString().length == 1) {
        m = "0" + month;
      }
      return m;
    }
  }
};
</script>
<style scoped>
.icon {
  font-size: 16px;
  font-weight: bold;
  margin: 16px 0;
}
.icon i {
  margin: 0 5px;
  font-size: 20px;
}
p i {
  margin-right: 30px;
}
.mr20 {
  margin-right: 20px;
}
.dateType {
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border: 1px solid #5a5b5b;
  border-radius: 4px;
  margin-left: 20px;
}
.dateType p {
  width: 50%;
  text-align: center;
  padding: 3px;
}
.bg_p {
  background: #5a5b5b;
  color: #fff;
}
.modelK {
  width: 150px;
  height: 75px;
  color: #fff;
  text-align: center;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 37px;
}
.bgfense {
  background: rgba(244, 181, 181, 0.17);
}
.bglanse {
  background: rgba(144, 217, 255, 0.17);
}
.bghuangse {
  background: rgba(247, 193, 127, 0.17);
}
.modelK p {
  padding: 5px 0;
}
.modelK p:first-of-type {
  color: #a59a9a;
}
.modelK p:last-of-type {
  color: #333;
  font-weight: 600;
  font-size: 16px;
}
.tooltipContent p {
  line-height: 20px;
  opacity: 0.9;
  font-size: 14px;
}
.tooltipContent p span {
  width: 100px;
  text-align: right;
  display: inline-block;
  margin-right: 20px;
}
.flex {
  display: flex;
  align-items: flex-start;
  margin-left: 10px;
}
.comSales {
  padding: 30px;
}
.mt {
  margin-top: 20px;
}
</style>


