import request from '@/utils/request'
import qs from 'qs'

// 获取卡片数据
export function getCardData () {
  return request({
    url: '/order/count/order_count',
    method: 'get',
    params: {
    }
  })
}
// 获取图表数据
export function getChartData () {
  return request({
    url: '/order/count/order_count_line',
    method: 'get',
    params: {
    }
  })
}







