<template>
  <div class="app-container" v-loading="loading">
    <!--卡片-->
    <div class="card_group">

      <div class="card_item">
        <div class="card_item_title">今日订单总金额</div>
        <div class="card_item_content">
           <span>
            {{cardData.todayOrderPrice || 0}}
           </span>
        </div>
      </div>

      <div class="card_item">
        <div class="card_item_title">今订单数总数</div>
        <div class="card_item_content">
           <span>
             {{cardData.todayOrderAmount  || 0}}
           </span>
        </div>
      </div>

      <div class="card_item">
        <div class="card_item_title">昨日订单总金额</div>
        <div class="card_item_content">
           <span>
            {{cardData.yesterdayOrderPrice  || 0}}
           </span>
        </div>
      </div>

      <div class="card_item">
        <div class="card_item_title">昨日订单数总数</div>
        <div class="card_item_content">
           <span>
             {{cardData.yesterdayOrderAmount  || 0}}
           </span>
        </div>
      </div>


    </div>

    <!--底部大区-->
    <div class="bottom_box">
      <!--左侧公告栏-->
      <div class="region left">
        <el-card class="box-card">
          <div slot="header" class="clearfix" style="height:20px;padding: 0">
            <span>最新公告</span>
            <el-button style="float: right; height: 20px;margin: 0;padding: 0" type="text">更多</el-button>
          </div>
          <div class="content">
            敬请期待
          </div>

        </el-card>
      </div>

      <!--右侧图表-->
      <div class="region right">
        <el-card class="box-card">
          <div slot="header" class="clearfix" style="height:20px;padding: 0">
            <span>订单统计</span>
          </div>
          <div class="content" id="orderChart"

               v-loading="chartLoading"
               element-loading-text="没有数据"
               element-loading-spinner="el-icon-s-release"
               element-loading-background="rgba(255, 255, 255, 0.8)"
          >
          </div>
        </el-card>
      </div>


    </div>

  </div>
</template>

<script>
  import Vue from 'vue'
  import {getCardData, getChartData} from './api/index.js'

  export default {
    name: 'index',
    data() {
      return {
        loading: true,//加载圈
        chartLoading: true,
        //卡片数据
        cardData: {
          todayOrderAmount: 0,
          todayOrderPrice: 0,
          yesterdayOrderAmount: 0,
          yesterdayOrderPrice: 0,
        },
        chartData: {}
      }
    },
    mounted() {
      this.getCardData() //获取卡片数据
      this.getChartData() //获取图表数据
      // this.drawChart()
    },
    methods: {
      /**
       * 获取卡片数据
       */
      getCardData: function () {
        getCardData().then(res => {
          console.log(res)
          let data = res.obj
          this.cardData = data //赋值
        }).catch(e => {
          this.$message.error('订单统计数据获取失败，请刷新重试')
        })
      },
      /**
       * 获取图表数据
       */
      getChartData: function () {
        getChartData().then(res => {
          console.log(res)
          let data = res.obj
          this.chartData = data //赋值
          this.chartLoading = false //图表遮罩关闭
          this.loading = false //全页遮罩关闭
          this.drawChart(this.chartData) //调用绘制方法
        }).catch(e => {
          this.$message.error('图表数据获取失败，请刷新重试')
        })
      },
      /**
       * 渲染图表
       */
      drawChart: function (data) {
        let cNumber = data.number
        let cSum = data.sum
        let cxData = data.xData
        var chart = this.$echarts.init(document.getElementById('orderChart'))
        chart.setOption({
          // title: {text: '商户统计', textStyle: {color: '#333', fontWeight: 'lighter', fontSize: '16'}},
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} @ {c} ',
          },
          legend: {
            data: ['总金额', '总单数']
          },
          toolbox: {
            feature: {
              magicType: {
                type: ['line', 'bar']
              },
              saveAsImage: {}
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: cxData
          },//X轴标题
          yAxis: {
            type: 'value'
          },
          series: [{
            data: cSum,
            name: '总金额',
            type: 'line',
            areaStyle: {}
          }, {
            data: cNumber,
            name: '总单数',
            type: 'line',
            areaStyle: {}
          }] //数据数组，多个数据，legend需要对于标题name
        })
      }
    }
  }
</script>

<style scoped>
  /*卡片*/
  .card_group {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  .card_item {
    width: 23%;

  }

  .card_item_title {
    background-color: #FF803E;
    letter-spacing: 0.1em;
    padding: 6px 10px;
    font-size: 12px;
    color: white;
    border: none;
  }

  .card_item_content {
    width: 100%;
    background: #f9f9f9;
    height: 100px;
    text-align: center;
  }

  .card_item_content span {
    line-height: 100px;
    font-size: 40px;
    color: #FF803E;
    /*text-shadow: 1px 1px 0px #37544b;*/
  }

  /*底部*/
  .bottom_box {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  .region {
    width: 49%
  }

  .content {
    height: 340px;
  }

  @media (max-width: 1024px) {
    .card_item {
      width: 50%;
    }

    .bottom_box {
      flex-wrap: wrap;
    }

    .region {
      width: 100%
    }
  }
</style>



