<template>
  <div class="app-container" v-loading="loading">
    <el-card class="box-card">
      <div
        style="display: flex;justify-content: space-between;border-bottom: 1px solid #ebeef5;    padding: 0px 20px 20px 20px;"
      >
        <p>导入记录</p>
        <p style="color:#1087C7" @click="toback">返回上一页</p>
      </div>
      <!-- 批量导入 -->
      <div style="width:100%;display: flex;justify-content: flex-end;">
        <el-button @click="bulkImport" style="margin-top:20px">批量导入</el-button>
      </div>
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="createTime" label="导入时间" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="totalNum" label="导入数量" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="successNum" label="成功数" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="failNum" label="失败数" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="name" label="操作人" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="操作" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <el-button @click="downloadReport(scope.row)" type="text" size="small">下载报表</el-button>
          </template>
        </el-table-column>
      </el-table>
      <pagination
                :total-elements="tableTotalElement"
                :change-callback="getimportList"
                ref="page"
              ></pagination>
    </el-card>
    <!-- 弹窗 -->
    <el-dialog class="tanchuang" title="批量导入" :visible.sync="dialogVisible" width="30%">
      <p>
        1、 您需要先下载数据导入模板，点击
        <span style="color:#1087C7" @click="DownloadTemplate">下载excel模板</span>
      </p>
      <p>2、 如果您已按照要求整理了需要导入的数据，即可将文件上传，然后点击上传按钮进行数据导入</p>
      <p>3、 文件导入成功后，即可查看数据导入记录，也可下载报表，查看详情记录</p>
      <div style="margin:20px 0;">
        <el-upload
          :headers="headers"
          class="upload-demo"
          ref="upload"
          name="editormd-image-file"
          :action="importUrl"
          :on-success="handlePreSuccess"
          :on-error="handlePreError"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :auto-upload="false"
          :limit="limitNum"
          :data="putaway"
        >
          <el-button slot="trigger" size="small">导入文件</el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传</el-button>
        </el-upload>
      </div>
      <el-checkbox v-model="putaway.isShelf" true-label=1 false-label=0 @change="getnumber">上架</el-checkbox>
    </el-dialog>
  </div>
</template>
<script>
import pagination from "@/components/pagination/index";
import { url } from "@/utils/request";
import { downTemplate, importList, downReport } from "../api/goodsManage";
export default {
  components: { pagination },
  data() {
    return {
      tableData: [],
      limitNum: 1,
      putaway: {
        isShelf: 0
      },
      loading: false, // 页面加载
      dialogVisible: false,
       tableTotalElement: 0, // 表格总条数
      headers: {
        authorized: sessionStorage.token
      },
      moban: "",
      key: "goods_template",
      importUrl: url + "/merchant/goods/import"
    };
  },
  created() {
    this.getimportList();
  },
  methods: {
    //   下载
    downloadReport(row) {
      let rowId = row.id;
      console.log(rowId);

      window.location.href = url + "/merchant/goods/import/report?id=" + rowId;
      //   downReport(rowId)
      //     .then(response => {

      //     })
      //     .catch(() => {});
    },
    //获取列表
    getimportList(pageNumber,pageSize) {
      importList(pageNumber,pageSize)
        .then(response => {
          let data = response.obj;
          this.tableData = data.content;
           this.tableTotalElement = data.totalElements;
        })
        .catch(() => {});
    },
    // 文件超出个数限制时的钩子
    exceedFile(files, fileList) {
      this.$message({
        type: "error",
        message: `只能选择 ${
          this.limitNum
        } 个文件，当前共选择了 ${files.length + fileList.length} 个`
      });
    },
    getnumber(e){
      this.putaway.isShelf = e;
    },
    handlePreSuccess(response, file, fileList){
      console.log('111',response);
      if(response.msg = '导入失败'){
      }else{
        this.dialogVisible = false;
        this.getimportList();
      }
    },
    handlePreError(err, file, fileList){
      console.log('444',err);
      console.log('555',file);
      console.log('666',fileList);
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {},
    //   下载模板
    DownloadTemplate() {
      downTemplate(this.key)
        .then(response => {
          this.moban = response.msg;
          window.location.href = this.moban;
        })
        .catch(() => {});
    },
    bulkImport() {
      this.dialogVisible = true;
    },
    toback() {
      this.$router.push({ path: "/goodsManage/views/goodsManage" });
    }
  }
};
</script>
<style scoped>
.tanchuang p {
  line-height: 30px;
}
</style>


