<template>
  <div class="app-container" v-loading="loading">
    <div class="tab_header_bar">
      <!--公司-->
      <el-input v-model="searchForm.name" @keyup.enter.native="search" placeholder="店名" size="small"
                class="formItem block"></el-input>
      <!--状态-->
      <el-select v-model="searchForm.status" @change="search" clearable placeholder="状态" size="small"
                 class="formItem block">
        <el-option
          v-for="item in statusOption"
          :key="item.value"
          :label="item.name"
          :value="item.value">
        </el-option>
      </el-select>
      <!--按钮-->
      <el-button type="primary" size="small" @click="search">查询</el-button>
      <el-button type="primary" size="small" @click="newMemberOpen">新增</el-button>
    </div>

    <!--表格-->
    <el-table
      :data="tableData"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        label="门店名称">
      </el-table-column>
      <el-table-column
        prop="storeNo"
        label="门店编号">
      </el-table-column>
      <el-table-column label="状态">
        <template slot-scope="scope">
          <template v-if="scope.row.status===1">启用</template>
          <template v-else-if="scope.row.status===2">注销</template>
        </template>
      </el-table-column>
      <el-table-column
        prop="createTime"
        label="创建时间">
      </el-table-column>
      <el-table-column
        label="当前行业">
        <template slot-scope="scope">
          <div class="cell"><el-button @click="showMask(scope.row)" type="button" class="el-button el-button--text">
            <span v-if="scope.row.industryCategory == null">设置行业</span>
            <span v-if="scope.row.industryCategory == 0">超市</span>
            <span v-if="scope.row.industryCategory == 1">自助点餐</span>
            <span v-if="scope.row.industryCategory == 2">医药</span>
            <span v-if="scope.row.industryCategory == 3">加油站</span>
            <span v-if="scope.row.industryCategory == 4">景区</span>
          </el-button></div>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        width="270">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="openEditDialog(scope.row)">编辑
          </el-button>
          <el-button type="text" @click="rooterDeatil(scope.row)">详情</el-button>
          <el-button type="text" @click="hornConfig(scope.row)">云喇叭管理</el-button>
          <el-button type="text" @click="printerConfig(scope.row)">打印机管理</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div v-if="mask" style="position: fixed; top: 0; left: 0; width: 100%; height: 100vh; background: rgba(0,0,0,0.3); z-index:1;">
      <div role="dialog" aria-modal="true" aria-label="行业设置" class="el-dialog" style="margin-top: 30vh; width: 450px;">
        <div class="el-dialog__header">
          <span class="el-dialog__title">行业设置</span>
          <button type="button" aria-label="Close" class="el-dialog__headerbtn" @click="()=>{mask = false}">
            <i class="el-dialog__close el-icon el-icon-close"></i>
          </button>
        </div>
        <div class="el-dialog__body">
          <div data-v-138c8e69="">  
            <form class="el-form" style="margin-top: -20px;">
              <div class="el-form-item is-required">
                <label class="el-form-item__label" style="width: 150px;">选择行业:</label>
                <div class="el-form-item__content" style="margin-left: 150px;">
                  <el-radio-group v-model="radio">
                    <label role="radio" tabindex="-1" class="el-radio mt10" style="line-height: 30px; display: table;" v-for="(item,index) in list">
                      <el-radio name="test" @change="getVal(index)" :label="index" :checked="item.isCheck">{{item.value}}</el-radio>
                    </label>
                  </el-radio-group>
                </div>
              </div>
              <div class="el-form-item" style="margin-bottom: 0px;">
              <div class="el-form-item__content" style="margin-left: 150px;">
                <div class="vm-text-right">
                  <el-button class="el-button el-button--default el-button--medium" @click="()=>{mask = false}">
                    <span>取 消</span>
                  </el-button>
                  <el-button type="primary" class="el-button el-button--primary el-button--medium" @click="Industry">
                    <span>确 定</span>
                  </el-button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
    </div>
    
    <pagination
      :total-elements="totalElements"
      :change-callback="getTableList"
      ref="page"></pagination>

    <!--新增模态框-->
    <el-dialog
      title="新增"
      :visible.sync="newMember"
      :before-close="newMemberClose">
      <el-form :model="storeForm" :rules="Rules" ref="ruleForm" :label-width="newFormLabelWidth">
        <el-form-item label="店名：" :label-width="formLabelWidth">
          <el-input v-model="storeForm.name"></el-input>
        </el-form-item>
        <el-form-item label="编号：" :label-width="formLabelWidth">
          <el-input v-model="storeForm.storeNo"></el-input>
        </el-form-item>
        <el-form-item label="状态:" :label-width="formLabelWidth">
          <el-select
            v-model="storeForm.status"
            collapse-tags>
            <el-option
              v-for="item in statusOption"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="省份：" :label-width="formLabelWidth">
          <el-select v-model="storeForm.province" placeholder="请选择省份"
                     size="small" class="formItem"
                     @change="getProv($event)">
            <el-option :value="n"
                       :key="index"
                       :label="n"
                       v-for="(n,index) in dataProv[0]['prov']">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="城市：" :label-width="formLabelWidth">
          <el-select v-model="storeForm.city" placeholder="请选择城市"
                     size="small" class="formItem"
                     @click="getCity($event)">
            <el-option :value="n"
                       :key="index"
                       :label="n"
                       v-for="(n,index) in dataCity">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="联系地址：" :label-width="formLabelWidth">
          <el-input v-model="storeForm.address"></el-input>
        </el-form-item>
        <el-form-item label="地址备注：" :label-width="formLabelWidth">
          <el-input v-model="storeForm.addressNote"></el-input>
        </el-form-item>
        <el-form-item label="门店电话：" :label-width="formLabelWidth">
          <el-input v-model="storeForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="支付凭证描述：" :label-width="formLabelWidth">
          <el-input v-model="storeForm.paymentDesciption"></el-input>
        </el-form-item>
        <el-form-item label="图片" :label-width="formLabelWidth">
          <el-upload
            class="logo-uploader"
            :action="url"
            :data="filesData"
            name="files"
            :show-file-list="false"
            :on-success="createLogoSuccess"
            :on-error="handleLogoFailed"
            :before-upload="beforeLogoUpload"
            :headers="headers">
            <img v-if="createdSrc" :src="createdSrc" class="logo">
            <i v-else class="el-icon-plus logo-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="newMemberClose">取消</el-button>
    <el-button type="primary" @click="addStore()">确定</el-button>
  </span>
    </el-dialog>
    <!--新增模态框-->

    <!--编辑模态框-->
    <el-dialog
      title="编辑门店信息"
      :visible.sync="editMember"
      :before-close="editMemberClose">
      <el-form :model="editStoreForm" :rules="Rules" ref="ruleForm" :label-width="newFormLabelWidth">

        <el-form-item label="店名：" :label-width="formLabelWidth">
          <el-input v-model="editStoreForm.name"></el-input>
        </el-form-item>
        <el-form-item label="编号：" :label-width="formLabelWidth">
          <el-input v-model="editStoreForm.storeNo"></el-input>
        </el-form-item>
        <el-form-item label="状态:" :label-width="formLabelWidth">
          <el-select
            v-model="editStoreForm.status"
            collapse-tags>
            <el-option
              v-for="item in statusOption"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="省份：" :label-width="formLabelWidth">
          <el-select v-model="editStoreForm.province" placeholder="请选择省份"
                     size="small" class="formItem"
                     @change="getProv($event)">
            <el-option :value="n"
                       :key="index"
                       :label="n"
                       v-for="(n,index) in dataProv[0]['prov']">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="城市：" :label-width="formLabelWidth">
          <el-select v-model="editStoreForm.city" placeholder="请选择城市"
                     size="small" class="formItem"
                     @click="getCity($event)">
            <el-option :value="n"
                       :key="index"
                       :label="n"
                       v-for="(n,index) in dataCity">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="联系地址：" :label-width="formLabelWidth">
          <el-input v-model="editStoreForm.address"></el-input>
        </el-form-item>
        <el-form-item label="地址备注：" :label-width="formLabelWidth">
          <el-input v-model="editStoreForm.addressNote"></el-input>
        </el-form-item>
        <el-form-item label="门店电话：" :label-width="formLabelWidth">
          <el-input v-model="editStoreForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="支付凭证描述：" :label-width="formLabelWidth">
          <el-input v-model="editStoreForm.paymentDesciption"></el-input>
        </el-form-item>
        <el-form-item label="图片" :label-width="formLabelWidth">
          <el-upload
            class="logo-uploader"
            :action="url"
            :data="filesData"
            name="files"
            :show-file-list="false"
            :on-success="editLogoSuccess"
            :on-error="handleLogoFailed"
            :before-upload="beforeLogoUpload"
            :headers="headers">
            <img v-if="editSrc" :src="editSrc" class="logo">
            <i v-else class="el-icon-plus logo-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editMemberClose">取 消</el-button>
    <el-button type="primary" @click="editOpera">确 定</el-button>
  </span>
    </el-dialog>
    <!--编辑模态框-->
    <!--云喇叭配置-->
    <el-dialog title="云喇叭管理"
               v-if="dialogHornConfig.show"
               :visible.sync="dialogHornConfig.show"
               width="800px">
      <hornConfigList :propsInfo="dialogHornConfig"></hornConfigList>
      <!--
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogHornConfig.show = false">关 闭</el-button>
      </span>
      -->
    </el-dialog>
     <!--云喇叭配置-->
    <el-dialog title="打印机管理"
               v-if="dialogPrinterConfig.show"
               :visible.sync="dialogPrinterConfig.show"
               width="800px">
      <printerConfig :propsInfo="dialogPrinterConfig"></printerConfig>
    </el-dialog>
  </div>
</template>

<script>
import {
  getTableList,
  addStore,
  editStore,
  Industry
} from '../api/store'
import pagination from '@/components/pagination/index'
import {returnThumbnailUrl, uploadUrl} from '@/modules/file/api/upload'
import cityJson from '@/assets/city/city.json'
import hornConfigList from './components/hornConfigList.vue'
import printerConfig from './components/printerConfig.vue'

export default {
  name: 'store',
  components: {pagination, hornConfigList, printerConfig},
  data () {
    return {
      filesData: {
        module: 'store'
      }, // 携带参数
      createdSrc: '',
      editSrc: '',
      headers: {
        authorized: sessionStorage.token
      },
      url: uploadUrl + '/store',
      tableData: [], // 表格数据
      multipleSelection: [], // 选中数据-多选
      totalElements: 0, // 数据条数
      loading: false, // loading效果
      btnLoading: false,
      formLabelWidth: '150px', // label宽度
      newFormLabelWidth: '100px', // label宽度
      // 查询------------------------------------------------------------
      advancedSearch: false, // 高级查询模态
      searchForm: {
        name: '', // 公司名
        status: ''// 状态
      }, // 搜索用的表单
      statusOption: [{value: 1, name: '启用'}, {value: 2, name: '注销'}], // 状态选择器

      // 新增--------------------------------------------------------------
      newMember: false, // 新增模态框
      storeForm: {
        name: '', // 名称
        storeNo: '', // 编号
        province: '', // 省
        city: '', // 市
        address: '', // 详细地址
        addressNote: '',
        phone: '', // 号码
        paymentDesciption: '',
        status: 1, // 状态
        photoId: '' // 图片id
      },
      // 编辑
      editMember: false, // 编辑模态框
      editStoreForm: {
        id: '',
        name: '', // 名称
        storeNo: '', // 编号
        province: '', // 省
        city: '', // 市
        address: '', // 详细地址
        addressNote: '',
        phone: '', // 号码
        paymentDesciption: '',
        status: '', // 状态
        photoId: '' // 图片id
      }, // 编辑表单

      // 省市联动--------------------------
      cityDetail: '',
      dataProv: cityJson['selectProv'],
      dataCity: '',
      dataCountry: '',
      provIndex: '',
      cityIndex: '',
      dialogForm: {
        name: '',
        selectProv: '',
        selectCity: '',
        selectCountry: '',
        mark: ''
      },
      selectItems: [],
      // 省市联动--------------------------
      // 新增判断规则
      Rules: {
        name: [
          {required: true, message: '请输入店名', trigger: 'blur'},
          {min: 2, max: 50, message: '长度在 3 到 50 个字符', trigger: 'blur'}
        ],
        storeNo: [
          {required: true, message: '请输入联系人名字', trigger: 'blur'},
          {min: 2, max: 12, message: '长度在 2 到 12 个字符', trigger: 'blur'}
        ],
        province: [
          {required: true, message: '请选择省份', trigger: 'blur'}
        ],
        city: [
          {required: true, message: '请选择城市/县', trigger: 'blur'}
        ],
        address: [
          {required: true, message: '请输入具体的地址', trigger: 'blur'},
          {min: 2, max: 50, message: '长度在 2 到 50 个字符', trigger: 'blur'}
        ]
      },
      dialogHornConfig: {
        show: false
      },
      dialogPrinterConfig: {
        show: false
      },
      mask: false,
      radio: '',
      list: [{
          value: '超市',
          isCheck:false
        },
        {
          value: '自助点餐',
          isCheck:false
        },
        {
          value: '医药',
          isCheck:false
        },
        {
          value: '加油站',
          isCheck:false
        },
        {
          value: '景区',
          isCheck:false
      }],
      rdioVal: '',
      industry: {
        id: '',
        industryCategory: ''
      },
      numb: ''
    }
  },
  mounted () {
    this.getTableList()
  },
  methods: {
    showMask (row) {
      this.industry.id = row.id;
      this.radio = row.industryCategory == null ? '' : row.industryCategory;
      console.log('961124',this.rdioVal);
      this.mask = true;
    },
    getVal (e){
      this.list.map((v,i)=>{ 
        if(i==e){
          v.isCheck = true
        }else{
          v.isCheck = false
        };
      });
    },
    Industry(){
      this.list.map((v,i)=>{
        if(v.isCheck){
          this.industry.industryCategory = i;
        };
      });
      Industry(this.industry).then(response => {
        this.refresh()
      }).catch(e => {
        console.log(e);
      });
      this.mask = false;
    },
    /**
     * 云喇叭配置
     **/
    hornConfig (data) {
      this.dialogHornConfig = {
        show: true,
        data: data
      }
    },
    /**
     * 打印机配置
     **/
    printerConfig (data) {
      this.dialogPrinterConfig = {
        show: true,
        data: data
      }
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    /**
       * 模态框操作  start----------------------------------------
       **/
    // 高级查询
    advancedSearchOpen: function () {
      this.advancedSearch = true
    },
    advancedSearchClose: function () {
      this.advancedSearch = false
    },

    // 省市
    getProv: function (prov) {
      this.provIndex = this.dataProv[0]['prov'].indexOf(prov)
      this.dataCity = this.dataProv[this.provIndex + 1]['0_' + this.provIndex]
      this.dialogForm.selectCity = this.dataCity[0]
      this.cityDetail = this.dataProv[this.provIndex + 1]['class1']
      this.dataCountry = this.cityDetail[0]['class2']
      this.dialogForm.selectCountry = this.dataCountry[0]
      this.storeForm.city = ''
      this.editStoreForm.city = ''
    },
    getCity: function (city) {
      this.cityIndex = this.dataCity.indexOf(city)
      this.dataCountry = this.cityDetail[this.cityIndex]['class2']
      this.dialogForm.selectCountry = this.dataCountry[0]
    },

    /*
     * 获取门店列表
    * */
    getTableList (number, pageSize) {
      this.loading = true
      getTableList(number, pageSize, this.searchForm).then(response => {
        let data = response.obj;
        this.tableData = data.content
        this.totalElements = data.totalElements
        setTimeout(() => {
          this.loading = false
        }, 500)
      }).catch(() => {
        this.loading = false
      })
    },
    // 新增
    newMemberOpen: function () {
      this.storeForm = {
        name: '', // 名称
        storeNo: '', // 编号
        province: '', // 省
        city: '', // 市
        address: '', // 详细地址
        addressNote: '',
        phone: '', // 号码
        paymentDesciption: '',
        status: 1, // 状态
        photoId: '' // 图片id
      }
      this.createdSrc = ''
      this.newMember = true
    },
    newMemberClose: function () {
      this.storeForm = {
        name: '', // 名称
        storeNo: '', // 编号
        province: '', // 省
        city: '', // 市
        address: '', // 详细地址
        addressNote: '',
        phone: '', // 号码
        paymentDesciption: '',
        status: 1, // 状态
        photoId: '' // 图片id
      }
      this.createdSrc = ''
      this.newMember = false
    },
    // 添加门店
    addStore: function () {
      addStore(this.storeForm).then(res => {
        this.$message({
          message: res.msg,
          type: 'success'
        })
        this.btnLoading = false
        this.storeForm = {
          name: '', // 名称
          storeNo: '', // 编号
          province: '', // 省
          city: '', // 市
          address: '', // 详细地址
          addressNote: '',
          phone: '', // 号码
          paymentDesciption: '',
          status: 1, // 状态
          photoId: '' // 图片id
        }
        this.createdSrc = ''
        this.newMember = false // 关闭模态框
        this.$refs.page.refresh()
      }).catch(e => {
        this.loading = false
      })
    },

    // 编辑
    openEditDialog: function (row) {
      console.log(row)
      // 打开模态框
      this.editStoreForm.id = row.id
      this.editStoreForm.name = row.name
      this.editStoreForm.storeNo = row.storeNo
      this.editStoreForm.province = row.province
      this.editStoreForm.city = row.city
      this.editStoreForm.address = row.address
      this.editStoreForm.addressNote = row.addressNote
      this.editStoreForm.phone = row.phone
      this.editStoreForm.paymentDesciption = row.paymentDesciption
      this.editStoreForm.status = row.status
      this.editStoreForm.photoId = row.photoId
      this.editSrc = this.getPicUrl(row.photoId)
      // 获取这个门店的信息
      this.editMember = true
    },

    getPicUrl (picid) {
      // 暂无图片替换图
      let noimg = 'data:image/png;base64,'

      if (picid == null || picid == '' || picid == undefined) {
        return noimg
      }
      return returnThumbnailUrl(picid)
    },
    // 关闭编辑框
    editMemberClose: function () {
      this.editMember = false
    },
    // 确认修改
    editOpera () {
      this.loading = true
      this.btnLoading = true
      editStore(this.editStoreForm).then(response => {
        this.$message({
          message: response.msg,
          type: 'success'
        })
        this.btnLoading = false
        this.editMemberClose()
        this.$refs.page.refresh()
      }).catch(() => {
        this.btnLoading = false
        this.loading = false
      })
    },

    // 跳转详情页
    rooterDeatil (data) {
      // console.log(data)
      // query发送ID到详情
      let id = data.id
      this.$router.push({path: '/store/views/storeDetail', query: {id: id}})
    },

    /**
       * 模态框操作 end----------------------------------------
       **/
    // 创建模态框 上传
    createLogoSuccess (response, file) {
      // 保存图片ID
      this.photoId = response.obj
      console.log(response)
      this.$message({
        message: response.msg,
        type: 'success'
      })
      this.storeForm.photoId = response.obj
      this.createdSrc = returnThumbnailUrl(response.obj)
    },
    // 编辑模态框 上传
    editLogoSuccess (response, file) {
      this.$message({
        message: response.msg,
        type: 'success'
      })
      this.editStoreForm.photoId = response.obj
      this.editSrc = returnThumbnailUrl(response.obj)
    },
    // 图片上传失败
    handleLogoFailed () {
      this.$message({
        message: '图片上传失败',
        type: 'error'
      })
    },
    // 对上传图片做限制
    beforeLogoUpload (file) {
      let isImage = file.type.substring(0, 5) === 'image'
      if (!isImage) {
        this.$message.error('只能上传图片')
        return false
      }
      return true
    },

    /*
   * 搜索
   * */
    search () {
      this.$refs.page.search()
    },
    /*
      * 重置
      * */
    reset () {
      this.searchForm = {
        company: '', // 公司名
        contact: '', // 联系人
        status: ''// 状态
      }// 搜索用的表单
      this.$refs.page.search()
    },

    /*
     * 刷新
     * */
    refresh () {
      this.$refs.page.refresh()
    }
  },
  filters: {
    // 列表状态过滤器
    statusfilter: function (s) {
      let text = ''
      if (s == '' || s == null || s == undefined) { // "",null,undefined
        text = 'error'
      } else {
        switch (s) {
          case '1':
            text = '未启用'
            break
          case '2':
            text = '待审核'
            break
          case '3':
            text = '已签约'
            break
          case '4':
            text = '过期'
            break
          case '5':
            text = '注销'
            break
          default :
            text = '未知'
            break
        }
      }
      return text
    }

  }
  
}
</script>

<style scoped>
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    distribute . js
    padding-right: 8px;
  }

  .formItem {
    display: inline-block;
    width: 150px;
    max-width: 100%;
  }

  .logo-uploader {
    display: inline-block;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .logo-uploader:hover {
    border-color: #409EFF;
  }

  .logo-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .logo {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



