import request from '@/utils/request'
import qs from 'qs'
// 获取分类列表
export function categoryList(parentId, pageNumber, pageSize) {
  return request({
    url: '/merchant/goods/category',
    method: 'get',
    params: {
      parentId,
      pageNumber,
      pageSize,
      industryCategory: 0
    }
  })
}

export function addclassify(names) {
  return request({
    url: '/merchant/goods/category',
    method: 'post',
    data:names,
    jsonHeader: true
  })
}

export function declassify(ids) {

  return request({
    url: '/merchant/goods/category/delete',
    method: 'post',
    data: qs.stringify({
      ids: ids
    })
  })
}


