<template>
  <div class="app-contanier">
    <div slot="header" class="clearfix">
      <el-button type="text" 
                style="float:right;font-size:16px" 
                @click="goBack" class="header-icon-back">
                <i class="el-icon-back"></i>返回列表
      </el-button>
    </div>
    <div class="header_bar">
      <div class="block">
        <el-input v-model="searchData.orderNum"
                  size="medium"
                  style="margin-right:10px"
                  @keyup.enter.native="search"
                  placeholder="订单号">
        </el-input>
      </div>
      <div class="block">
        <el-input v-model="searchData.staffName" 
                  size="medium"
                  style="margin-right:10px"
                  @keyup.enter.native="search"
                  placeholder="收银员">
        </el-input> 
      </div>

      <div class="block">
        <el-select
          v-model="searchData.payType"
          clearable
          placeholder="支付类型"
          size="medium"
          style="margin-right:10px"
          @change="search"
        >
          <el-option
            v-for="item in payType"
            :key="item.value"
            :label="item.name"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>

      <div class="block">
        <el-date-picker
          v-model="searchData.beginTime"
          size="medium"
          type="date"
          placeholder="开始日期"
          format="yyyy 年 MM 月 dd 日"
          value-format="yyyy-MM-dd HH:mm:ss"
          style="width: 190px">
        </el-date-picker>
        至
        <el-date-picker
          v-model="searchData.endTime"
          type="date"
          size="medium"
          placeholder="结束日期"
          format="yyyy 年 MM 月 dd 日"
          value-format="yyyy-MM-dd HH:mm:ss"
          style="width: 190px">
        </el-date-picker>
      </div>
      <div class="block">
        <el-button size="medium" 
                    type="primary" 
                   @click="search">
                   查 询
        </el-button>
        <el-button size="medium"
                   @click="deviceDetailExport">
                   导 出
        </el-button>
      </div>
    </div>
    <div class="middle_list">
      <span style="font-weight:bold">订单总数:</span>
      <span style="margin-right:15px">{{middleList.orderNum}}元</span>
      <span style="font-weight:bold">订单总金额:</span>
      <span style="margin-right:15px">{{middleList.totalMoney}}元</span>
      <span style="font-weight:bold">退款金额:</span>
      <span style="margin-right:15px">{{middleList.refundMoney}}元</span>
      <span style="font-weight:bold">客户付款:</span>
      <span style="margin-right:15px">{{middleList.payMoney}}元</span>
      <span style="font-weight:bold">优惠:</span>
      <span style="margin-right:15px">{{middleList.discountMoney}}元</span>
    </div>
    <div class="tab_bottom">

      <el-table :data="tableData" style="width: 100%">
          <el-table-column type="index" label="序号" header-align="left"></el-table-column>
          <el-table-column prop="orderNum" label="订单号" show-overflow-tooltip></el-table-column>
          <el-table-column prop="storeName" label="门店" show-overflow-tooltip></el-table-column>
          <el-table-column prop="staffName" label="收银员"></el-table-column>
          <el-table-column prop="orderMoney" label="订单金额"></el-table-column>
          <el-table-column prop="createTime" label="创建时间"></el-table-column>
          <el-table-column label="订单状态">
            <template slot-scope="scope">{{payStatus(scope.row.status)}}</template>
          </el-table-column>
          <el-table-column label="支付方式">
            <template slot-scope="scope">{{payWay(scope.row.payWay)}}</template>
          </el-table-column>
          <el-table-column label="支付类型">
            <template slot-scope="scope">{{paymentType(scope.row.payType)}}</template>
          </el-table-column>
          <el-table-column prop="type" label="支付通道">
            <template slot-scope="scope">{{payChannelCount(scope.row.paychannel)}}</template>
          </el-table-column>
          <el-table-column prop="actualPayMoney" label="实际付款"></el-table-column>
          <el-table-column prop="discountMoney" label="优惠"></el-table-column>
        </el-table>

      <!-- <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          type="index"
          label="序号"
          width="60">
        </el-table-column>
        <el-table-column
          prop="orderNum"
          label="订单号">
        </el-table-column>
        <el-table-column
          prop="storeName"
          label="门店">
        </el-table-column>
        <el-table-column
          prop="staffName"
          label="收银员">
        </el-table-column>
        <el-table-column
          prop="orderMoney"
          label="订单金额">
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="创建时间">
        </el-table-column>
        <el-table-column
          prop="status"
          label="订单状态">
        </el-table-column>
        <el-table-column
          prop="payWay"
          label="支付方式">
        </el-table-column>
        <el-table-column label="支付类型">
            <template slot-scope="scope">{{paymentType(scope.row.payType)}}</template>
          </el-table-column>
        <el-table-column
          prop="payType"
          label="支付类型">
        </el-table-column>
        <el-table-column
          prop="paychannel"
          label="支付通道">
        </el-table-column>
        <el-table-column
          prop="actualPayMoney"
          label="实际付款">
        </el-table-column>
        <el-table-column
          prop="discountMoney"
          label="优惠">
        </el-table-column>
      </el-table> -->
      <pagination :total-elements="totalElements"
                :change-callback="getDeviceDetailList"
                ref="page">
      </pagination>
    </div>
  </div>
</template>

<script>
import pagination from '@/components/pagination/index'
import { getDeviceDetail,getDeviceDetailList,getDeviceDetailListExport } from'../api/device'
import { url } from '@/utils/request'
export default {
  components:{ pagination },
  data(){
    return {
      id:'',
      loading:false,
      totalElements:0,//数据条数
      tableData:[],//表格数据
      headers: {
        authorized:sessionStorage.token
      },
      //搜索条件
      searchData: {
        orderNum:'',
        staffName:'',
        payType: '',
        beginTime:'',
        endTime:'',
      },
      payChannel: [
        { value: 1, name: "官方" },
        { value: 2, name: "会员宝" },
        { value: 3, name: "易融码" },
        { value: 4, name: "统统收" },
        { value: 5, name: "惠闪付" },
        { value: 6, name: "富友" },
        { value: 7, name: "随行付" },
        { value: 8, name: "卡拉卡" },
        { value: 12, name: "天阙随行付" }
      ],
      payType: [
        { value: 1, name: "扫码" },
        { value: 3, name: "刷脸" },
      ],
      payWayOption: [
        {value: 1, name: "微信"},
        {value: 2, name: "支付宝"},
        {value: 3, name: "银行卡"},
        {value: 4, name: "会员卡"}
      ], // 支付方式选项
      statusOption: [
        {value: 1, name: "未支付"},
        {value: 2, name: "支付成功"},
        {value: 3, name: "支付失败"},
        {value: 4, name: "已撤销"},
        {value: 5, name: "全额退款成功"},
        {value: 6, name: "部分退款成功"},
        {value: 7, name: "退款失败"}
      ], // 订单状态选项
      middleList:{
        orderNum:'0',
        totalMoney:'0',
        refundMoney:'0',
        payMoney:'0',
        discountMoney:'0'
      }
    }
  },

  mounted(){
    this.id = this.$route.query.id
    this.getDeviceDetail(),
    this.getDeviceDetailList()
    

  },
  methods:{
    //返回列表
    goBack:function(id){
      this.$router.go('-1')
    },
    //流水清单列表提交查询
    search:function(){
      this.$refs.page.search()
      this.getDeviceDetail()
    },
    //导出流水清单列表
    deviceDetailExport(){
      location.href = url +'/auth/equipment/export?orderNum='+this.searchData.orderNum+'&staffName='+this.searchData.staffName+
      +'&payType='+this.searchData.payType+'&beginTime='+this.searchData.beginTime+'&endTime='+this.searchData.endTime+'&equipmentId='+this.id
    },
    getDeviceDetailListExport(number,pageSize){
      getDeviceDetailListExport(number,pageSize,this.searchData,this.id).then(response => {
        let data = response.obj
        this.totalElements = data.totalElements
        this.tableData = data.content
        setTimeout(() =>{
            this.loading = false
          },500)
        }).catch(() =>{
          this.loading = false
        })
    },

    //获取流水清单的列表数据
    getDeviceDetailList(number,pageSize){
      this.loading = true
      getDeviceDetailList(number,pageSize,this.searchData,this.id).then(response => {
        let data = response.obj
        this.totalElements = data.totalElements
        this.tableData = data.content
        setTimeout(() =>{
          this.loading = false
        },500)
      }).catch(() => {
        this.loading = false
      })
    },
    
    //获取流水清单的笔数
    getDeviceDetail(number,pageSize) {
      this.loading=true,
      getDeviceDetail(number,pageSize,this.searchData,this.id).then(response=>{
        let data = response.obj
        //console.log(data)
        //console.log(data.content)
        this.middleList.orderNum = data.orderNum
        this.middleList.totalMoney = data.totalMoney
        this.middleList.refundMoney = data.refundMoney
        this.middleList.payMoney = data.payMoney
        this.middleList.discountMoney = data.discountMoney
        setTimeout(()=>{
          this.loading = false
        },500)
      }).catch(()=>{
        this.loading = false
      })
    },

    // 支付方式
    payWay: function(val) {
      let str = "未知";
      let pt = this.payWayOption;
      for (let i in pt) {
        if (pt[i].value == val) {
          str = pt[i].name; // 赋值匹配名字
          break; // 停止循环
        }
      }
      return str;
    },
    // 支付类型
    paymentType: function(val) {
      let str = "未知";
      let channel = this.payType;
      channel.forEach(item => {
        if (item.value === val) {
          str = item.name;
        }
      });
      return str;
    },
    // 支付通道
    payChannelCount: function(val) {
      let str = "未知";
      let channel = this.payChannel;
      channel.forEach(item => {
        if (item.value === val) {
          str = item.name;
        }
      });
      return str;
    },
    // 支付状态
    payStatus: function(val) {
      let str = "未知";
      let pt = this.statusOption;
      for (let i in pt) {
        if (pt[i].value == val) {
          str = pt[i].name; // 赋值匹配名字
          break; // 停止循环
        }
      }
      return str;
    }
  }
}
</script>

<style scoped>
.header_bar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 20px 0;

}
.header_bar .block{
  display: inline-block;
  vertical-align: middle;
  margin: 0 12px 15px 0;
}
.block{
  display: inline-block;
  vertical-align: middle;
}
.header-icon-back{
  font-size: 16px;
  padding: 18px 20px;
}
.middle_list{
  padding: 20px;
  font-size: 14px;
}
.app-contanier {
  padding: 18px 20px;
}
</style>


