import request from '@/utils/request'
import qs from 'qs'

// 获取积分规则参数
export function findPointsRuler () {
  return request({
    url: '/member/credits_ruler/find',
    method: 'get',
    params: {}
  })
}

// 保存积分规则
export function savePointsRuler (form) {
  return request({
    url: '/member/credits_ruler/update',
    method: 'post',
    data: qs.stringify(
      form
    )
  })
}



