import request from '@/utils/request'
import qs from 'qs'

// 获取订单列表
export function findOrderList (pageNum, pageSize, orderNumber, userName, storeId, payWay, status, payType, start_payTime, end_payTime) {
  return request({
    url: '/order/running_account/merchant_running_account_list_for_store',
    method: 'get',
    params: {
      pageNum,
      pageSize,
      orderNumber,
      userName,
      storeId,
      payWay,
      status,
      payType,
      start_payTime,
      end_payTime
    }
  })
}








