import request from '@/utils/request'
import qs from 'qs'
// 获取商品列表
export function goodsList(goodsName, isShelf, pageNumber, pageSize) {
  console.log(pageNumber, pageSize);
  return request({
    url: '/merchant/goods/getGoodsPage',
    method: 'get',
    params: {
      goodsName,
      isShelf,
      pageNumber,
      pageSize,
      industryCategory: 1
    }
  })
}

export function addGoods(names) {
  return request({
    url: '/merchant/goods',
    method: 'post',
    data: names,
    jsonHeader: true
  })
}

export function delGoods(ids) {
  return request({
    url: '/merchant/goods/delete',
    method: 'post',
    data: qs.stringify({
      ids: ids
    })
  })
}
export function delGoods2(ids) {
  return request({
    url: '/merchant/goods/delete',
    method: 'post',
    data: qs.stringify({
      ids: ids.join()
    })
  })
}
export function batchSel(ids, isShelf) {
  // ids = ids.map(item => item.ids)
 
  return request({
    url: '/merchant/goods/batch/shelf',
    method: 'post',
    data: qs.stringify({
      ids: ids.join(), 
      isShelf: isShelf
    })
  })
}
//获取分类
export function getClassify() {
  return request({
    url: '/merchant/goods/category/tree',
    method: 'get',
    params: {
      industryCategory : 1
    }
  })
}

export function getcount() {
  return request({
    url: '/merchant/goods/count',
    method: 'get',
    params: {
      industryCategory: 1
    }
  })
}

////////////////////////
//下载模板
export function downTemplate(key) {
  return request({
    url: '/auth/systemconfig',
    method: 'get',
    params: {
      key
    }
  })
}

export function importList(pageNumber, pageSize) {
  return request({
    url: '/merchant/goods/import',
    method: 'get',
    params: {
      pageNumber, pageSize
    }
  })
}
export function downReport(id) {
  return request({
    url: '/merchant/goods/import/report',
    method: 'get',
    params: {
      id
    }
  })
}


