import request from '@/utils/request'
import qs from 'qs'

//获取
export function getMemCardDetail () {
  return request({
    url: '/member/member/card/detail',
    method: 'get',
    params: {}
  })
}

//更新
export function saveMemCardDetail (data) {
  return request({
    url: '/member/member/card/update',
    method: 'post',
    data: qs.stringify(
      data
    )
  })
}

//检查是否有卡券
export function findBindCardCoupon () {
  return request({
    url: '/member/coupon/find_type_coupon',
    method: 'get',
    params: {
      type: 2
    }
  })
}


// 删除绑定卡券
export function deleteCoupon (id) {
  return request({
    url: '/member/coupon/delete',
    method: 'post',
    data: qs.stringify({
      ids: id
    })
  })
}



