<template>
  <div class="app-container">
    <el-card class="card-box">
      <div class="tab_header_bar">
        <div class="block">
          <el-input size="small"
                    v-model="searchForm.keyWords"
                    placeholder="店铺名称/订单编号">
          </el-input>
        </div>
        <div class="block">
          <el-date-picker type="date"
                          size="small"
                          v-model="searchForm.startTime"
                          placeholder="开始日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd"
                          style="width: 190px">
          </el-date-picker>
          <span>至</span>
          <el-date-picker type="date"
                          size="small"
                          v-model="searchForm.endTime"
                          placeholder="结束日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd"
                          style="width: 190px">
          </el-date-picker>
        </div>
        <div class="block">
          <el-select size="small"
                     v-model="searchForm.payType"
                     placeholder="支付方式">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="block"
             style="margin-left: 10px">
          <el-button size="small"
                     type="primary"
                     @click="search">查 询
          </el-button>
        </div>
      </div>
      <el-table :data="tableData"
                style="width: 100%">
        <el-table-column prop="storeName"
                         label="门店名称"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="orderNo"
                         label="订单编号"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="payTime"
                         label="支付时间"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="orderPrice"
                         label="订单金额"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="payPrice"
                         label="实付金额"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="payType"
                         label="支付方式"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column label="操作"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <el-button type="text"
                       size="small"
                       @click="orderdetails(scope.row)"
            >查看订单</el-button>
          </template>
        </el-table-column>
      </el-table>
      <pagination :total-elements="tableTotalElements"
                  :change-callback="getOrderList"
                  ref="page">
      </pagination>
    </el-card>
  </div>
</template>
<script>
import pagination from '@/components/pagination/index'
import { getOrder } from '../api/orderManage'
export default {
  name: 'orderManage',
  components: { pagination },
  data () {
    return {
      loading: true,
      tableTotalElements: 0,
      tableData: [],
      searchForm: {
        keyWords: '',
        startTime: '',
        endTime: '',
        payType: ''
      },
      options: [{
        value: 1,
        name: '会员卡'
      }, {
        value: 2,
        name: '支付宝'
      }, {
        value: 3,
        name: '微信'
      }]
    }
  },
  mounted () {
    this.getOrderList()
  },
  methods: {
    search () {
      this.$refs.page.search()
    },
    getOrderList (number, pagesize) {
      this.loading = true
      // console.log('123' + response.obj)
      getOrder(number, pagesize, this.searchForm).then(response => {
        let data = response.obj
        this.tableData = data.content
        this.totalElements = data.totalElements
        data.content.forEach(element => {
          switch (element.payType) {
            case 1:
              element.payType = '会员卡'
              break
            case 2:
              element.payType = '支付宝'
              break
            case 3:
              element.payType = '微信'
              break
          }
        })
        setTimeout(() => {
          this.loading = false
        }, 500)
      }).catch(() => {
        this.loading = false
      })
    },
    orderdetails(row){
      console.log(row);
      this.$router.push({
        path: '/goodsManage/views/orderDetails',
        query: {
          id: row.id,
          payType: row.payType
        }
      });
    }
  }
}
</script>

<style scoped>
.tab_header_bar {
  margin-bottom: 10px;
}

.block {
  display: inline-block;
  vertical-align: middle;
}
span {
  font-size: 14px;
}
</style>



