<template>
  <div class="app-container" v-loading="loading">
    <el-card class="box-card">
      <div class="header_bar">
        <div class="tab_header_bar">
          <!--第一行功能条-->
          <div class="block">
            <el-input
              v-model="searchForm.orderNumber"
              placeholder="订单号"
              size="small"
              @input="submitSearch"
            ></el-input>
          </div>
          <div class="block">
            <el-input
              v-model="searchForm.userName"
              placeholder="收银员"
              size="small"
              style="width: 120px"
              @input="submitSearch"
            ></el-input>
          </div>
          <div class="block">
            <el-input
              v-model="searchForm.qrName"
              placeholder="二维码名称"
              size="small"
              style="width: 120px"
              @input="submitSearch"
            ></el-input>
          </div>

          <div class="block">
            <el-select
              v-if="userType==1"
              clearable
              v-model="searchForm.storeId"
              placeholder="商户"
              size="small"
              style="width: 200px"
              @change="submitSearch"
            >
              <el-option
                v-for="item in storeOption"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>

          <div class="block">
            <el-select
              v-model="searchForm.payWay"
              clearable
              placeholder="支付方式"
              size="small"
              style="width: 100px"
              @change="submitSearch"
            >
              <el-option
                v-for="item in payWayOption"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>

          <div class="block">
            <el-select
              v-model="searchForm.status"
              clearable
              placeholder="订单状态"
              size="small"
              @change="submitSearch"
              style="width: 100px"
            >
              <el-option
                v-for="item in statusOption"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>

          <div class="block">
            <el-select
              v-model="searchForm.payType"
              clearable
              placeholder="支付类型"
              size="small"
              style="width: 100px"
              @change="submitSearch"
            >
              <el-option
                v-for="item in payType"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>

          <div class="block">
            <el-date-picker
              @change="checkDataRange"
              v-model="searchForm.days"
              type="daterange"
              range-separator="~"
              start-placeholder="开始"
              end-placeholder="结束"
              format="yyyy年MM月dd"
              value-format="timestamp"
              size="small"
            ></el-date-picker>
          </div>
        </div>

        <div class="line-2_btn">
          <!--第二行-->
          <el-button type="primary" @click="getOrderList()">查 询</el-button>
          <el-button @click="reset">重 置</el-button>
          <el-button @click="exportflowOrderList">导 出</el-button>
        </div>
      </div>
    </el-card>

    <el-card class="box-card">
      <div content="total_view">
        <span class="little_title">订单总数</span>
        <span class="little_content">{{totalCount.orderNum}}笔；</span>
        <span class="little_title">订单总金额</span>
        <span class="little_content">{{totalCount.orderAmount}}元；</span>
        <span class="little_title">退款总金额</span>
        <span class="little_content">{{totalCount.refundAmount}}元；</span>
        <span class="little_title">顾客实付</span>
        <span class="little_content">{{totalCount.customerPaidAmount}}元；</span>
        <span class="little_title">优惠</span>
        <span class="little_content">{{totalCount.discountAmount}}元</span>
      </div>
    </el-card>

    <el-card class="box-card">
      <!--表格-->
      <div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column type="index" label="序号" header-align="left"></el-table-column>
          <el-table-column prop="orderNumber" label="订单号" show-overflow-tooltip></el-table-column>
          <el-table-column prop="storeName" label="门店" show-overflow-tooltip></el-table-column>
          <el-table-column prop="userName" label="收银员"></el-table-column>
          <!--
          <el-table-column
            prop=""
            label="二维码名称">
          </el-table-column>
          -->
          <el-table-column prop="totalPrice" label="订单金额"></el-table-column>
          <el-table-column prop="createTime" label="创建时间" width="200px"></el-table-column>
          <el-table-column label="状态">
            <template slot-scope="scope">{{payStatus(scope.row.status)}}</template>
          </el-table-column>
          <el-table-column label="支付方式">
            <template slot-scope="scope">{{payWay(scope.row.payWay)}}</template>
          </el-table-column>
          <el-table-column label="支付类型">
            <template slot-scope="scope">{{paymentType(scope.row.payType)}}</template>
          </el-table-column>
          <el-table-column prop="type" label="支付通道">
            <template slot-scope="scope">{{payChannelCount(scope.row.payChannel)}}</template>
          </el-table-column>
          <el-table-column prop="actPayPrice" label="顾客实付"></el-table-column>
          <el-table-column prop="disCountPrice" label="优惠"></el-table-column>
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-button type="primary" size="small" @click="goDetail(scope.row.orderNumber)">详细信息</el-button>
            </template>
          </el-table-column>
        </el-table>

        <pagination :total-elements="tableTotalElement" :change-callback="getOrderList" ref="page"></pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import { findOrderList } from "../api/flowOrderList";
import { url } from "@/utils/request";
import {
  findOrderStatusOption,
  findOrderPayWayOption,
  findStoreOption,
  exportList
} from "../api/public";
import pagination from "@/components/pagination/index";

export default {
  name: "flowOrderList",
  components: { pagination },
  data() {
    return {
      loading: true,
      userType: 0,
      searchForm: {
        orderNumber: "",
        userName: "",
        qrName: "",
        storeId: "",
        days: [],
        payWay: "",
        status: "",
        payType: ''
      }, // 搜索条件
      payChannel: [
        { value: 1, name: "官方" },
        { value: 2, name: "会员宝" },
        { value: 3, name: "易融码" },
        { value: 4, name: "统统收" },
        { value: 5, name: "惠闪付" },
        { value: 6, name: "富友" },
        { value: 7, name: "随行付" },
        { value: 8, name: "卡拉卡" },
        { value: 12, name: "天阙随行付" }
      ],
      payType: [
        { value: 1, name: "扫码" },
        { value: 3, name: "刷脸" },
      ],
      totalCount: {},
      storeOption: [],
      payWayOption: [], // 支付方式选项
      statusOption: [], // 订单状态选项
      tableData: [], // 表格
      headers: {
        authorized: sessionStorage.token
      },
      tableTotalElement: 0 // 表格
    };
  },
  mounted() {
    // 获取URL参数
    this.userType = sessionStorage.getItem("userType");
    // 设置当前门店
    this.searchForm.storeId =
      this.$route.query.id == undefined ? "" : this.$route.query.id; //接收ID

    this.changeDays(); // 设置默认日期
    this.getStatusOption(); // 获取支付状态
    this.getPayWayOption(); // 获取支付方式
    this.getStoreOption(); // 获取门店下拉框选项
    this.getOrderList(1, 10);
  },
  filters: {
    // 流水状态
    statusFil: function(val) {
      let v = parseInt(val);
      let txt = "";
      switch (v) {
        case 1:
          txt = "未支付";
          break;
        case 2:
          txt = "支付成功";
          break;
        case 3:
          txt = "支付失败";
          break;
        case 4:
          txt = "已撤销";
          break;
        case 5:
          txt = "全额退款成功";
          break;
        case 6:
          txt = "部分退款成功";
          break;
        case 7:
          txt = "退款失败";
          break;
        default:
          txt = "未知";
          break;
      }
      return txt;
    },
    //流水状态
    payWayFil: function(val) {
      let v = parseInt(val);
      let txt = "";
      switch (v) {
        case 1:
          txt = "微信";
          break;
        case 2:
          txt = "支付宝";
          break;
        case 3:
          txt = "银行卡";
          break;
        case 4:
          txt = "会员卡";
          break;
        default:
          txt = "未知";
      }
      return txt;
    }
  },
  computed: {
    // 支付方式
    payWay: function(val) {
      return function(val) {
        let str = "未知";
        let pt = this.payWayOption;
        for (let i in pt) {
          if (pt[i].value == val) {
            str = pt[i].name; // 赋值匹配名字
            break; // 停止循环
          }
        }
        return str;
      };
    },
    // 支付类型
    paymentType: function(val) {
      return function(val) {
        let str = "未知";
        let channel = this.payType;
        channel.forEach(item => {
          if (item.value === val) {
            str = item.name;
          }
        });
        return str;
      };
    },
    // 支付通道
    payChannelCount: function(val) {
      return function(val) {
        let str = "未知";
        let channel = this.payChannel;
        channel.forEach(item => {
          if (item.value === val) {
            str = item.name;
          }
        });
        return str;
      };
    },
    // 支付状态
    payStatus: function(val) {
      return function(val) {
        let str = "未知";
        let pt = this.statusOption;
        for (let i in pt) {
          if (pt[i].value == val) {
            str = pt[i].name; // 赋值匹配名字
            break; // 停止循环
          }
        }
        return str;
      };
    }
  },
  // 方法
  methods: {
    exportflowOrderList() {
      let orderNumber = this.searchForm.orderNumber;
      let userName = this.searchForm.userName;
      let storeId = this.searchForm.storeId;
      let payWay = this.searchForm.payWay;
      let payType = this.searchForm.payType;
      let status = this.searchForm.status;
      let userId = sessionStorage.getItem("userId");
     
      let start_payTime = this.format(this.searchForm.days[0]);
      let end_payTime = this.format(this.searchForm.days[1]);
      

      // exportList(
      //   orderNumber,
      //   userName,
      //   storeId,
      //   payWay,
      //   status,
      //   start_payTime,
      //   end_payTime
      // )
      //   .then(res => {
      //     let blob = new Blob([res.data], {
      //       type: "application/vnd.ms-excel"
      //     });
      //     let objectUrl = URL.createObjectURL(blob);
      //     window.location.href = objectUrl;
      //   })
      //   .catch(e => {
      //     this.$message.error(e);
      //   });
      window.location.href =
        url +
        "/order/running_account/export/merchantList?orderNumber=" +
        orderNumber +
        "&userName=" +
        userName +
        "&storeId=" +
        storeId +
        "&payWay=" +
        payWay +
        "&payType=" +
        payType +
        "&status=" +
        status +
        "&start_payTime=" +
        start_payTime +
        "&end_payTime=" +
        end_payTime +
        '&userId='+
          userId
        ;
    },
    /**
     * 获取支付状态
     */
    getStatusOption: function() {
      findOrderStatusOption()
        .then(res => {
          // console.log(res)
          this.statusOption = res.obj;
        })
        .catch(e => {
          this.$message.error("支付状态选项无法获取");
        });
    },
    /**
     * 获取支付方式
     */
    getPayWayOption: function() {
      findOrderPayWayOption()
        .then(res => {
          // console.log(res)
          this.payWayOption = res.obj;
        })
        .catch(e => {
          this.$message.error("支付方式选项无法获取");
        });
    },
    /**
     * 获取门店
     */
    getStoreOption: function() {
      findStoreOption()
        .then(res => {
          // console.log(res)
          this.storeOption = res.obj;
        })
        .catch(e => {
          this.$message.error("门店选项无法获取");
        });
    },

    /**
     * 发起搜索
     */
    submitSearch: function() {
      this.getOrderList(1);
    },

    /**
     *获取订单列表
     */
    getOrderList: function(pageNumber, pageSize) {
      let pn = pageNumber == undefined ? 1 : pageNumber;
      let ps = pageSize == undefined ? this.pageSize : pageSize;
      let orderNumber = this.searchForm.orderNumber;
      let userName = this.searchForm.userName;
      let storeId = this.searchForm.storeId;
      let payWay = this.searchForm.payWay;
      let status = this.searchForm.status;
      let payType = this.searchForm.payType;
      let st = this.format(this.searchForm.days[0]);
      let et = this.format(this.searchForm.days[1]);
      findOrderList(
        pn,
        ps,
        orderNumber,
        userName,
        storeId,
        payWay,
        status,
        payType,
        st,
        et
      )
        .then(res => {
          this.pageNumber = pn;
          this.pageSize = ps;
          let data = res.obj;
          this.tableData = data.listData;
          this.totalCount = data.totalCount;
          this.tableTotalElement = data.totalElement;
          this.loading = false;
        })
        .catch(e => {
          this.$message.error("列表获取错误");
        });
    },
    /**
     *跳转详情
     */
    goDetail: function(data) {
      console.log(data);
      this.$router.push({
        path: "/pay/views/flowOrderDetail",
        query: { id: data }
      });
    },

    /**
     *检查时间
     * 如果不对，则修改为里结束日期的前31天。
     */
    checkDataRange() {
      let timestamp = new Date().getTime();
      if (this.searchForm.days == null) {
        this.searchForm.days = new Array();
        this.searchForm.days[0] = timestamp;
        this.searchForm.days[1] = timestamp;
        this.$message("日期选项已回到今日");
        return;
      }
      let startTime = this.searchForm.days[0];
      let endTime = this.searchForm.days[1];
      if (endTime - startTime > 24 * 60 * 60 * 1000 * 31) {
        this.$message.error("前后时间不能超过31天");
        this.$set(
          this.searchForm.days,
          0,
          timestamp - 24 * 60 * 60 * 1000 * 31
        );
        this.$set(this.searchForm.days, 1, timestamp);
      }
    },
    /**
     * 修改日期
     * @param event 日期范围选项
     */
    changeDays: function() {
      let timestamp = new Date().getTime();
      let startTime = timestamp - 24 * 60 * 60 * 1000 * 6;
      let endTime = timestamp;
      this.$set(this.searchForm.days, 0, startTime);
      this.$set(this.searchForm.days, 1, endTime);
    },
    /**
     * reset重置
     */
    reset: function() {
      this.searchForm = {
        orderNumber: "",
        userName: "",
        qrName: "",
        days: [],
        storeId: "",
        payWay: "",
        status: ""
      }; // reset搜索条件
      let timestamp = new Date().getTime();
      let startTime = timestamp - 24 * 60 * 60 * 1000 * 6;
      let endTime = timestamp;
      this.$set(this.searchForm.days, 0, startTime);
      this.$set(this.searchForm.days, 1, endTime);

      this.submitSearch();
    },

    /**
     * 工具
     * @param ts 时间戳
     * @returns {string} 格式化事时间
     */
    // 时间格式化
    format(ts) {
      let time = new Date(ts);
      let y = time.getFullYear();
      let m = time.getMonth() + 1;
      let d = time.getDate();
      return y + "-" + this.add0(m) + "-" + this.add0(d);
    },

    /**
     * 补充0
     * @param  m 月和日，补齐2位
     * @returns {string} 补齐2二位后的时间
     */
    add0(m) {
      return m < 10 ? "0" + m : m;
    }
  }
};
</script>

<style scoped>
.block {
  display: inline-block;
  vertical-align: middle;
}

.box-card {
  margin: 0 0 10px 0;
}

.line-2_btn {
  margin-top: 5px;
}

.little_title {
  font-weight: bold;
  margin: 0 10px 0 0;
  font-size: 14px;
  color: #6e6e6e;
}

.little_title:after {
  content: ":";
}

.little_content {
  font-size: 14px;
  margin: 0 5px 0 0;
  color: #3a3a3a;
}
</style>



