<template>
  <div class="app-container">
    <el-card class="card-box">
      <div class="tab_header_bar">
        <div class="block">
          <span style="color:#606266;">按订单时间：</span>
        </div>
        <div class="block">
          <el-date-picker
            v-model="timeBox"
            type="datetimerange"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00','23:59:59']"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-s-claim"
            style="height: 32px;"
            @change="getVal"
          ></el-date-picker>
        </div>
        <div class="block" style="width: 100px;">
          <el-select size="small"
                     v-model="searchForm.payType"
                     placeholder="全部类型">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="block"
             style="margin-left: 10px">
          <el-button size="small"
                     type="primary"
                     @click="search">查 询
                     <i class="el-icon-search"></i>
          </el-button>
          <el-button size="small"
                     type="primary"
                     @click="reset">重 置
                     <i class="el-icon-refresh-right"></i>
          </el-button>
        </div>
      </div>
      <el-table :data="tableData"
                style="width: 100%">
        <el-table-column prop="orderNo"
                         label="订单编号"
                         header-align="center"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column prop="payTime"
                         label="支付时间"
                         header-align="center"
                         :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column label="订单金额"
                         header-align="center"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
              <div style="text-align: center;">￥{{scope.row.orderPrice}}</div>
          </template>
        </el-table-column>
        <el-table-column label="实付金额"
                         header-align="center"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
              <div style="text-align: center;">￥{{scope.row.payPrice}}</div>
          </template>
        </el-table-column>
        <el-table-column label="类型"
                         header-align="center"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
              <div style="text-align: center;">{{scope.row.payType}}</div>
          </template>
        </el-table-column>
        <el-table-column label="取餐号"
                         header-align="center"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
              <div style="text-align: center;">{{scope.row.payType}}</div>
          </template>
        </el-table-column>
        <el-table-column label="操作"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <el-button type="text"
                       size="small"
                       @click="orderdetails(scope.row)"
            >查看订单</el-button>
          </template>
        </el-table-column>
      </el-table>
      <pagination :total-elements="tableTotalElements"
                  :change-callback="getOrderList"
                  ref="page">
      </pagination>
    </el-card>
  </div>
</template>
<script>
import pagination from '@/components/pagination/index'
import { getOrder } from '../api/order'
export default {
  name: 'orderManage',
  components: { pagination },
  data () {
    return {
      loading: true,
      tableTotalElements: 0,
      tableData: [],
      searchForm: {
        keyWords: '',
        startTime: '',
        endTime: '',
        payType: ''
      },
      options: [{
        value: 1,
        name: '堂食'
      }, {
        value: 2,
        name: '外带'
      }],
      timeBox: []
    }
  },
  mounted () {
    this.getOrderList()
  },
  methods: {
    search () {
      this.$refs.page.search()
    },
    reset(){
      this.timeBox = [];
      this.searchForm.keyWords = '';
      this.searchForm.startTime = '';
      this.searchForm.endTime = '';
      this.searchForm.payType = '';
      this.getOrderList();
    },
    getOrderList (number, pagesize) {
      this.loading = true
      // console.log('123' + response.obj)
      getOrder(number, pagesize, this.searchForm).then(response => {
        let data = response.obj
        this.tableData = data.content
        this.totalElements = data.totalElements
        data.content.forEach(element => {
          switch (element.payType) {
            case 1:
              element.payType = '堂食'
              break
            case 2:
              element.payType = '外带'
              break
          }
        })
        setTimeout(() => {
          this.loading = false
        }, 500)
      }).catch(() => {
        this.loading = false
      })
    },
    orderdetails(row){
      console.log(row);
      this.$router.push({
        path: '/goodsManage/views/orderDetails',
        query: {
          id: row.id,
          payType: row.payType
        }
      });
    },
    getVal(){
      // console.log(this.timeBox);
      this.searchForm.startTime = this.timeBox[0];
      this.searchForm.endTime = this.timeBox[1];
    }
  }
}
</script>

<style scoped>
.tab_header_bar {
  margin-bottom: 10px;
}

.block {
  display: inline-block;
  vertical-align: middle;
}
span {
  font-size: 14px;
}
</style>


