import request from '@/utils/request'
import qs from 'qs'

export function ruleList (status, startTime, endTime, pageNumber, pageSize) {
  return request({
    url: '/merchant/member/rule',
    method: 'get',
    params: {
      status,
      startTime,
      endTime,
      pageSize,
      pageNumber

    }
  })
}

export function addRule (ruleData) {
  return request({
    url: '/merchant/member/rule',
    method: 'post',
    data: ruleData,
    jsonHeader: true
  })
}



