<template>
  <div class="app-container"
       v-loading="loading">
    <!--内容-->
    <!--列表-->
    <div class="fullTitle">
      <p @click="fTitle"
         :class="isActive?'active':''">
        <i class="el-icon-bank-card"></i>
        <span>满减活动</span>
      </p>
      <p @click="sTitle"
         :class="!isActive?'active':''">
        <i class="el-icon-bank-card"></i>
        <span>优惠券</span>
      </p>
    </div>
    <!--  优惠券表格  -->
    <div v-if="!isActive">
      <div class="tab_header_bar">
        <div class="block">
          <el-date-picker v-model="actTimeStart"
                          type="date"
                          size="small"
                          placeholder="开始日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd HH:mm:ss"
                          style="width: 190px"
                          @change="getsedate">
          </el-date-picker>
          <span>--</span>
          <el-date-picker v-model="actTimeEnd"
                          type="date"
                          size="small"
                          placeholder="结束日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd HH:mm:ss"
                          style="width: 190px"
                          @change="getsedate2">
          </el-date-picker>
        </div>
        <div class="block">
          <el-select clearable
                     size="small"
                     v-model="state"
                     placeholder="状态"
                     @change="getSatus">
            <el-option v-for="item in options2"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="block"
             style="margin-left: 10px">
          <el-button size="small"
                     type="primary"
                     icon="el-icon-search"
                     @click="cheackList">查 询
          </el-button>
        </div>
      </div>
      <div style="width:100%;display: flex;justify-content: flex-end;">
        <el-button @click="addRuleBtn"
                   type="primary"
                   size="small">新增优惠券
        </el-button>
      </div>
      <div class="tab_bottom">
        <el-table :data="tableData"
                  style="width: 100%">
          <el-table-column type="index"
                           label="序号"
                           align="center"
                           :show-overflow-tooltip="true">
          </el-table-column>
          <el-table-column prop="name"
                           label="卡券名称"
                           align="center"
                           :show-overflow-tooltip="true">
          </el-table-column>
          <el-table-column label="可领取时间"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              {{scope.row.actTimeStart}}~{{scope.row.actTimeEnd}}
            </template>
          </el-table-column>
          <el-table-column label="卡券价值"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <p v-if="scope.row.miniExpendLimit && scope.row.money">
                订单满{{scope.row.miniExpendLimit}}减{{scope.row.money}}
              </p>
              <p v-else>--</p>
            </template>
          </el-table-column>
          <el-table-column label="已领取/总发放（张）"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <p>{{scope.row.changeInventory}}/{{scope.row.totalInventory}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="actStatus"
                           label="状态"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <span v-if="scope.row.actStatus==1">未开始</span>
              <span v-if="scope.row.actStatus==2">进行中</span>
              <span v-if="scope.row.actStatus==3">已结束</span>
            </template>
          </el-table-column>
          <el-table-column label="操作"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <el-button type="text"
                         size="mini"
                         @click="to_couponDetail(1,scope.row.id)">查 看
              </el-button>
              <!-- <el-button type="text" size="mini" @click="to_couponDetail(2,scope.row.id)">编辑</el-button> -->
              <el-button type="text"
                         size="mini"
                         v-if="!scope.row.actStatus==3">提前结束
              </el-button>
              <el-button type="text"
                         size="mini"
                         @click="todelRule(scope.row.id)"
                         v-else>删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <pagination :change-callback="getCouponList"
                    :total-elements="tableTotalElement"
                    ref="page">
        </pagination>
      </div>
    </div>
    <!-- 满减活动表格 -->
    <div v-else>
      <div class="tab_header_bar">
        <div class="block">
          <el-date-picker v-model="startTime"
                          type="date"
                          size="small"
                          placeholder="开始日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd HH:mm:ss"
                          style="width: 190px">
          </el-date-picker>
          <span>--</span>
          <el-date-picker v-model="endTime"
                          type="date"
                          size="small"
                          placeholder="结束日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd HH:mm:ss"
                          style="width: 190px">
          </el-date-picker>
        </div>
        <div class="block">
          <el-select clearable
                     size="small"
                     v-model="status"
                     placeholder="状态">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="block"
             style="margin-left: 10px">
          <el-button size="small"
                     type="primary"
                     icon="el-icon-search"
                     @click="getactivityList(1,10)">查 询
          </el-button>
        </div>
      </div>
      <div style="width:100%;display: flex;justify-content: flex-end;">
        <el-button @click="addRuleBtn"
                   type="primary"
                   size="small">添加规则
        </el-button>
      </div>
      <div class="tab_bottom">
        <el-table :data="activitytableData"
                  style="width: 100%">
          <el-table-column type="index"
                           label="序号"
                           align="center"
                           :show-overflow-tooltip="true">
          </el-table-column>
          <el-table-column prop="ruleName"
                           label="满减活动名称"
                           align="center"
                           :show-overflow-tooltip="true">
          </el-table-column>
          <el-table-column label="有效期"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              {{scope.row.startTime}}~{{scope.row.endTime}}
            </template>
          </el-table-column>
          <el-table-column label="满减规则"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.rule"
                   :key="index">
                订单满{{item.full?item.full:0}}减{{item.less?item.less:0}}
                <span v-if="index!==scope.row.rule.length-1">，</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="状态"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <p>{{scope.row.status?'启动':'禁用'}}</p>
            </template>
          </el-table-column>
          <el-table-column label="操作"
                           align="center"
                           :show-overflow-tooltip="true">
            <template slot-scope="scope">
              <el-button @click="operation(scope.row)"
                         type="text"
                         size="mini">{{!scope.row.status?'启动':'禁用'}}
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <pagination :change-callback="getCouponList"
                    :total-elements="tableTotalElement2"
                    ref="page">
        </pagination>
      </div>
    </div>
    <!--模态框-->
    <el-dialog title="创建满减活动"
               :visible.sync="editMemberDialogVisible"
               width="40%">
      <div class="justcenter mt">
        <span class="red">*</span>
        <span class="weight7">规则名称</span>
        <el-input v-model="ruleData.ruleName"
                  class="ml20"
                  style="width:80%;"
                  size="mini">
        </el-input>
      </div>
      <div class="justcenter mt">
        <span class="red">*</span>
        <span class="weight7">有效日期</span>
        <el-date-picker class="ml20"
                        size="mini"
                        style="width:35%"
                        v-model="ruleData.startTime"
                        type="date"
                        value-format="timestamp"
                        placeholder="开始日期">
        </el-date-picker>
        <span style="margin:10px">--</span>
        <el-date-picker size="mini"
                        style="width:35%"
                        value-format="timestamp"
                        v-model="ruleData.endTime"
                        type="date"
                        placeholder="结束日期">
        </el-date-picker>
      </div>
      <div class="reductionRules">
        <p>
          <span class="red">*</span>
          <span class="weight7">满减规则</span>
        </p>
        <div style="width:84%">
          <div class="justleft ml20 mt"
               v-for="(item,index) in ruleData.rule"
               :key="index">
            <span>订单满</span>
            <el-input v-model="item.full"
                      style="width:25%;margin:0 5px"
                      size="mini">
            </el-input>
            元减
            <el-input v-model="item.less"
                      style="width:25%;margin:0 5px"
                      size="mini">
            </el-input>
            元
            <i @click="plusOutline"
               class="el-icon-circle-plus-outline ml20"
               style="font-size:20px"></i>
            <i @click="removeOuteline(index)"
               v-if="ruleData.rule.length>1"
               class="el-icon-remove-outline ml20"
               style="font-size:20px"></i>
          </div>
        </div>
      </div>
      <div class="justcenter mt status">
        <span class="weight7 mr20 ml20">状态</span>
        <span :style="ruleData.status==1?'color:#000':'color:#1087C7'">禁用</span>
        <el-switch style="margin:0 10px"
                   size="small"
                   v-model="ruleData.status"
                   :active-value="1"
                   :inactive-value="0"
                   active-color="#1087C7"
                   inactive-color="#ccc">
        </el-switch>
        <span :style="ruleData.status==0?'color:#000':'color:#1087C7'">开启</span>
      </div>
      <!--***-->
      <span slot="footer"
            class="dialog-footer">
        <el-button size="small"
                   @click="editMemberDialogVisible = false">取 消
        </el-button>
        <el-button @click="saveRuleData"
                   size="small"
                   type="primary"
                   v-loading="btnLoading"
                   :disabled="btnLoading">保存
        </el-button>
      </span>
    </el-dialog>
    <!-- 添加优惠券弹框 -->
    <!-- <el-dialog title="创建优惠券"
               :visible.sync="dialogVisible"
               width="70%">
      <p class="DTitle">基本信息：</p>
      <div class="flex">
        <p class="DT">卡券名称：</p>
        <el-input class="Dinput"
                  v-model="couponData.name"
                  placeholder="请输入卡券名称"
                  size="small">
        </el-input>
      </div>
      <div class="flex">
        <p class="DT">卡券面值：</p>
        <el-input class="Dinput"
                  type="number"
                  v-model="couponData.money"
                  placeholder="请输入卡券面值">
        </el-input>
        元
      </div>
      <div class="flex">
        <p class="DT">卡券logo：</p>
        <el-upload class="avatar-uploader"
                   :headers="headers"
                   :action="imageUrl"
                   :show-file-list="false"
                   name="file"
                   :on-success="handleAvatarSuccess3"
                   :before-upload="beforeAvatarUpload3">
          <img style="width:100%"
               v-if="couponData.photoUrl"
               :src="couponData.photoUrl"
               class="avatar" />
          <i v-else
             class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>
      <div class="flex">
        <p class="DT">有效期：</p>
        <el-radio @change="getvalidityRadio"
                  v-model="validityRadio"
                  :label="0">领取后立即生效
        </el-radio>
        有效期为
        <el-input :disabled="validityRadio?true:false"
                  class="Dinput"
                  v-model="couponData.claimedTime"
                  size="small"
                  style="width:10%">
        </el-input>
        天
      </div>
      <div class="flex"
           style="margin-left:120px">
        <el-radio @change="getvalidityRadio"
                  v-model="validityRadio"
                  :label="1">固定日期
        </el-radio>
        <el-date-picker type="date"
                        :disabled="!validityRadio?true:false"
                        v-model="couponData.validTimeStart"
                        size="small"
                        @change="getDate"
                        value-format="yyyy-MM-dd"
                        placeholder="选择日期">
        </el-date-picker>
        <span>--</span>
        <el-date-picker :disabled="!validityRadio?true:false"
                        v-model="couponData.validTimeEnd"
                        size="small"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="选择日期">
        </el-date-picker>
      </div>
      <div class="flex">
        <p class="DT">发放总量：</p>
        <el-radio v-model="couponData.sendType"
                  :label="1">不限
        </el-radio>
      </div>
      <div class="flex"
           style="margin-left:120px">
        <el-radio v-model="couponData.sendType"
                  :label="2">设置库存
        </el-radio>
        <el-input :disabled="couponData.sendType==1?true:false"
                  size="small"
                  v-model="couponData.totalInventory"
                  style="width:10%;margin:0 5px">
        </el-input>
        张
      </div>
      <div class="flex">
        <p class="DT">活动有效时间：</p>
        <el-date-picker v-model="couponData.actTimeStart"
                        size="small"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        type="date"
                        placeholder="选择日期">
        </el-date-picker>
        <span>--</span>
        <el-date-picker value-format="yyyy-MM-dd HH:mm:ss"
                        v-model="couponData.actTimeEnd"
                        size="small"
                        type="date"
                        placeholder="选择日期">
        </el-date-picker>
      </div>
      <div class="flex">
        <p class="DT">卡券说明：</p>
        <el-input class="Dinput"
                  v-model="couponData.remark"
                  size="small"
                  type="textarea">
        </el-input>
      </div>
      <p class="DTitle">规则配置：</p>
      <div class="flex">
        <p class="DT">最低消费：</p>
        <el-select @change="getLowest"
                   size="small"
                   v-model="lowSelect"
                   placeholder="请选择">
          <el-option v-for="item in options3"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
          </el-option>
        </el-select>
        <el-input v-if="LowestInput"
                  v-model="couponData.miniExpendLimit"
                  size="small"
                  style="width:10%;margin:0 5px">
        </el-input>
        <span v-if="LowestInput">元</span>
      </div>
      <div class="flex mt20">
        <p class="DT">使用时段：</p>
        <el-select @change="gettimeFrame"
                   size="small"
                   v-model="timeFrame"
                   placeholder="请选择">
          <el-option v-for="item in options4"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
          </el-option>
        </el-select>
        <div v-if="timeFrameInput"
             style="margin:0 5px;">
          <el-checkbox-group @change="getWeek"
                             v-model="useTimeWeekList">
            <el-checkbox v-for="(item,index) in Weekitems"
                         :key="item.id"
                         :label="item.id"
                         :checked="item.checked">{{item.content}}
            </el-checkbox>
          </el-checkbox-group>
        </div>
      </div>
      <div v-if="timeFrameInput"
           class="flex"
           style="margin:0px 0 20px 112px">
        <el-time-select v-model="sTimeDay"
                        size="small"
                        format="HH:mm"
                        :picker-options="{  start: '00:00', step: '00:15',end: '23:59' }"
                        placeholder="选择时间">
        </el-time-select>
        <span>--</span>
        <el-time-select v-model="eTimeDay"
                        size="small"
                        format="HH:mm"
                        :picker-options="{ start: '00:00', step: '00:15',end: '23:59' }"
                        placeholder="选择时间">
        </el-time-select>
      </div>
      <div class="flex">
        <p class="DT">使用门店：</p>
        <el-select @change="getsemend"
                   size="small"
                   v-model="couponData.storeIds"
                   placeholder="请选择">
          <el-option v-for="item in options5"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
          </el-option>
        </el-select>
        <p class="addquan"
           v-if="issemend && !shopIdNumber"
           @click="semendian">选择门店</p>
        <p style="margin:0 10px;"
           v-if="shopIdNumber>0">
          已选择{{shopIdNumber}}家，
          <span style="color:#1087C7"
                @click="semendian">查看或修改</span>
        </p>
      </div>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="dialogVisible = false"
                   size="small">取 消
        </el-button>
        <el-button type="primary"
                   @click="addcoupon"
                   size="small">确 定
        </el-button>
      </span>
    </el-dialog> -->
    <el-dialog title="选择门店"
               :visible.sync="dialogVisible2"
               width="50%">
      <div>
        <el-input v-model="shopName"
                  style="width:50%"
                  placeholder="输入门店名"
                  size="small">
        </el-input>
        <el-button type="primary"
                   size="small"
                   @click="getshopTable">搜索
        </el-button>
        <el-table ref="multipleTable"
                  :data="shopList"
                  tooltip-effect="dark"
                  style="width: 100%;margin-top:20px"
                  @selection-change="handleSelectionChange">
          <el-table-column type="selection"
                           label="选择"
                           align="center"
                           width="55">
          </el-table-column>
          <el-table-column prop="name"
                           label="门店"
                           align="center"
                           show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="address"
                           label="地址"
                           align="center"
                           show-overflow-tooltip>
          </el-table-column>
        </el-table>
      </div>
      <span slot="footer"
            class="dialog-footer">
        <el-button size="small"
                   @click="toggleSelection()">取 消
        </el-button>
        <el-button size="small"
                   type="primary"
                   @click="getshopList">确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { url } from '@/utils/request'
import {
  couponList,
  addRule,
  activityList,
  delRule,
  coupon,
  shopTable
} from '../api/fullReduceRules'
import pagination from '@/components/pagination/index'
export default {
  name: 'memberList',
  components: { pagination },
  data () {
    return {
      validityRadio: 0,
      sTimeDay: '',
      eTimeDay: '',
      timeFrame: 0,
      issemend: false,
      timeFrameInput: false,
      shopName: '',
      lowSelect: 0,
      shopIdNumber: 0,
      quanUrl: '',
      LowestInput: false,
      useTimeWeekList: [],
      imageUrl: url + '/member/sem/upload/imageOrVideo',
      value1: '',
      radio: '',
      headers: {
        authorized: sessionStorage.token
      },
      Weekitems: [
        { id: 1, content: '周一' },
        { id: 2, content: '周二' },
        { id: 3, content: '周三' },
        { id: 4, content: '周四' },
        { id: 5, content: '周五' },
        { id: 6, content: '周六' },
        { id: 7, content: '周日' }
      ],
      checked: '',
      dialogVisible: false,
      dialogVisible2: false,
      value: '',
      dialogGrade: false,
      isActive: false,
      actTimeStart: null,
      actTimeEnd: null,
      ruleData: {
        ruleName: '',
        startTime: '',
        endTime: '',
        status: 0,
        rule: [{ full: '', less: '' }]
      },
      options: [
        {
          value: 1,
          label: '开启'
        },
        {
          value: 0,
          label: '禁用'
        }
      ],
      options2: [
        {
          value: 1,
          label: '进行中'
        },
        {
          value: 2,
          label: '未开始'
        },
        {
          value: 3,
          label: '已结束'
        }
      ],
      options3: [
        {
          value: 0,
          label: '不限制'
        },
        {
          value: 1,
          label: '设置最低消费'
        }
      ],
      options4: [
        {
          value: 0,
          label: '不限制'
        },
        {
          value: 1,
          label: '设置使用时段'
        }
      ],
      options5: [
        {
          value: 0,
          label: '全部门店'
        },
        {
          value: 1,
          label: '指定门店'
        }
      ],
      state: '',
      loading: false, // 页面加载
      btnLoading: false, // 按钮锁定
      importUrl: url + '/member/member/member_import',
      tableData: [], // 表格数据
      tableTotalElement: 0, // 表格总条数
      // 模态框
      editMemberDialogVisible: false, // 编辑模态框
      editMemberDialogVisible2: false,
      getstatus: 0,
      couponData: {
        name: '',
        photoUrl: '',
        claimedTime: '',
        validTimeStart: null,
        validTimeEnd: null,
        sendType: 1,
        money: 0,
        totalInventory: '',
        actTimeStart: '',
        actTimeEnd: '',
        remark: '',
        miniExpendLimit: '',
        useTimeWeek: [],
        useTimeDay: '',
        storeIds: 0,
        validType: 0,
        photoId: '',
        interrupt: 0,
        type: 2,
        cardType: 1
      },
      actStatus: '',
      activitytableData: [],
      tableTotalElement2: 0,
      startTime: '',
      endTime: '',
      status: '',
      shopList: [],
      type: 2
    }
  },

  created () {
    this.getCouponList(1, 10)
    this.getactivityList(1, 10)
  },
  methods: {
    getDate (val) {
    },
    todelRule (id) {
      delRule(id)
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.getCouponList(1, 10)
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 确定选择门店
    getshopList () {
      let shopID = this.shopId.split(',')
      this.shopIdNumber = shopID.length
      this.dialogVisible2 = false
    },
    // 获取指定门店
    getsemend (val) {
      if (val) {
        this.issemend = true
      } else {
        this.issemend = false
      }
    },
    addcoupon () {
      if (this.quanUrl) {
        this.couponData.photoUrl = this.quanUrl
      }
      this.couponData.useTimeDay = this.sTimeDay + '-' + this.eTimeDay
      this.couponData.money = Number(this.couponData.money)
      this.couponData.miniExpendLimit = Number(this.couponData.miniExpendLimit)
      this.couponData.validType = this.validityRadio
      this.couponData.type = 1
      console.log(this.couponData, 'couponData')

      coupon(this.couponData)
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.dialogVisible = false
          this.getCouponList(1, 10)
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    getshopTable () {
      shopTable(this.shopName)
        .then(response => {
          this.shopList = response.obj.content
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 多选
    handleSelectionChange (val) {
      this.shopId = ''
      for (var i = 0; i < val.length; i++) {
        if (!this.shopId) {
          this.shopId = val[i].id
        } else {
          this.shopId += ',' + val[i].id
        }
        console.log(this.shopId)
      }
    },
    getsedate (val) {
      this.actTimeStart = val
      console.log(val)
    },
    getsedate2 (val) {
      this.actTimeEnd = val
      console.log(this.actTimeEnd)
    },
    getCouponList: function (pageNumber, pageSize) {
      this.loading = true
      couponList(
        this.actStatus,
        pageSize,
        pageNumber,
        this.actTimeStart,
        this.actTimeEnd,
        this.type
      )
        .then(response => {
          this.tableData = response.obj.content
          this.tableTotalElement = response.obj.totalElements
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    semendian () {
      this.dialogVisible = true
    },
    fTitle () {
      this.isActive = true
      console.log(this.isActive, 'fTitle')
    },
    to_couponDetail (index, id) {
      this.$router.push({
        path: '/alipayMember/views/couponDetail',
        query: { index: index, id: id }
      })
    },
    sTitle () {
      this.isActive = false
      console.log(this.isActive, 'sTitle')
    },
    cheackList () {
      this.getCouponList(1, 10)
    },
    getSatus (val) {
      this.actStatus = val
    },

    // changeStatus () {
    //   changeStatus({
    //     ...this.ruleData,
    //     rule: JSON.stringify(this.ruleData.rule)
    //   })
    //     .then(response => {
    //       this.$message({
    //         message: response.msg,
    //         type: 'success'
    //       })
    //       this.getactivityList(1, 10)
    //     })
    //     .catch(() => { })
    // },
    // 满减活动列表
    getactivityList: function (pageNumber, pageSize) {
      this.loading = true
      activityList(
        this.status,
        this.startTime,
        this.endTime,
        pageNumber,
        pageSize
      )
        .then(res => {
          let data = res.obj
          this.activitytableData = data.content
          this.tableTotalElement2 = data.totalElements
          this.activitytableData.forEach(item => {
            item.rule = JSON.parse(item.rule)
          })
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 满减禁用启用
    operation (row) {
      if (row.status === 0) {
        row.status = 1
      } else {
        row.status = 0
      }
      this.ruleData = row
      this.saveRuleData()
    },
    // 新增满减规则保存
    saveRuleData () {
      // 保存
      addRule({
        ...this.ruleData,
        rule: JSON.stringify(this.ruleData.rule)
      })
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.editMemberDialogVisible = false
          this.getactivityList(1, 10)
        })
        .catch(() => { })
    },
    //   添加规则
    addRule () {
      this.editMemberDialogVisible = true
      this.ruleData = { ruleName: '', startTime: '', endTime: '', status: 0, rule: [{ full: '', less: '' }] }
    },
    plusOutline () {
      if (this.ruleData.rule.length < 5) {
        this.ruleData.rule.push({
          full: '',
          less: ''
        })
      }
    },
    handleAvatarSuccess3 (res, file) {
      this.quanUrl = res.obj.imageUrl
      this.couponData.photoId = res.obj.imageId
      this.couponData.photoUrl = URL.createObjectURL(file.raw)
      console.log(this.couponData.photoUrl, 'photoUrl')
    },
    // 获取validityRadio
    getvalidityRadio (val) {
      console.log(val, 'getvalidityRadio')
    },
    beforeAvatarUpload3 (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    removeOuteline (index) {
      this.ruleData.rule.splice(index, 1)
    },
    // 获取最低消费
    getLowest (val) {
      if (val) {
        this.LowestInput = true
      } else {
        this.LowestInput = false
      }
    },
    // 获取星期几
    getWeek (val) {
      console.log(val, 'val')
      this.couponData.useTimeWeek = val.join(',')
    },
    // 获取时段
    gettimeFrame (val) {
      if (val) {
        this.timeFrameInput = true
      } else {
        this.timeFrameInput = false
      }
    },
    //   添加规则
    addRuleBtn () {
      if (this.isActive) {
        this.editMemberDialogVisible = true
        this.ruleData = {
          ruleName: '',
          startTime: '',
          endTime: '',
          status: 0,
          rule: [{ full: '', less: '' }]
        }
      } else {
        console.log(111)
        // this.dialogVisible = true
        this.$router.push({ path: '/alipayMember/views/createCoupon', query: { type: 2 } })
      }
    }
  }
}
</script>

<style scoped>
.addquan {
  border: 1px solid #1087C7;
  padding: 5px 10px;
  color: #1087C7;
  border-radius: 5px;
  font-size: 14px;
  margin: 0 5px;
}
.Dinput {
  width: 30%;
  margin: 10px 5px;
}
.DT {
  width: 120px;
  text-align: right;
  line-height: 50px;
}
.avatar-uploader {
  width: 100px;
  height: 100px;
  border: 1px dashed #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
}
.DTitle {
  margin: 20px 0;
  padding-left: 10px;
  font-weight: bold;
  border-left: 2px solid #333;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  /* flex-wrap: wrap; */
}
.active {
  border: 1px solid #1087C7;
  background: #d2e3d9;
}
.fullTitle {
  display: flex;
  margin-bottom: 20px;
}
.fullTitle p {
  width: 200px;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid #e3e3e3;
  margin-right: 20px;
}
.reductionRules {
  display: flex;
  align-items: flex-start;
}
.reductionRules p {
  margin-top: 25px;
}
/*public*/
.status {
  display: flex;
  justify-content: flex-start;
  padding: 0 20px;
}
.nodata {
  position: relative;
  left: 0;
  top: -20px;
  width: 100%;
  height: 100%;
  background-color: #3a3a3a;
}

.block {
  display: inline-block;
  vertical-align: middle;
}

/*private*/
.tab_header_bar {
  /* margin-bottom: 20px; */
}

.tab_bottom {
}

/*卡片*/
.card_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}

.card_item {
  padding: 0;
  width: 15%;
  max-width: 100%;
  margin: 20px 1% 0 1%;
}

.tools_box {
  padding: 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.tools_img {
  width: 30%;
  text-align: left;
}

.tools_text {
  width: 70%;
  text-align: right;
}

.tools_text p {
  margin: 0;
  padding: 0;
  cursor: default;
}

.tools_text p:nth-child(1) {
  font-size: 12px;
  color: #888;
}

.tools_text p:nth-child(2) {
  padding: 5px 0 0 0;
  font-size: 20px;
  color: #3f3f3f;
}

/*图表*/
.chart_img {
  width: 100%;
  height: 400px;
}

.lineTwo_chart_img {
  width: 50%;
  height: 400px;
}

.lineTwo_chart_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.analysisBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.analysis_table {
  border: 1px solid #cdcdcd;
  font-size: 14px;
}

.analysis_table thead tr {
  font-weight: bold;
  height: 24px;
  line-height: 28px;
  text-align: left;
}

.analysis_table tbody tr td {
  color: #718195;
}

.gender_bar_box {
}

.gender_bar_item {
  margin: 0 auto;
  width: 50%;
}

.bar_content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bar_title p {
  padding: 0;
  font-size: 16px;
  text-align: center;
}

.bar_icon_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.bar_icon {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.icon_img {
}

.bar_proportion {
  height: 20px;
  width: 100%;
  background-color: #ffac21;
}

.girl_mark {
  position: relative;
  top: 0;
  left: 0;
  height: 20px;
  background-color: #81be94;
}
span {
  font-size: 14px;
}
</style>



