<template>
  <div class="app-container" v-loading="loading">
    <div class="tab_header_bar">
      <el-button icon="el-icon-plus" size="small" class="block" type="primary"
                 @click="openCreatedDialog">添加二维码
      </el-button>

      <!--<el-button type="danger" icon="el-icon-delete" size="small"-->
      <!--@click="">删除-->
      <!--</el-button>-->
      <el-button icon="el-icon-refresh" size="small" class="block" @click="refresh">刷新</el-button>
      <!--选择门店-->
      <el-select class="block" v-if="userType==1" clearable v-model="searchForm.store" @change="searchQr" placeholder="选择门店"
                 size="small"
                 style="width: 120px">
        <el-option
          v-for="item in storeOption"
          :key="item.value"
          :label="item.name"
          :value="item.value">
        </el-option>
      </el-select>
      <!--选择二维码-->
      <!--<el-select v-model="searchForm.qr" clearable  @change="searchQr" placeholder="选择二维码" size="small" style="width: 140px">-->
      <!--<el-option-->
      <!--v-for="item in qrOption"-->
      <!--:key="item.value"-->
      <!--:label="item.name"-->
      <!--:value="item.value">-->
      <!--</el-option>-->
      <!--</el-select>-->
      <!--选择用户-->
<!--      <el-select v-model="searchForm.user" clearable @change="searchQr" placeholder="选择用户" size="small"-->
<!--                 style="width: 140px">-->
<!--        <el-option-->
<!--          v-for="item in userOption"-->
<!--          :key="item.value"-->
<!--          :label="item.name"-->
<!--          :value="item.value">-->
<!--        </el-option>-->
<!--      </el-select>-->

      <el-input class="block" v-model="searchForm.name" placeholder="请输入二维码名" @input="searchQr" size="small" style="width: 140px" ></el-input>

      <!--二维码状态-->
      <el-select v-model="searchForm.status" clearable @change="searchQr" placeholder="二维码状态" size="small"
                 style="width: 140px">
        <el-option
          v-for="item in statusOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <span class="tips">查看当前状态是“启用”或“禁用”的二维码</span>

    </div>
    <!--方块视图-->
    <div class="Area_box">

      <div :span="6" v-for="item in tableData" :key="item.id" class="qr-box">
        <el-card>
          <div>
            <div class="qrcode-item">
              <div class="qrcode-content">
                <span class="qr_image">
                  <!-- <img
                    :src="item.qrCode|qrcodeFil"
                    alt=""> -->
                    <img
                    :src="item.qrCode|qrcodeFil"
                    alt="" :id="item.id">
                </span>
              </div>
              <div class="qrcode-info">
                <div class="qrcode-text">
                  <label class="label-left">店名:</label>
                  <div class="label-right">{{item.storeName}}</div>
                </div>
                <div class="qrcode-text">
                  <label class="label-left">用户:</label>
                  <div class="label-right">{{item.userName}}</div>
                </div>
                <div class="qrcode-text">
                  <label class="label-left">名称:</label>
                  <div class="label-right">{{item.name}}</div>
                </div>
                <div class="qrcode-text">
                  <label class="label-left">金额:</label>
                  <div class="label-right">{{item.money}}</div>
                </div>
                <div class="qrcode-text">
                  <label class="label-left">描述:</label>
                  <div class="label-right">{{item.description}}</div>
                </div>
              </div>
            </div>
          </div>
          <div class="qr_btn">
            <!--<el-button size="small" @click="downLoadQE">下载</el-button>-->
            <div class="open status" v-if="item.status==1"></div>
            <div class="off status" v-if="item.status==2"></div>
            <div class="nullstatus status" v-if="item.status==null"></div>
            <el-button v-if="item.status==1" size="small" @click="offQR(item.id)">设为禁用</el-button>
            <el-button v-if="item.status==2" size="small" @click="onQR(item.id)">设为启用</el-button>
            <el-button size="small" @click="openEditDialog(item)">修改</el-button>
            <el-button size="small" @click="downLoadImg(item)">下载</el-button>
            <!--<el-button size="small">绑定设备</el-button>-->
          </div>
        </el-card>
      </div>

    </div>

    <!--分页-->
    <pagination
      :total-elements="totalElements"
      :change-callback="getqrcodeList"
      ref="page"></pagination>
    <!--添加二维码dialog-->
    <el-dialog
      title="添加二维码"
      :visible.sync="createdDialog"
      :before-close="closeCreatedDialog">
      <el-form :model="form"  ref="form">
        <el-form-item label="名称:" :label-width="formLabelWidth" required>
          <el-input v-model="form.name"></el-input>
        </el-form-item>

        <el-form-item v-if="userType=='1'" label="门店" :label-width="formLabelWidth" required>
          <el-select v-model="form.storeId" placeholder="请选择门店" @change="createFindUser">
            <el-option
              v-for="item in storeOption"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="用户" :label-width="formLabelWidth" required>
          <el-select v-if="userType=='1'" v-model="form.userId" placeholder="请选择用户">
            <el-option
              v-for="item in createdUserOption"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>

          <el-select v-if="userType=='2'" v-model="form.userId" placeholder="请选择用户">
            <el-option
              v-for="item in userOption"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>


        <el-form-item label="金额:" :label-width="formLabelWidth" prop="money">
          <el-input v-model="form.money"></el-input>
        </el-form-item>
        <el-form-item label="描述:" :label-width="formLabelWidth">
          <el-input v-model="form.description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeCreatedDialog">取 消</el-button>
        <el-button type="primary" @click="addprinterArea" :loading="btnLoading">确 定</el-button>
      </div>
    </el-dialog>


    <el-dialog
      title="编辑二维码信息"
      :visible.sync="editDialog"
      :before-close="closeEditDialog">
      <el-form :model="editQRForm"  ref="editQRForm">
        <el-form-item label="二维码名称" :label-width="formLabelWidth" required>
          <el-input v-model="editQRForm.name"></el-input>
        </el-form-item>

        <el-form-item v-if="userType=='1'" label="门店" :label-width="formLabelWidth" required>
          <el-select v-model="editQRForm.storeId" placeholder="请选择门店" @change="editFindUser">
            <el-option
              v-for="item in storeOption"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="用户" :label-width="formLabelWidth" required>
          <el-select v-if="userType=='1'" v-model="editQRForm.userId" placeholder="请选择用户">
            <el-option
              v-for="item in editUserOption"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>

          <el-select v-if="userType=='2'" v-model="editQRForm.userId" placeholder="请选择用户">
            <el-option
              v-for="item in userOption"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="二维码金额" :label-width="formLabelWidth" prop="money">
          <el-input v-model="editQRForm.money"></el-input>
        </el-form-item>
        <el-form-item label="二维码描述" :label-width="formLabelWidth">
          <el-input v-model="editQRForm.description"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeEditDialog">取 消</el-button>
        <el-button type="primary" @click="editQR" :loading="btnLoading">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 二维码下载功能2019-9-18 锋之云 -->
    <el-dialog title="二维码尺寸" :visible.sync="dialogTableVisible" width="34%">
      <el-table id="scope-text" :data="gridData" style="">
        <el-table-column property="date" label="二维码边长(cm)" width="200"></el-table-column>
        <el-table-column property="name" label="建议扫描距离(米)" width="200"></el-table-column>
        <el-table-column label="操作" width="200">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">
              下载
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      
      <div slot="footer" class="dialog-footer" style="text-align: center;">
        <el-button type="primary" @click="dialogTableVisible = false" class="Brand Color">取 消</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  import {
    qrcodeList,
    qrcodeAdd,
    qrcodeUpdate,
    findMerchant,
    findUser,
    onQRCODE,
    offQRCODE,
    findUserByStore
  } from '../api/qrcode'
  import pagination from '@/components/pagination/index'
  import Vue from 'vue'
  import {returnThumbnailUrl, returnFilesUrl, uploadUrl} from '@/modules/file/api/upload'
  import {url} from '@/utils/request'
  // Vue.prototype.publicUrl=
  export default {
    name: 'printerArea',
    data () {
      var validateActualPrice = (rule, value, callback) => {
        let reg = /^(\d+|\d+\.\d{1,2})$/
        if (!reg.test(value)) {
          callback(new Error('金额格式错误'))
        } else {
          callback()
        }
      }
      return {
        tableData: [], // 表格数据
        totalElements: 0, // 数据条数
        multipleSelection: [], // 选中数据-多选
        loading: true, // loading效果
        createdDialog: false, // 二维码模态框
        formLabelWidth: '120px',
        printerOptions: [],
        specListForm: {
          list: [],
        },
        form: {
          name: '',
          money: '',
          storeId: '',
          userId: '',
          description: '',
          status: '1'
        }, // 二维码数据
        btnLoading: false, // 按钮loading
        editDialog: false, // 编辑模态框,
        editQRForm: {
          id: '',
          name: '',
          storeId: '',
          userId: '',
          money: '',
          description: '',
        },
        headers: {
          authorized: sessionStorage.token
        }, // 请求头部
        userType: '',//用户权限类型

        //  搜索
        searchForm: {
          store: '',
          qr: '',
          user: '',
          status: '',
          name:''
        },//搜索条件
        storeOption: [],//门店选项
        qrOption: [
          {value: 0, name: '二维码1'},
          {value: 1, name: '二维码2'},
          {value: 2, name: '二维码3'},
        ],//二维码选项
        userOption: [],//用户选项
        createdUserOption: [],//用户选项
        editUserOption: [],//用户选项
        statusOption: [
          {value: 1, label: '启用'},
          {value: 2, label: '禁用'}
        ],//状态选项

        //规则
        QrRules: {
          name: [
            {required: true, message: '必须输入名称', trigger: 'blur'},
            {min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur'}
          ],
          money: [
            {required: true, message: '必须输入金额', trigger: 'blur'},
            {validator: validateActualPrice, trigger: 'blur'}
          ],
          userId: [
            {required: true, message: '请选择用户', trigger: 'blur'}
          ],
          storeId: [
            {required: true, message: '请选择门店', trigger: 'blur'}
          ],
        },
        /**二维码下载功能数据 */
        gridData: [{
          date: '8cm',
          name: '0.5m',
        }, {
          date: '12cm',
          name: '0.8m',
        }],
        dialogTableVisible: false,
        imageInfo: '',

      }
    },
    components: {pagination},

    mounted () {
      //缺省获取列表
      this.getqrcodeList(1, 10)
      //用户类型
      this.userType = sessionStorage.getItem('userType')

      //获取门店下拉
      this.getStoreOption()
      //获取用户下拉
      this.getUserOption()
    },
    filters: {
      //二维码换成http路径
      qrcodeFil: function (val) {
        //resource
        let urltxt = url + '/resource/' + val
        return urltxt
      }
    },

    methods: {
      /**
       *获取门店下拉
       */
      getStoreOption: function () {
        findMerchant().then(res => {
          console.log(res)
          let data = res.obj
          this.storeOption = data
        }).catch(e => {

        })
      },
      /**
       * 搜索
       */
      searchQr: function () {
        this.getqrcodeList(1, 10)
      },

      /**
       *获取用户下拉
       */
      getUserOption: function () {
        findUser().then(res => {
          console.log(res)
          let data = res.obj
          this.userOption = data
        }).catch(e => {

        })
      },
      /**
       * 获取二维码列表
       * */
      getqrcodeList: function (number, pageSize) {
        this.loading = true
        qrcodeList(number, pageSize, this.searchForm).then(response => {
          let data = response.obj
          //console.log(data)
          this.totalElements = data.totalElements
          this.tableData = data.content
          console.log(this.tableData)
          this.loading = false
        }).catch(() => {
          this.loading = false
        })
      },
      /**
       * 刷新
       * */
      refresh () {
        this.$refs.page.refresh()
      },

      /**
       * 通过门店ID获取用户 新增
       */
      createFindUser: function () {
        let id = this.form.storeId
        this.$set(this.form, 'userId', '')
        findUserByStore(id).then(res => {
          let data = res.obj
          this.createdUserOption = data
        }).catch(e => {
          this.$message('获取门店店员失败。')
        })
      },
      /**
       * 通过门店ID获取用户 编辑
       */
      editFindUser: function () {
        this.$set(this.editQRForm, 'userId', '')
        findUserByStore(this.editQRForm.storeId).then(res => {
          this.editUserOption = res.obj
        }).catch(e => {

        })
      },

      /*
      * 打开二维码模态框
      * */
      openCreatedDialog () {
        this.form = {
          name: '',
          money: '',
          description: '',
        }
        this.createdDialog = true
      },
      /*
      * 关闭二维码模态框
      * */
      closeCreatedDialog () {
        this.form = {
          name: '',
          money: '',
          description: '',
        }
        this.createdDialog = false
      },
      /*
      * 添加二维码
      * */
      addprinterArea () {
        /*
        this.$refs['form'].validate((valid) => {
          if (valid) {
            console.log('ok')
          } else {
            this.$message.error('还有未输入的内容。')
            return false
          }
        })
        */
        this.loading = true
        this.btnLoading = true
        qrcodeAdd(this.form).then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.btnLoading = false
          this.closeCreatedDialog()
          this.getqrcodeList()
        }).catch(() => {
          this.btnLoading = false
          this.loading = false
        })
      },
      /*
      * 打开编辑二维码模态框
      * */
      openEditDialog (row) {
        this.editQRForm.id = row.id
        this.editQRForm.money = row.money
        this.editQRForm.storeId = row.storeId
        this.editQRForm.userId = row.userId
        this.editQRForm.name = row.name
        this.editQRForm.description = row.description

        if (this.userType == 1) {
          findUserByStore(row.storeId).then(res => {
            this.editUserOption = res.obj
          }).catch(e => {

          })
        } else if (this.userType == 2) {
          findUser().then(res => {
            console.log(res)
            let data = res.obj
            this.userOption = data
          }).catch(e => {

          })
        }

        this.editDialog = true

      },
      /*
      * 关闭编辑二维码模态框
      * */
      closeEditDialog () {

        this.$refs['editQRForm'].resetFields()
        this.editDialog = false
      },
      /*
      * 编辑二维码
      * */
      editQR () {
        /*
        this.$refs['editQRForm'].validate((valid) => {
          if (valid) {
          } else {
            this.$message.error('还有未输入的内容。')
            return false
          }
        })
        */
        this.loading = true
        this.btnLoading = true
        qrcodeUpdate(this.editQRForm).then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.btnLoading = false
          this.loading = false
          this.closeEditDialog()
          this.refresh()
        }).catch(() => {
          this.loading = false
          this.btnLoading = false
        })
      },
      /**
       *关闭二维码
       */
      offQR: function (data) {
        offQRCODE(data).then(res => {
          this.$message.success('禁用二维码成功')
          this.refresh()
        }).catch(e => {

        })
      },
      /**
       *开启二维码
       */
      onQR: function (data) {
        onQRCODE(data).then(res => {
          this.$message.success('启用二维码成功')
          this.refresh()
        }).catch(e => {

        })

      },
      /**
       *下载二维码
       */
      downLoadQE: function (data) {

      },

      getImg: function (id) {
        //console.log('图片ID' + id)
        if (id == null || id == '') {
          return ''
        }
        return returnThumbnailUrl(id)
      },

      /*
      * 搜索
      * */
      search () {
        this.$refs.page.search()
      },
      /**
       * 下载二维码
       */
      downLoadImg(item){
        this.dialogTableVisible = true;
        this.imageInfo = item;
      },

      /**
       * 确认下载二维码
       */
      handleEdit(index, row) {
        let itemImg = this.imageInfo
        let img = document.getElementById(itemImg.id)
        let src = img.src;
        let widthImg = 258;
        let heightImg = 258;
        if(index == 1){
          widthImg = 368;
          heightImg = 368;
        }
        let canvas = document.createElement('canvas');
        img.onload = function(e) {
            canvas.width = widthImg;
            canvas.height = heightImg;
            index = index + 1
            canvas.getContext('2d').drawImage(img, 0, 0, widthImg, heightImg, 0, 0, widthImg * index + 192, heightImg * index + 192);
            canvas.toBlob((blob)=>{
                let link = document.createElement('a');
                link.href = window.URL.createObjectURL(blob);
                link.download = 'fzy_' + (new Date()).getTime(); 
                link.click();  
            }, "image/jpeg");
        }
        img.setAttribute("crossOrigin",'Anonymous');
        img.src = src;

      },
    }
  }
</script>

<style scoped>
  .logo-uploader {
    display: inline-block;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .logo-uploader:hover {
    border-color: #409EFF;
  }

  .logo-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .logo {
    width: 178px;
    height: 178px;
    display: block;
  }

  /*方块视图*/

  .Area_box {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

  }

  .qr-box {
    width: 24%;
    margin-right: 1%;
    margin-bottom: 1%;
    height: auto;
  }


  /*卡片*/
  .qrcode-item .qrcode-content {
    float: left;
    display: inline;
  }

  .qrcode-item .qrcode-info {
    padding-top: 4px;
  }

  .qr_image {
    width: 140px;
    height: 140px;
  }

  .qr_image img {
    width: 140px;
    height: 140px;
  }

  .qrcode-text {
    font-size: 14px;
    line-height: 22px;
  }

  .qrcode-text .label-left {
    display: inline-block;
    color: #808080;
    max-width: 100%;
    margin-bottom: 5px;
    font-weight: 700;
  }

  .qrcode-text .label-right {
    padding: 0 0 0 3px;
    display: inline-block;
    color: #515151;
  }

  .qr_btn {
    padding: 0 0 0 8px;
    clear: both;
  }

  .tips {
    font-size: 13px;
    color: #808080;
  }

  .status {
    display: inline-block;
    margin-right: 10px;
    width: 10px;
    height: 10px;
    border-radius: 5px;

  }

  .open {
    background-color: #409eff;
  }

  .off {
    background-color: #f56c6c;
  }

  .nullstatus {
    background-color: #808080;
  }

</style>



