<template>
  <div class="app-container"
       v-loading="loading">
    <!--内容-->
    <!--列表-->
    <div class="tab_header_bar">
      <div class="block">
        <el-date-picker v-model="getstartTime"
                        type="date"
                        size="small"
                        placeholder="开始日期"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        style="width: 190px"
                        @change="getsedate"></el-date-picker>
        <span>--</span>
        <el-date-picker v-model="getendTime"
                        type="date"
                        size="small"
                        placeholder="结束日期"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        style="width: 190px"
                        @change="getsedate2"></el-date-picker>
      </div>
      <div class="block">
        <el-select clearable
                   size="small"
                   v-model="state"
                   placeholder="状态"
                   @change="getSatus">
          <el-option v-for="item in options"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value"></el-option>
        </el-select>
      </div>
      <div class="block"
           style="margin-left: 10px">
        <el-button size="small"
                   type="primary"
                   icon="el-icon-search"
                   @click="cheackList">查 询</el-button>
      </div>
    </div>
    <div style="width:100%;display: flex;justify-content: flex-end;">
      <el-button @click="addRule"
                 type="primary"
                 size="small">新增规则</el-button>
    </div>
    <div class="tab_bottom">
      <!--表格-->
      <el-table :data="tableData"
                style="width: 100%">
        <el-table-column type="index"
                         label="序号"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="ruleName"
                         label="规则名称"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="endTime"
                         label="有效期"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">{{scope.row.startTime}}~{{scope.row.endTime}}</template>
        </el-table-column>
        <el-table-column label="满减规则"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <div v-for="(item,index) in scope.row.rule"
                 :key="index">订单满{{item.full?item.full:0}}减{{item.less?item.less:0}}
                 <span v-if="index!==scope.row.rule.length-1">，</span></div>

          </template>
        </el-table-column>
        <el-table-column prop="status"
                         label="状态"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{!scope.row.status?'禁用':'开启'}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <el-button @click="operation(scope.row)"
                       type="text"
                       size="small">{{scope.row.status?'禁用':'开启'}}</el-button>
          </template>
        </el-table-column>
      </el-table>

      <pagination :total-elements="tableTotalElement"
                  :change-callback="getruleList"
                  ref="page"></pagination>
    </div>

    <!--模态框-->
    <el-dialog title="新增满减规则"
               :visible.sync="editMemberDialogVisible"
               width="40%">
      <div class="justcenter mt">
        <span class="red">*</span>
        <span class="weight7">规则名称</span>
        <el-input v-model="ruleData.ruleName"
                  class="ml20"
                  style="width:80%;"
                  size="mini"></el-input>
      </div>
      <div class="justcenter mt">
        <span class="red">*</span>
        <span class="weight7">有效日期</span>
        <el-date-picker class="ml20"
                        size="mini"
                        style="width:35%"
                        v-model="ruleData.startTime"
                        type="date"
                        value-format="timestamp"
                        placeholder="开始日期"
                        @change="getdateVal"></el-date-picker>
        <span style="margin:10px">--</span>
        <el-date-picker size="mini"
                        style="width:35%"
                        value-format="timestamp"
                        v-model="ruleData.endTime"
                        type="date"
                        placeholder="结束日期"
                        @change="getdateVal2"></el-date-picker>
      </div>
      <div class="reductionRules">
        <p>
          <span class="red">*</span>
          <span class="weight7">满减规则</span>
        </p>
        <div style="width:84%">
          <div class="justleft ml20 mt"
               v-for="(item,index) in ruleData.rule"
               :key="index">
            <span>订单满</span>
            <el-input v-model="item.full"
                      style="width:25%;margin:0 5px"
                      size="mini"></el-input>元减
            <el-input v-model="item.less"
                      style="width:25%;margin:0 5px"
                      size="mini"></el-input>元
            <i @click="plusOutline"
               class="el-icon-circle-plus-outline ml20"
               style="font-size:20px"></i>
            <i @click="removeOuteline(index)"
               v-if="ruleData.rule.length>1"
               class="el-icon-remove-outline ml20"
               style="font-size:20px"></i>
          </div>
        </div>
      </div>
      <div class="justcenter mt status">
        <span class="weight7 mr20 ml20">状态</span>
        <span :style="ruleData.status==1?'color:#000':'color:#1087C7'">禁用</span>
        <el-switch style="margin:0 10px"
                   size="small"
                   v-model="ruleData.status"
                   :active-value="1"
                   :inactive-value="0"
                   active-color="#1087C7"
                   inactive-color="#ccc"></el-switch>
        <span :style="ruleData.status==0?'color:#000':'color:#1087C7'">开启</span>
      </div>
      <!--***-->
      <span slot="footer"
            class="dialog-footer">
        <el-button size="small"
                   @click="editMemberDialogVisible = false">取 消</el-button>
        <el-button @click="saveRuleData"
                   size="small"
                   type="primary"
                   v-loading="btnLoading"
                   :disabled="btnLoading">保存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import pagination from '@/components/pagination/index'
import { url } from '@/utils/request'
import { ruleList, addRule } from '../api/fullReduceRules'

export default {
  name: 'memberList',
  components: { pagination },
  data () {
    return {
      getstartTime: '',
      getendTime: '',
      open: false,
      ruleData: {
        ruleName: '',
        startTime: '',
        endTime: '',
        status: 0,
        rule: [{ full: '', less: '' }]
      },
      options: [
        {
          value: 1,
          label: '开启'
        },
        {
          value: 0,
          label: '禁用'
        }
      ],
      value3: '',
      editMemberClose: false,
      state: '',
      loading: false, // 页面加载
      btnLoading: false, // 按钮锁定
      tabsName: '1', // 标签页
      importUrl: url + '/member/member/member_import',
      headers: {
        authorized: sessionStorage.token
      },
      searchForm: {
        memberNum: '',
        startTime: '',
        endTime: '',
        phone: ''
      }, // 搜索条件
      tableData: [], // 表格数据
      newRule: [],
      tableTotalElement: 0, // 表格总条数

      // 模态框
      editMemberDialogVisible: false, // 编辑模态框
      editForm: {
        id: '',
        nickname: '',
        phone: '',
        sex: '',
        birthday: ''
      },
      editMemberRules: {
        nickname: [
          { required: true, message: '请输入完整姓名', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 2 到 12 个字符', trigger: 'blur' }
        ],
        sex: [{ required: true, message: '必须选择性别', trigger: 'blur' }]
      }, // 编辑表单验证

      //* *********************************************
      dayRange: '0', // 日期范围
      getstatus: '',
      // 会员活跃度分析
      activityData: [{ name: '暂无数据', num: '暂无数据' }], // 下图表数据
      activityDataTotal: 0
    }
  },

  created () {
    this.getruleList()
  },
  methods: {
    getsedate (val) {
      this.getstartTime = val
      console.log(this.getstartTime)
    },
    getsedate2 () { },
    cheackList () {
      this.getruleList()
    },
    getSatus (val) {
      console.log(val, '状态')
      this.getstatus = val
    },
    operation (row) {
      if (row.status === 0) {
        row.status = 1
      } else {
        row.status = 0
      }
      this.ruleData = row
      this.saveRuleData()
    },
    getdateVal (val) {
      this.startTime = val
      console.log(this.startTime, '开始时间')
    },
    getdateVal2 (val) {
      this.endTime = val
      console.log(this.endTime, '结束时间')
    },
    saveRuleData () {
      // 保存
      addRule({
        ...this.ruleData,
        rule: JSON.stringify(this.ruleData.rule)
      })
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.editMemberDialogVisible = false
          this.getruleList()
        })
        .catch(() => { })
    },
    plusOutline () {
      if (this.ruleData.rule.length < 5) {
        this.ruleData.rule.push({
          full: '',
          less: ''
        })
      }
    },
    removeOuteline (index) {
      this.ruleData.rule.splice(index, 1)
    },
    //   添加规则
    addRule () {
      this.editMemberDialogVisible = true
      this.ruleData = { ruleName: '', startTime: '', endTime: '', status: 0, rule: [{ full: '', less: '' }] }
    },
    getruleList: function (pageNumber, pageSize) {
      this.loading = true
      ruleList(this.getstatus, this.getstartTime, this.getendTime, pageNumber, pageSize)
        .then(response => {
          let data = response.obj
          this.tableTotalElement = data.totalElements
          this.tableData = data.content
          this.tableData.forEach(item => {
            item.rule = JSON.parse(item.rule)
          })
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    }
  }
}
</script>

<style scoped>
.reductionRules {
  display: flex;
  align-items: flex-start;
}
.reductionRules p {
  margin-top: 25px;
}
/*public*/
.status {
  display: flex;
  justify-content: flex-start;
  padding: 0 20px;
}
.nodata {
  position: relative;
  left: 0;
  top: -20px;
  width: 100%;
  height: 100%;
  background-color: #3a3a3a;
}

.block {
  display: inline-block;
  vertical-align: middle;
}

/*private*/
.tab_header_bar {
  /* margin-bottom: 20px; */
}

.tab_bottom {
}

/*卡片*/
.card_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}

.card_item {
  padding: 0;
  width: 15%;
  max-width: 100%;
  margin: 20px 1% 0 1%;
}

.tools_box {
  padding: 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.tools_img {
  width: 30%;
  text-align: left;
}

.tools_text {
  width: 70%;
  text-align: right;
}

.tools_text p {
  margin: 0;
  padding: 0;
  cursor: default;
}

.tools_text p:nth-child(1) {
  font-size: 12px;
  color: #888;
}

.tools_text p:nth-child(2) {
  padding: 5px 0 0 0;
  font-size: 20px;
  color: #3f3f3f;
}

/*图表*/
.chart_img {
  width: 100%;
  height: 400px;
}

.lineTwo_chart_img {
  width: 50%;
  height: 400px;
}

.lineTwo_chart_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.analysisBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.analysis_table {
  border: 1px solid #cdcdcd;
  font-size: 14px;
}

.analysis_table thead tr {
  font-weight: bold;
  height: 24px;
  line-height: 28px;
  text-align: left;
}

.analysis_table tbody tr td {
  color: #718195;
}

.gender_bar_box {
}

.gender_bar_item {
  margin: 0 auto;
  width: 50%;
}

.bar_content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bar_title p {
  padding: 0;
  font-size: 16px;
  text-align: center;
}

.bar_icon_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.bar_icon {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.icon_img {
}

.bar_proportion {
  height: 20px;
  width: 100%;
  background-color: #ffac21;
}

.girl_mark {
  position: relative;
  top: 0;
  left: 0;
  height: 20px;
  background-color: #81be94;
}
span {
  font-size: 14px;
}
</style>



