<template>
  <div class="scancode" @click="to_code">
    <p>点击可立即前往扫码点餐</p>
    <i class="el-icon-d-arrow-right"></i>
  </div>
</template>
<script>
import { url } from "@/utils/request";
export default {
  data() {
    return {};
  },

  created() {},
  methods: {
    to_code() {
      window.location.href =
        url +
        "/app/fp_smdc.php?i=231485440&c=entry&do=admin&action=index&m=dy_more";
    }
  }
};
</script>

<style scoped>
i {
  margin: 0 5px;
}
.scancode {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #1087C7;
}
</style>


