<template>
  <div class="login-container"
       v-bind:style="bgStyle">
    <div class="login-header">
      <div class="header-left">
        <img v-if="siteInfo.logo"
             :src="siteInfo.logo"></div>
      <div class="header-right">
        <!-- <span>客服热线：400-021-1220</span>
        <span><img src="../../assets/images/login/weChat.png">关注微信公众号</span> -->
      </div>
    </div>

    <div class="login-form loginform">
      <p class="title_zh">商户后台管理系统</p>
      <el-form class="form-box">
        <el-form-item class="form-item-box">
          <el-input v-model.trim="loginForm.username"
                    placeholder="请输入登录账号"></el-input>
          <i class="horn hornTF"></i>
          <i class="horn hornTR"></i>
          <i class="horn hornBF"></i>
          <i class="horn hornBR"></i>
        </el-form-item>

        <el-form-item>
          <el-input v-model="loginForm.password"
                    :type="pwdType"
                    @keyup.enter.native="handleLogin"
                    placeholder="请输入密码"></el-input>
          <i class="horn hornTF"></i>
          <i class="horn hornTR"></i>
          <i class="horn hornBF"></i>
          <i class="horn hornBR"></i>
        </el-form-item>

        <el-form-item class="loginBtn">
          <el-button type="primary"
                     :loading="loading"
                     @click="handleLogin"
                     class="login-btn"
                     style="">登录</el-button>
        </el-form-item>

        <el-form-item class="fogotBtn_lay">
          <div style="float:left;">
            <el-checkbox v-model="checked">记住密码</el-checkbox>
          </div>
          <div style="float:right;">
            <el-button type="text"
                       class="fogotBtn"
                       @click="goFogotPw">忘记密码？</el-button>
          </div>
        </el-form-item>
      </el-form>
      <div class="null-box"></div>
    </div>
    <div class="login-footer">
      <p>技术支持：{{info.techSupport||''}}</p>
      <p>网站ICP备案号：{{info.icpInfo||''}} Copyright {{info.copyrightInfo||''}}</p>
    </div>
  </div>
</template>

<script>
import { getSiteInfo } from '@/modules/login/api/login'
import { returnThumbnailUrl, returnFilesUrl, uploadUrl } from '@/modules/file/api/upload' // 图片操作API

export default {
  name: 'login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      siteInfo: {
        bg: '',
        logo: ''
      },
      bgStyle: {
        background: '',
        backgroundSize: ''
      },
      loading: false,
      pwdType: 'password',
      info: {},
      checked: false
    }
  },
  mounted () {
    // 获取默认信息
    this.getInfo()
    this.getCookie()
  },
  methods: {
    showPwd () {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },
    handleLogin () {
      // 判断复选框是否被勾选 勾选则调用配置cookie方法
      if (this.checked) {
        // 传入账号名，密码，和保存天数3个参数
        this.setCookie(this.loginForm.username, this.loginForm.password, 7)
      } else {
        // 清空Cookie
        this.clearCookie()
      }
      this.loading = true
      this.$store.dispatch('login', this.loginForm).then(() => {
        this.$router.replace({ path: '/' })
      }).catch(() => {
        this.loading = false
      })
    },
    /**
     * 获取信息
     */
    getInfo: function () {
      getSiteInfo().then(res => {
        console.log(res)
        let data = res.obj
        // 如果有背景图
        if (data.mctBgmPhoto !== '' && data.mctBgmPhoto != null) {
          this.siteInfo.bg = "url('" + this.getImg(data.mctBgmPhoto) + "') 50% no-repeat"
          this.bgStyle.background = this.siteInfo.bg // 背景图片background: url(/static/img/bg.e499aee.png) 50% no-repeat;
          this.bgStyle.backgroundSize = 'cover' // 100%大小
        }
        // 如果有LOGO
        if (data.logo !== '' && data.logo != null) {
          this.siteInfo.logo = this.getImg(data.logo)
          sessionStorage.removeItem('logoImg')
          sessionStorage.setItem('logoImg', this.getImg(data.logo))
        }
        this.info = data
      })
    },
    /**
     * 换取图片
     */
    getImg: function (val) {
      let reg = /^[0-9]*$/
      if (reg.test(val)) {
        return returnFilesUrl(val)
      } else {
        let base64 = 'data:image/png;base64,'
        return base64
      }
    },
    /**
     * 忘记密码
     */
    goFogotPw () {
      this.$alert('请联系管理员！', '提示', {
        confirmButtonText: '确定',
        type: 'warning',
        callback: action => {
        }
      })
      // this.$router.push({ path: '/fogot' })
    },
    // 设置cookie
    setCookie (c_name, c_pwd, exdays) {
      var exdate = new Date() // 获取时间
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) // 保存的天数
      // 字符串拼接cookie
      window.document.cookie = 'userName' + '=' + c_name + ';path=/;expires=' + exdate.toGMTString()
      window.document.cookie = 'userPwd' + '=' + c_pwd + ';path=/;expires=' + exdate.toGMTString()
    },
    // 读取cookie
    getCookie () {
      if (document.cookie.length > 0) {
        var arr = document.cookie.split('; ') // 这里显示的格式需要切割一下自己可输出看下
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split('=') // 再次切割
          // 判断查找相对应的值
          if (arr2[0] == 'userName') {
            this.loginForm.username = arr2[1] // 保存到保存数据的地方
            this.checked = true
          } else if (arr2[0] == 'userPwd') {
            this.loginForm.password = arr2[1]
          }
        }
      }
    },
    // 清除cookie
    clearCookie: function () {
      this.setCookie('', '', -1) // 修改2值都为空，天数为负1天就好了
    }
  }
}
</script>



