<template>
  <div>
    <el-form  ref="validateForm" label-width="100px" :model="params" :rules="rules" @submit.native.prevent>
      <el-form-item label="状态:" prop="status">
        <el-switch v-model="params.status"
                   :active-value="1"
                   :inactive-value="0">
        </el-switch>
        <span>{{params.status?'启用':'禁用'}}</span>
      </el-form-item>
      <el-form-item label="设备id:" prop="deviceId">
        <el-input size="small"
                  placeholder="设备id"
                  :maxlength="200"
                  v-model.trim="params.deviceId"></el-input>
      </el-form-item>
      <el-form-item label="语音启用:" prop="isSuffix">
        <el-switch v-model="params.isSuffix"
                   :active-value="1"
                   :inactive-value="0">
        </el-switch>
        <span>{{params.isSuffix?'启用':'禁用'}}</span>
      </el-form-item>
      <el-form-item label="语音内容:" :required="params.isSuffix?true:false">
        <el-input size="small"
                  placeholder="语音内容(15字内)"
                  :maxlength="15"
                  v-model.trim="params.advStr"></el-input>
      </el-form-item>
      <el-form-item label="备注:" prop="remark">
        <el-input placeholder="请输入备注"
                  type="textarea"
                  :rows="5"
                  :maxlength="200"
                  v-model.trim="params.remark">
        </el-input>
      </el-form-item>
      <el-form-item style="margin-bottom:0;">
        <div class="vm-text-right">
          <el-button size="medium" @click="propsInfo.show = false">取 消</el-button>
          <el-button size="medium" type="primary" @click="submitInfo('validateForm')">确 定</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import * as storeApi from '../../api/store'
export default {
  mixins: [],
  components: {
    // ImgUpload
  },
  props: {
    storeId: {
      type: [String, Number],
      default: null
    },
    propsInfo: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      params: {
        deviceId: null,
        remark: null,
        status: 1,
        storeId: null,
        isSuffix: 0,
        advStr: ''
      },
      rules: {
        deviceId: [
          { required: true, message: '请输入设备id' }
        ]
      }
    }
  },
  watch: {},
  computed: {},
  created () {
    if (this.propsInfo.type === 2) {
      this.params = Object.assign({}, this.propsInfo.data)
    } else {
      this.params.storeId = this.storeId
    }
  },
  methods: {
    submitInfo (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.params.isSuffix && (!this.params.advStr)) {
            this.$message.warning('请输入语音内容')
          } else {
            this.confirmSubmit()
          }
        } else {
          return false
        }
      })
    },
    confirmSubmit () {
      storeApi.hornListAdd(this.params).then(res => {
        this.$message.success('保存成功')
        this.$emit('getTableList')
        this.propsInfo.show = false
      })
    }
  }
}
</script>



