<template>
  <div class="app-container" v-loading="loading">
    <div>

      <el-card class="box-card">

        <div slot="header" class="clearfix">
          <span>流水概览</span>
        </div>

        <!--功能区-->

        <div>
          <!--搜索条-->
          <div>
            <div class="search_bar">
              <div>
                <!--上方选项-->
                <div class="tab_header_bar">
                  <!--选项条-->
                  <div class="block">
                    <el-radio-group v-model="searchDays" size="small" @change="changeDays($event)">
                      <el-radio-button label="0">近7日</el-radio-button>
                      <el-radio-button label="1">近15日</el-radio-button>
                      <el-radio-button label="2">近30日</el-radio-button>
                    </el-radio-group>
                  </div>
                  <!--日期选择-->
                  <div class="block">
                    <el-date-picker
                      @change="checkDataRange"
                      v-model="searchForm.days"
                      :clearable="false"
                      type="daterange"
                      range-separator="至"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                      format="yyyy 年 MM 月 dd 日"
                      value-format="timestamp"
                      size="small">
                    </el-date-picker>
                  </div>
                  <!--按钮-->
                  <div class="block">
                    <el-button type="primary" size="small" @click="findCountData">搜 索
                    </el-button>
                    <el-button type="" size="small" @click="reset">重 置</el-button>
                  </div>
                </div>
                <!--下方选项-->
                <div style="margin-top: 10px">

                </div>
              </div>
            </div>
          </div>
        </div>
        <!--卡片区-->
        <div>
          <div class="card_group">
            <!--实际营收-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/实际营收.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>实际营收
                      <el-tooltip class="item" effect="light" content="实际营收=商户实收 - 商户实退，由于各渠道差异，该数据可能产生偏差。"
                                  placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>￥{{cardData.actualRevenue || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>
            <!--商户实收-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/商户实收.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>商户实收
                      <el-tooltip class="item" effect="light" content="商户实际到账金额总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>￥{{cardData.settlementTotalFee || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>
            <!--商户实退-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/商户实退.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>商户实退
                      <el-tooltip class="item" effect="light" content="商户实际退款金额总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>￥{{cardData.settlementRefundFee || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>

            <!--顾客实付-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/顾客实付.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>顾客实付
                      <el-tooltip class="item" effect="light" content="顾客实际支付金额总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>￥{{cardData.realPayAmount || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>

          </div>
          <div class="card_group">
            <!--订单总数-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/订单总数.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>订单总数
                      <el-tooltip class="item" effect="light" content="支付成功订单数量总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>{{cardData.orderTotal || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>
            <!--订单金额-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/订单金额.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>订单金额
                      <el-tooltip class="item" effect="light" content="每笔订单实际收入总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>￥{{cardData.orderAmount || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>
            <!--商户优惠-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/商户优惠.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>商户优惠
                      <el-tooltip class="item" effect="light" content="商户实际出资的优惠金额总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>￥{{cardData.merchantDiscount || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>

            <!--其他优惠-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/其他优惠.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>其他优惠
                      <el-tooltip class="item" effect="light" content="其他方实际出资的优惠金额总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>￥{{cardData.otherDiscount || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>

          </div>
          <div class="card_group">
            <!--退款总数-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/退款总数.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>退款总数
                      <el-tooltip class="item" effect="light" content="退款订单数量总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>{{cardData.refundCount || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>
            <!--退款金额-->
            <div class="card_item">
              <el-card class="box-card">
                <div class="text item tools_box">
                  <div class="tools_img">
                    <img src="../../../assets/images/pay/icon/退款金额.png" alt="">
                  </div>
                  <div class="tools_text">
                    <p>退款金额
                      <el-tooltip class="item" effect="light" content="每笔订单退款金额总和。" placement="top-start">
                        <i class="el-icon-info"></i>
                      </el-tooltip>
                    </p>
                    <p>￥{{cardData.refundAmount || 0}}</p>
                  </div>
                </div>
              </el-card>
            </div>
          </div>
        </div>
        <!--图表区-->
        <div>
          <div class="data_chart">

            <el-card class="box-card chart_card_left">
              <el-tabs class="left_tabs" v-model="leftActiveName" @tab-click="leftChangeTabs">
                <el-tab-pane label="实际营收" name="1">
                  <div id="left_chart1" class="chart_img"
                       v-loading="leftDataNull"
                       element-loading-text="没有数据"
                       element-loading-spinner="el-icon-view"
                       element-loading-background="rgba(255, 255, 255, 1)">
                  </div>
                </el-tab-pane>
                <el-tab-pane label="订单" name="2">
                  <div id="left_chart2" class="chart_img"
                       v-loading="leftDataNull"
                       element-loading-text="没有数据"
                       element-loading-spinner="el-icon-view"
                       element-loading-background="rgba(255, 255, 255, 1)">
                  </div>
                </el-tab-pane>
                <el-tab-pane label="退款" name="3">
                  <div id="left_chart3" class="chart_img"
                       v-loading="leftDataNull"
                       element-loading-text="没有数据"
                       element-loading-spinner="el-icon-view"
                       element-loading-background="rgba(255, 255, 255, 1)">
                  </div>
                </el-tab-pane>
              </el-tabs>
            </el-card>

            <el-card class="box-card chart_card_right">
              <el-tabs v-model="rightActiveName" @tab-click="rightChangeTabs">
                <el-tab-pane label="订单状态" name="1">
                  <div id="right_chart1" class="chart_img"
                       v-loading="rightDataNull"
                       element-loading-text="没有数据"
                       element-loading-spinner="el-icon-view"
                       element-loading-background="rgba(255, 255, 255, 1)">
                  </div>
                </el-tab-pane>
                <el-tab-pane label="支付方式" name="2">
                  <div id="right_chart2" class="chart_img"
                       v-loading="rightDataNull"
                       element-loading-text="没有数据"
                       element-loading-spinner="el-icon-view"
                       element-loading-background="rgba(255, 255, 255, 1)">
                  </div>
                </el-tab-pane>
                <el-tab-pane label="终端比例" name="3">
                  <div id="right_chart3" class="chart_img"
                       v-loading="rightDataNull"
                       element-loading-text="没有数据"
                       element-loading-spinner="el-icon-view"
                       element-loading-background="rgba(255, 255, 255, 1)">
                  </div>
                </el-tab-pane>
              </el-tabs>
            </el-card>

          </div>
        </div>
        <!--表格区-->
        <div style="position: relative;">
          <el-card class="box-card table_box">
            <!-- <el-button @click="memberExport()" style="position: absolute; top: 0.6rem; right: 2rem;" type="text">导出</el-button> -->
            <!--商户-->
            <el-tabs v-model="tableActiveName" v-if="userType==1" @tab-click="changeStoreTable">
              <el-tab-pane label="门店流水统计" name="1">
                <el-table
                  :data="storeFlowTable"
                  style="width: 100%">
                  <el-table-column
                    type="index"
                    label="序号"
                    header-align="left"
                    width="50">
                  </el-table-column>
                  <el-table-column
                    prop="storeName"
                    label="门店"
                    width="250"
                    show-overflow-tooltip>
                  </el-table-column>
                  <el-table-column
                    prop="orderNum"
                    label="有效订单数">
                  </el-table-column>
                  <el-table-column
                    prop="orderAmount"
                    label="订单金额（￥）">
                  </el-table-column>
                  <el-table-column
                    prop="refundAmount"
                    label="退款金额（￥）">
                  </el-table-column>
                  <el-table-column
                    prop="customerPaidAmount"
                    label="顾客实付（￥）">
                  </el-table-column>
                  <el-table-column
                    prop="discountAmount"
                    label="优惠（￥）">
                  </el-table-column>
                  <el-table-column
                    label="操作"
                    width="200">
                    <template slot-scope="scope">
                      <el-button type="primary" size="small" @click="goDetailByStore(scope.row.storeId)"> 详细信息</el-button>
                    </template>
                  </el-table-column>
                </el-table>
                <pagination
                  :total-elements="storeFlowTotalElement"
                  :change-callback="getStoreList"
                  ref="page">
                </pagination>
              </el-tab-pane>
            </el-tabs>
            <!--店长-->
            <el-tabs v-model="tableActiveName" v-if="userType==2" @tab-click="changeMasterTable">
              <el-tab-pane label="收银员流水统计" name="1">
                <!--流水统计 (订单)-->
                <el-table
                  :data="cashierFlowTable"
                  style="width: 100%">
                  <el-table-column
                    type="index"
                    label="序号"
                    width="50">
                  </el-table-column>
                  <el-table-column
                    prop="orderNumber"
                    label="订单号"
                    width="180">
                  </el-table-column>
                  <el-table-column
                    prop="storeName"
                    label="门店">
                  </el-table-column>
                  <el-table-column
                    prop="userName"
                    label="收银员">
                  </el-table-column>
                  <el-table-column
                    prop="actPayPrice"
                    label="实收">
                  </el-table-column>
                  <el-table-column
                    prop="refundPayPrice"
                    label="退款金额">
                  </el-table-column>
                  <el-table-column
                    label="支付方式">
                    <template slot-scope="scope">
                      <span>{{scope.row.payWay|payWayFil}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column
                    label="状态">
                    <template slot-scope="scope">
                      <span>{{scope.row.status|statusFil}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column
                    prop="createTime"
                    label="创建时间">
                  </el-table-column>
                  <el-table-column
                    label="操作">
                    <template slot-scope="scope">
                      <el-button type="primary" size="small" @click="goDetailByOrder(scope.row.orderNumber)">查看详情
                      </el-button>
                    </template>
                  </el-table-column>
                </el-table>

                <!--流水统计-->
                <pagination
                  :total-elements="cashierFlowTotalElement"
                  :change-callback="getOrderList"
                  ref="page"></pagination>
              </el-tab-pane>
              <!--<el-tab-pane label="二维码流水统计" name="2">-->
              <!--<el-table-->
              <!--:data="qrFlowTable"-->
              <!--style="width: 100%">-->
              <!--<el-table-column-->
              <!--type="index"-->
              <!--label="序号"-->
              <!--width="50">-->
              <!--</el-table-column>-->
              <!--<el-table-column-->
              <!--prop="name"-->
              <!--label="二维码"-->
              <!--width="180">-->
              <!--</el-table-column>-->
              <!--<el-table-column-->
              <!--prop="num"-->
              <!--label="订单数">-->
              <!--</el-table-column>-->
              <!--<el-table-column-->
              <!--prop="price"-->
              <!--label="订单金额">-->
              <!--</el-table-column>-->
              <!--<el-table-column-->
              <!--prop="act"-->
              <!--label="商户实收">-->
              <!--</el-table-column>-->
              <!--<el-table-column-->
              <!--prop="actRe"-->
              <!--label="商户实退">-->
              <!--</el-table-column>-->
              <!--<el-table-column-->
              <!--label="操作">-->
              <!--<template slot-scpoe="scpoe">-->
              <!--<el-button type="primary" size="small">查看详情</el-button>-->
              <!--</template>-->
              <!--</el-table-column>-->
              <!--</el-table>-->
              <!--</el-tab-pane>-->
            </el-tabs>
          </el-card>
        </div>
      </el-card>
    </div>

  </div>
</template>

<script>
import {
  findCountData,
  findActualRevenueChart,
  findOrderChart,
  findRefundChart,
  findOrderStatusChart,
  findPaywayStatusChart,
  findClientChart,
  findStoreList,
  findOrderList,
  findOrderStatusOption,
  findOrderPayWayOption
} from '../api/flowPreview'
import pagination from '@/components/pagination/index'
import echartResizeMixin from './mixin/echartResizeMixin.js'
import {url} from '@/utils/request'

export default {
  name: 'flowPreview',
  mixins: [echartResizeMixin],
  components: {pagination},
  data () {
    return {
      loading: true,
      userType: '', // 登录用户类型
      searchDays: '0', // 搜索天数
      searchForm: {
        days: [], // 日期
        type: '2'// 类型
      },
      cardData: {}, // 卡片数据
      leftActiveName: '1', // 左侧图表标签页Index
      rightActiveName: '1', // 右侧图表标签页Index
      tableActiveName: '1', // 表格标签页
      leftDataNull: true,
      rightDataNull: true,

      // usertype=1 商户 只看门店流水
      storeFlowTable: [], // 门店流水表格
      storeFlowTotalElement: 0, // 门店流水表格总条数
      // usertype=2 店长 只看员工和二维码流水
      cashierFlowTable: [], // 收银员流水表格
      cashierFlowTotalElement: 0, // 收银员流水表格总条数
      qrFlowTable: [], // 二维码流水表格
      qrFlowTotalElement: 0, // 二维码流水表格总条数

      statusOption: [],
      payWayOption: [],
      chart1: null,
      chart2: null,
      chart3: null
    }
  },
  filters: {
    // 流水状态
    statusFil: function (val) {
      let v = parseInt(val)
      let txt = ''
      switch (v) {
        case 1:
          txt = '未支付'
          break
        case 2:
          txt = '支付成功'
          break
        case 3:
          txt = '支付失败'
          break
        case 4:
          txt = '已撤销'
          break
        case 5:
          txt = '全额退款成功'
          break
        case 6:
          txt = '部分退款成功'
          break
        case 7:
          txt = '退款失败'
          break
        default:
          txt = '未知'
          break
      }
      return txt
    },
    // 流水状态
    payWayFil: function (val) {
      let v = parseInt(val)
      let txt = ''
      switch (v) {
        case 1:
          txt = '微信'
          break
        case 2:
          txt = '支付宝'
          break
        case 3:
          txt = '银行卡'
          break
        case 4:
          txt = '会员卡'
          break
        default :
          txt = '未知'
      }
      return txt
    }
  },
  mounted () {
    this.userType = sessionStorage.getItem('userType')
    // 默认查询
    this.defaultSearch()
  },
  methods: {
    memberExport: function () {
      this.$confirm('是否导出流水概览列表?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let cid = sessionStorage.getItem('companyId')
        let userId = sessionStorage.getItem('userId')
        let ty = this.searchForm.type
        location.href = url + "/auth/running_account/export/merchant?userId=" + userId +
                        "&pageSize=" + 10 + "&pageNum=" + 1 + "&type=" + ty
      })
    },
    /**
       * 获取支付状态
       */
    getStatusOption: function () {
      findOrderStatusOption().then(res => {
        this.statusOption = res.obj
      }).catch(e => {
        this.$message.error('支付状态选项无法获取')
      })
    },
    /**
       * 获取支付方式
       */
    getPayWayOption: function () {
      findOrderPayWayOption().then(res => {
        this.payWayOption = res.obj
      }).catch(e => {
        this.$message.error('支付方式选项无法获取')
      })
    },

    /**
       * 第一次默认查询
       */
    defaultSearch () {
      // 设定7天日期//2
      this.changeDays(0)
    },

    /**
       *检查时间
       * 如果不对，则修改为里结束日期的前31天。
       */
    checkDataRange () {
      let timestamp = (new Date()).getTime()
      let startTime = this.searchForm.days[0]
      let endTime = this.searchForm.days[1]
      if (endTime - startTime > 24 * 60 * 60 * 1000 * 32) {
        this.$message.error('前后时间不能超过31天')
        this.$set(this.searchForm.days, 0, timestamp - (24 * 60 * 60 * 1000 * 31))
        this.$set(this.searchForm.days, 1, timestamp)
      } else if (endTime > timestamp) {
        this.$message.error('日期不能大于当天')
        this.$set(this.searchForm.days, 1, timestamp)
      }
    },
    /**
       * 快捷方式修改日期
       * @param event 日期范围选项
       */
    changeDays: function (event) {
      let timestamp = (new Date()).getTime()
      // 0=7  1=15 2=30
      if (event == 0) {
        let startTime = timestamp - (24 * 60 * 60 * 1000 * 6)
        let endTime = timestamp
        this.$set(this.searchForm.days, 0, startTime)
        this.$set(this.searchForm.days, 1, endTime)
      } else if (event == 1) {
        let startTime = timestamp - (24 * 60 * 60 * 1000 * 14)
        let endTime = timestamp
        this.$set(this.searchForm.days, 0, startTime)
        this.$set(this.searchForm.days, 1, endTime)
      } else if (event == 2) {
        let startTime = timestamp - (24 * 60 * 60 * 1000 * 29)
        let endTime = timestamp
        this.$set(this.searchForm.days, 0, startTime)
        this.$set(this.searchForm.days, 1, endTime)
      }
      // 获取卡片数据
      this.findCountData()
      // 获取列表数据(分流检查)
      this.getListCheck()
    },

    /**
       * 获取卡片数据 后 获取图表数据
       */
    findCountData: function () {
      let st = this.format(this.searchForm.days[0]) || ''
      let et = this.format(this.searchForm.days[1]) || ''

      findCountData(st, et).then(res => {
        this.cardData = res.obj ? res.obj : {}
        this.loading = false

        this.leftChangeTabs() // 为左边图表获取数据
        this.rightChangeTabs() // 为右边图表获取数据
      }).catch(e => {

      })
    },
    /**
       * 获取列表 判断用户
       */
    getListCheck: function () {
      if (this.userType == 1) {
        this.getStoreList(1, 10)
      } else if (this.userType == 2) {
        this.getOrderList(1, 10)
      }
    },

    /**
       * 获取门店列表
       */
    getStoreList: function (pageNum, pageSize) {
      let st = this.format(this.searchForm.days[0]) || ''
      let et = this.format(this.searchForm.days[1]) || ''
      findStoreList(pageNum, pageSize, st, et).then(res => {
        let data = res.obj
        this.storeFlowTable = data.listData
        this.storeFlowTotalElement = data.totalElement
      }).catch(e => {

      })
    },

    /**
       * 获取列表 **************有误
       */
    getOrderList: function (pageNum, pageSize) {
      let st = this.format(this.searchForm.days[0]) || ''
      let et = this.format(this.searchForm.days[1]) || ''
      findOrderList(pageNum, pageSize, st, et).then(res => {
        let data = res.obj
        this.cashierFlowTable = data.listData
        this.cashierFlowTotalElement = data.totalElement
      }).catch(e => {

      })
    },

    /**
       *跳转详情
       */
    goDetailByStore: function (data) {
      this.$router.push({path: '/pay/views/flowOrderList', query: {id: data}})
    },
    /**
       *跳转详情
       */
    goDetailByOrder: function (data) {
      this.$router.push({path: '/pay/views/flowOrderDetail', query: {id: data}})
    },

    /**
       * 切换左的图表标签页---------------------------------------------------
       */
    leftChangeTabs () {
      let st = this.format(this.searchForm.days[0]) || ''
      let et = this.format(this.searchForm.days[1]) || ''
      // let ty = this.searchForm.type
      let tabs = this.leftActiveName
      if (tabs == '1') {
        this.getActualRevenueChart(st, et)
      } else if (tabs == '2') {
        this.getOrderChart(st, et)
      } else if (tabs == '3') {
        this.getRefundChart(st, et)
      }
    },

    /**
       *获取实际营收图表数据
       */
    getActualRevenueChart: function (st, et) {
      findActualRevenueChart(st, et).then(res => {
        if (res.obj != null) {
          this.drawChartLeftOne(res.obj)
          this.leftDataNull = false
        }
      }).catch(e => {
        this.leftDataNull = true
      })
    },
    /**
       *获取订单图表数据
       */
    getOrderChart: function (st, et) {
      findOrderChart(st, et).then(res => {
        if (res.obj != null) {
          this.drawChartLeftTwo(res.obj)
          this.leftDataNull = false
        }
      }).catch(e => {
        this.leftDataNull = true
      })
    },
    /**
       *获取退款图表数据
       */
    getRefundChart: function (st, et) {
      findRefundChart(st, et).then(res => {
        if (res.obj != null) {
          this.drawChartLeftThree(res.obj)
          this.leftDataNull = false
        }
      }).catch(e => {
        this.leftDataNull = true
      })
    },

    /**
       * 渲染图表 左1
       */
    drawChartLeftOne: function (data) {
      let x1 = data.xdata // 日期
      let y1 = data.ydata // 金额
      let y2 = data.num
      var chart = this.$echarts.init(document.getElementById('left_chart1'))
      this.chart1 = chart
      this.chart1.resize()
      this.adaptWindowResize(this.chart1)
      chart.setOption({
        // title: {text: '商户统计', textStyle: {color: '#333', fontWeight: 'lighter', fontSize: '16'}},
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} @ {c} 元 '
        },
        legend: {
          data: ['实际营收']
        },
        toolbox: {
          feature: {
            magicType: {
              type: ['line', 'bar']
            },
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: x1
        }, // X轴标题
        yAxis: {
          type: 'value'
        },
        series: [{
          data: y1,
          name: '实际营收',
          type: 'line',
          areaStyle: {}
        }
          //   {
          //   data: cNumber,
          //   name: '总单数',
          //   type: 'line',
          //   areaStyle: {}
          // }
        ] // 数据数组，多个数据，legend需要对于标题name
      })
    },
    /**
       * 渲染图表 左2
       */
    drawChartLeftTwo: function (data) {
      let x1 = data.xdata // 日期
      let y1 = data.ydata // 金额
      let y2 = data.num // 数量
      var chart = this.$echarts.init(document.getElementById('left_chart2'))
      this.chart2 = chart
      this.chart2.resize()
      this.adaptWindowResize(this.chart2)
      chart.setOption({
        // title: {text: '商户统计', textStyle: {color: '#333', fontWeight: 'lighter', fontSize: '16'}},
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} @ {c} '
        },
        legend: {
          data: ['金额', '数量']
        },
        toolbox: {
          feature: {
            magicType: {
              type: ['line', 'bar']
            },
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: x1
        }, // X轴标题
        yAxis: {
          type: 'value'
        },
        series: [{
          data: y1,
          name: '金额',
          type: 'line',
          areaStyle: {}
        },
        {
          data: y2,
          name: '数量',
          type: 'line',
          areaStyle: {}
        }
        ] // 数据数组，多个数据，legend需要对于标题name
      })
    },
    /**
       * 渲染图表 左3
       */
    drawChartLeftThree: function (data) {
      let x1 = data.xdata // 日期
      let y1 = data.ydata // 金额
      let y2 = data.num // 数量
      var chart = this.$echarts.init(document.getElementById('left_chart3'))
      this.chart3 = chart
      this.chart3.resize()
      this.adaptWindowResize(this.chart3)
      chart.setOption({
        // title: {text: '商户统计', textStyle: {color: '#333', fontWeight: 'lighter', fontSize: '16'}},
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} @ {c} '
        },
        legend: {
          data: ['金额', '数量']
        },
        toolbox: {
          feature: {
            magicType: {
              type: ['line', 'bar']
            },
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: x1
        }, // X轴标题
        yAxis: {
          type: 'value'
        },
        series: [{
          data: y1,
          name: '金额',
          type: 'line',
          areaStyle: {}
        },
        {
          data: y2,
          name: '数量',
          type: 'line',
          areaStyle: {}
        }
        ] // 数据数组，多个数据，legend需要对于标题name
      })
    },

    /**
       * 切换右的图表标签页---------------------------------------------------
       */
    rightChangeTabs () {
      let st = this.format(this.searchForm.days[0]) || ''
      let et = this.format(this.searchForm.days[1]) || ''
      let tabs = this.rightActiveName
      if (tabs == '1') {
        this.getOrderStatusChart(st, et)
      } else if (tabs == '2') {
        this.getPaywayStatusChart(st, et)
      } else if (tabs == '3') {
        this.getClientChart(st, et)
      }
    },
    /**
       *获取订单状态
       */
    getOrderStatusChart: function (st, et) {
      findOrderStatusChart(st, et).then(res => {
        if (res.obj != null) {
          if (res.obj.length) {
            this.drawChartRightOne(res.obj)
            this.rightDataNull = false
          } else {
            this.rightDataNull = true
          }
        }
      }).catch(e => {
        this.rightDataNull = true
      })
    },
    /**
       *获取支付方式
       */
    getPaywayStatusChart: function (st, et) {
      findPaywayStatusChart(st, et).then(res => {
        if (res.obj != null) {
          if (res.obj.length) {
            this.drawChartRightTwo(res.obj)
            this.rightDataNull = false
          } else {
            this.rightDataNull = true
          }
        }
      }).catch(e => {
        this.rightDataNull = true
      })
    },
    /**
       *获取支付终端
       */
    getClientChart: function (st, et) {
      findClientChart(st, et).then(res => {
        if (res.obj != null) {
          if (res.obj.length) {
            this.drawChartRightThree(res.obj)
            this.rightDataNull = false
          } else {
            this.rightDataNull = true
          }
        }
      }).catch(e => {
        this.rightDataNull = true
      })
    },

    /**
       * 渲染图表 右1
       */
    drawChartRightOne: function (data) {
      let title = ['未支付', '支付成功', '支付失败', '已撤销', '全额退款成功', '部分退款成功', '退款失败']
      var chart = this.$echarts.init(document.getElementById('right_chart1'))
      chart.setOption({
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: title
        },
        color: ['#FFAF2C', '#56A2FD', '#FF7570', '#BFBFBF', '#5FB870', '#00E6BD', '#7D70FF'],
        series: [
          {
            name: '订单状态',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '30',
                  fontWeight: 'bold'
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: data
          }
        ]
      })
    },

    /**
       * 渲染图表 右2
       */
    drawChartRightTwo: function (data) {
      let title = ['微信', '支付宝', '银行卡', '会员卡', '未知']
      var chart = this.$echarts.init(document.getElementById('right_chart2'))
      chart.setOption({
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: title
        },
        color: ['#FFAF2C', '#56A2FD', '#FF7570', '#BFBFBF', '#5FB870', '#00E6BD', '#7D70FF'],
        series: [
          {
            name: '支付方式',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '30',
                  fontWeight: 'bold'
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: data
          }
        ]
      })
    },

    /**
       * 渲染图表 右3
       */
    drawChartRightThree: function (data) {
      let title = ['PC端', 'App端' ]
      var chart = this.$echarts.init(document.getElementById('right_chart3'))
      chart.setOption({
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: title
        },
        color: ['#FFAF2C', '#56A2FD', '#FF7570', '#BFBFBF', '#5FB870', '#00E6BD', '#7D70FF'],
        series: [
          {
            name: '支付终端',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '30',
                  fontWeight: 'bold'
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: data
          }
        ]
      })
    },

    /**
       * 切换表格（商户角色）---------------------------------------------------
       */
    changeStoreTable: function () {

    },
    /**
       * 切换表格（店长角色）
       */
    changeMasterTable: function () {

    },

    /**
       * reset 重置
       */

    reset: function () {
      let timestamp = (new Date()).getTime()
      let startTime = timestamp
      let endTime = timestamp
      this.searchDays = '0'
      this.$set(this.searchForm.days, 0, startTime)
      this.$set(this.searchForm.days, 1, endTime)
    },

    /**
       * 工具
       * @param ts 时间戳
       * @returns {string} 格式化事时间
       */
    // 时间格式化
    format (ts) {
      let time = new Date(ts)
      let y = time.getFullYear()
      let m = time.getMonth() + 1
      let d = time.getDate()
      return y + '-' + this.add0(m) + '-' + this.add0(d)
    },
    /**
       * 补充0
       * @param  m 月和日，补齐2位
       * @returns {string} 补齐2二位后的时间
       */
    add0 (m) {
      return m < 10 ? '0' + m : m
    }

  }

}
</script>

<style scoped>
  .block {
    display: inline-block;
    vertical-align: middle;
  }

  .search_bar {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  /*卡片*/
  .card_group {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
  }

  .card_item {
    padding: 0;
    width: 23%;
    max-width: 100%;
    margin: 20px 1% 0 1%;
  }

  .tools_box {
    padding: 0 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }

  .tools_img {
    width: 30%;
    text-align: left;
  }
  .tools_img img{
    width:24px;
  }
  .tools_text {
    width: 70%;
    text-align: right;
  }

  .tools_text p {
    margin: 0;
    padding: 0;
    cursor: default;
  }

  .tools_text p:nth-child(1) {
    font-size: 12px;
    color: #888;
  }

  .tools_text p:nth-child(2) {
    padding: 5px 0 0 0;
    font-size: 20px;
    color: #3f3f3f;
  }

  /*图表*/
  .data_chart {
    margin-top: 20px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .chart_card_left {
    width: 59%;
  }

  .chart_card_right {
    width: 39%;
  }

  .chart_img {
    width: 100%;
    height: 500px;
  }

  /*表格*/
  .table_box {
    margin-top: 20px;
  }
</style>



