<template>
  <div class="memberPaySetting">
    <div class="container">
      <p class="title">充值赠送的金额</p>
      <el-radio v-model="rechargeUse"
                :label="1">可立即使用</el-radio>
      <el-radio v-model="rechargeUse"
                :label="2">下次消费才可使用</el-radio>
    </div>
    <div class="container">
      <p class="title">积分是否可抵扣金额</p>
      <el-radio v-model="integralUse"
                :label="1">抵扣</el-radio>
      <el-input size="small"
                v-model="integralRule.integralNum"
                class="input"></el-input>
      <span>积分</span>
      <span>抵扣</span>
      <el-input size="small"
                v-model="integralRule.deduction"
                class="input"></el-input>
      <span>元</span>
      <span>最多抵扣</span>
      <el-input size="small"
                v-model="integralRule.deductionMax"
                class="input"></el-input>
      <span>积分</span>
      <br />
      <el-radio class="mt"
                v-model="integralUse"
                :label="0">不抵扣</el-radio>
    </div>
    <div class="container">
      <p class="title">折扣/优惠券/满减优惠是否同享</p>
      <el-checkbox v-model="simultaneouslyUse1"
                   :true-label="1"
                   :false-label="2"
                   @change="">展示满减</el-checkbox>
      <el-checkbox v-model="simultaneouslyUse2"
                   :true-label="1"
                   :false-label="2"
                   @change="">展示优惠</el-checkbox>
    </div>
    <div class="container mt tip">
      <p>提示：</p>
      <p>1.单独选择满减/优惠券时，设备上仅展示满减或优惠券信息；</p>
      <p>2.满减和优惠券都选择时，设备上展示两样优惠信息，但是用户只能选择一个优惠；</p>
      <p>3.满减和优惠券都不选择时，有优惠信息也不展示</p>
    </div>
    <el-button class="operate-btn"
               type="primary"
               size="small"
               @click="baocunpayrule">保存</el-button>
  </div>
</template>
<script>
import { payruleList, setpayrule } from "../api/memberLevel";
export default {
  name: "memberLevel",
  data() {
    return {
      rechargeUse: '1', //使用
      integralUse: '1',
      simultaneouslyUse: '',
      simultaneouslyUse1: '',
      simultaneouslyUse2: '',
      manjian: 1,
      youhui: 1,
      integralRule: {
        integralNum: "",
        deduction: "",
        deductionMax: ""
      }
    };
  },
  created() {
    this.getpayrule();
  },
  methods: {

    getpayrule() {
      payruleList()
        .then(response => {
          let data = response.obj;
          this.rechargeUse = data.rechargeUse;
          this.integralUse = data.integralUse;
          this.simultaneouslyUse = data.simultaneouslyUse;
          this.integralRule = JSON.parse(data.integralRule);

          if(this.simultaneouslyUse == 3) {
               this.simultaneouslyUse1 = 1
               this.simultaneouslyUse2 = 1
            }else if(this.simultaneouslyUse == 1){
               this.simultaneouslyUse1 = 1
               this.simultaneouslyUse2 = 2
            }else if(this.simultaneouslyUse == 2) {
              this.simultaneouslyUse1 = 2
              this.simultaneouslyUse2 = 1
            }else {
              this.simultaneouslyUse1 = 2
              this.simultaneouslyUse2 = 2
            }
        })
        .catch(() => { });
    },
    baocunpayrule() {
      if(this.simultaneouslyUse1 == 1 && this.simultaneouslyUse2 == 1) {
         this.simultaneouslyUse = 3
      }else if(this.simultaneouslyUse1 == 1){
        this.simultaneouslyUse = 1
      }else if(this.simultaneouslyUse2 == 1) {
        this.simultaneouslyUse = 2
      }else {
        this.simultaneouslyUse = 0
      }
      this.integralRule = JSON.stringify(this.integralRule);
      setpayrule(
        this.integralRule,
        this.integralUse,
        this.rechargeUse,
        this.simultaneouslyUse
      )
        .then(response => {
          this.$message({
            message: response.msg,
            type: "success"
          });
          this.getpayrule();
        })
        .catch(() => { });
    }
  }
};
</script>
<style scoped>
.tip {
  font-size: 14px;
  line-height: 24px;
}
.input {
  width: 10%;
}
span {
  font-size: 14px;
}
.operate-btn {
  margin-top: 50px;
}
.memberPaySetting {
  padding: 0 100px;
}
.title {
  font-weight: 700;
  font-size: 16px;
  margin: 50px 0 30px;
}
</style>


