import request from '@/utils/request'
import qs from 'qs'
// 获取押金管理列表
export function getMoney (pageNumber, pageSize, id, status, startTime, endTime) {
  return request({
    url: '/merchant/deposit/list',
    method: 'get',
    params: {
      pageNumber,
      pageSize,
      id,
      status,
      startTime,
      endTime
    }
  })
}
// 修改备注
export function editRem (remForm) {
  return request({
    url: '/merchant/deposit',
    method: 'put',
    jsonHeader: true,
    data: remForm
  })
}
// 退押金
export function quitMon (quitForm) {
  return request({
    url: '/merchant/deposit/refund',
    method: 'post',
    jsonHeader: true,
    data: quitForm
  })
}



