import request from '@/utils/request'
import qs from 'qs'

// 获取支付状态选项
export function findOrderStatusOption () {
  return request({
    url: '/order/order/find_order_status',
    method: 'get',
    params: {}
  })
}

// 获取支付方式
export function findOrderPayWayOption () {
  return request({
    url: '/order/order/find_pay_way',
    method: 'get',
    params: {}
  })
}

// 获取流水概览 卡片数据
export function findCountData (st, et) {
  return request({
    url: '/order/running_account/merchant_running_account_overview_count_data',
    method: 'get',
    params: {
      start_payTime: st,
      end_payTime: et
    }
  })
}

// 获取实际营收
export function findActualRevenueChart (st, et) {
  return request({
    url: '/order/running_account/merchant_running_account_overview_income_chart',
    method: 'get',
    params: {
      start_payTime: st,
      end_payTime: et
    }
  })
}

// 获取订单
export function findOrderChart (st, et) {
  return request({
    url: '/order/running_account/merchant_running_account_overview_order_chart',
    method: 'get',
    params: {
      start_payTime: st,
      end_payTime: et
    }
  })
}

