<template>
  <div class="newCommission">
    <el-card class="card-box">
      <div>
        <p class="dataLabel"
           style="font-size:16px">数据统计</p>
        <div class="settleDiv">
          <ul>
            <li>新用户</li>
            <li>5522</li>
          </ul>
          <ul>
            <li>交易金额</li>
            <li>100.526</li>
          </ul>
          <ul>
            <li>返佣金额</li>
            <li>13</li>
          </ul>
          <ul>
            <li>已结算</li>
            <li>13</li>
          </ul>
          <ul>
            <li>未结算</li>
            <li>13</li>
          </ul>
        </div>
      </div>
      <div class="tab_header_bar">
        <p class="dataLabel"
           style="font-size:16px">返佣结算</p>
        <div class="block"
             style="margin-top:10px;font-size:14px">
          按佣金生成日期：
          <el-date-picker type="date"
                          size="small"
                          v-model="startTime"
                          placeholder="开始日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd"
                          style="width: 190px">
          </el-date-picker>
          <span>--</span>
          <el-date-picker type="date"
                          size="small"
                          v-model="endTime"
                          placeholder="结束日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd"
                          style="width: 190px">
          </el-date-picker>
        </div>
        <div class="block">
          <el-select size="small"
                     v-model="value"
                     placeholder="请选择">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </div>
        <div class="block"
             style="margin-left: 10px">
          <el-button size="small"
                     type="primary"
                     @click="getSettleList(1,10)">查 询
          </el-button>
        </div>
        <div class="block"
             style="margin-left: 10px">
          <el-button size="small"
                     type=""
                     @click="getSettleList(1,10)">重 置
          </el-button>
        </div>
      </div>
      <div style="font-size:16px">
        <span style="margin-left:20px">合计：</span>
        <span style="margin-left:20px">消费金额：5466元</span>
        <span style="margin-left:20px">返佣金额 12.5元</span>
      </div>
      <!-- 表格 -->
      <el-table :data="tableData"
                style="width: 100%">
        <el-table-column prop="time"
                         label="佣金生成时间"
                         :show-overflow-tooltip="true"
                         align="center">
        </el-table-column>
        <el-table-column prop="orderNo"
                         label="顾客名称"
                         :show-overflow-tooltip="true"
                         align="center">
        </el-table-column>
        <el-table-column prop="fee"
                         label="交易金额"
                         :show-overflow-tooltip="true"
                         align="center">
        </el-table-column>
        <el-table-column prop="reFee"
                         label="返利金额"
                         :show-overflow-tooltip="true"
                         align="center">
        </el-table-column>
        <el-table-column prop="status"
                         label="状态"
                         :show-overflow-tooltip="true"
                         align="center">
        </el-table-column>
        <el-table-column prop="endTime"
                         label="结算时间"
                         :show-overflow-tooltip="true"
                         align="center">
        </el-table-column>
      </el-table>
      <pagination :total-elements="tableTotalElements"
                  :change-callback="getSettleList"
                  ref="page">
      </pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      loading: false,
      name: '',
      value: '',
      startTime: '',
      endTime: '',
      options: [
        {
          value: '选项5',
          label: '北京烤鸭'
        }
      ],
      tableData: [
        {
          time: '2019-10-19',
          orderNo: '阿秋',
          fee: '123',
          reFee: '1.24',
          status: '未结算',
          endTime: ''
        }
      ],
      tableTotalElements: 0
    }
  },
  mounted () {
    this.getSettleList()
  },
  methods: {
    getSettleList () {
      this.loading = true
    },
    toDetail (id) {
      this.$router.push({
        path: '/merCommission/views/commissionDetail',
        query: { id: id }
      })
    }
  }
}
</script>
<style scoped>
.settleDiv {
  display: flex;
  justify-content: space-around;
  border-bottom: 1px dashed #eaeaea;
}
.settleDiv ul li:first-of-type {
  color: #757575;
  font-size: 16px;
  padding-bottom: 5px;
}
.settleDiv ul li:last-of-type {
  font-size: 28px;
}
.dataLabel {
  padding-left: 5px;
  border-left: 5px solid #1d1e1e;
  color: #1d1e1e;
}
ul {
  list-style: none;
  text-align: center;
  padding: 0;
}
.newCommission {
  padding: 30px;
}
.tab_header_bar {
  margin-bottom: 10px;
  padding-top: 20px;
}

.block {
  display: inline-block;
  vertical-align: middle;
}
</style>



