<template>
  <div class="app-contanier">
    <el-card class="card-box">
      <div class="flex">
        <p>
          <i class="el-icon-user-solid"></i>今日交易概况
          <i @click="gettradingSituation" class="el-icon-refresh"></i>
        </p>
        <div style="font-size:14px;">
          更新时间：{{startTime}}
          <span v-if="userType!=3" style="color:#1087C7" @click="to_more">
            更多数据
            <i style="font-size:14px;" class="el-icon-arrow-right"></i>
          </span>
        </div>
      </div>
      <!--  -->
      <div class="tongji">
        <ul class="distUl">
          <li>交易金额</li>
          <li>
            <span>{{traList.todayOrderPrice?traList.todayOrderPrice:0}}</span>元
          </li> 
          <li
          >相较昨日{{Math.round((traList.todayOrderPrice-traList.yesterdayOrderPrice)*100)/100}}元</li>
          <li>昨日{{traList.yesterdayOrderPrice?traList.yesterdayOrderPrice:0}}元</li>
        </ul>
        <ul class="distUl">
          <li>交易笔数</li>
          <li>
            <span>{{traList.todayOrderAmount?traList.todayOrderAmount:0}}</span>笔
          </li>
          <li
          >相较昨日{{Math.round((traList.todayOrderAmount-traList.yesterdayOrderAmount)*100)/100}}笔</li>
          <li>昨日{{traList.yesterdayOrderAmount?traList.yesterdayOrderAmount:0}}笔</li>
        </ul>
        <ul class="distUl">
          <li>退款金额</li>
          <li>
            <span>{{traList.todayRefund?traList.todayRefund:0}}</span>元
          </li>
          <li
          >相较昨日{{Math.round((traList.todayRefund-traList.yesterdayRefund)*100)/100}}元</li>
          <li>昨日{{traList.yesterdayRefund?traList.yesterdayRefund:0 }}元</li>
        </ul>
        <ul class="distUl">
          <li>退款笔数</li>
          <li>
            <span>{{traList.todayRefundCount?traList.todayRefundCount:0}}</span>笔
          </li>
          <li
          >相较昨日{{Math.round((traList.todayRefundCount-traList.yesterdayRefundCount)*100)/100}}笔</li>
          <li>昨日{{traList.yesterdayRefundCount?traList.yesterdayRefundCount:0}}笔</li>
        </ul>
        <ul class="distUl">
          <li>实际营收</li>
          <li>
            <span>{{traList.todayTotal?traList.todayTotal:0 }}</span>元
          </li>
          <li
          >相较昨日{{Math.round((traList.todayTotal-traList.yesterdayTotal)*100)/100}}元</li>
          <li>昨日{{traList.yesterdayTotal?traList.yesterdayTotal:0 }}元</li>
        </ul>
      </div>
      <!-- 图 -->
      <div class="flex">
        <div class="tongji3">
          <el-radio-group v-model="isTotal" size="mini" style="margin:20px;" @change="getisTotal">
            <el-radio-button :label="true">交易金额</el-radio-button>
            <el-radio-button :label="false">交易笔数</el-radio-button>
          </el-radio-group>
          <el-select size="mini" v-model="value" placeholder="请选择" @change="getType">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <p>昨日全天{{yesterdayTotalFee}}元</p>
      </div>
      <!-- 折线图 -->
      <div id="myChart1" style="width: 100%;height: 400px;"></div>
    </el-card>
    <el-card>
      <!-- 交易数据统计 -->
      <div class="flex">
        <p>
          <i class="el-icon-s-management"></i>交易数据统计
        </p>
        <div class="tongji3">
          <el-radio-group v-model="searchDays" size="mini" style="margin:20px;" @change="getDates">
            <el-radio-button label="昨日"></el-radio-button>
            <el-radio-button label="近3日"></el-radio-button>
            <el-radio-button label="近7日"></el-radio-button>
            <el-radio-button label="近15日"></el-radio-button>
            <el-radio-button label="近30日"></el-radio-button>
          </el-radio-group>
          <el-date-picker
            type="date"
            v-model="startTime2"
            @change="getstartTime2"
            size="small"
            placeholder="开始日期"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd HH:mm:ss"
            style="width: 190px"
          ></el-date-picker>
          <span>--</span>
          <el-date-picker
            type="date"
            v-model="endTime2"
            @change="getendTime2"
            size="small"
            placeholder="结束日期"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd HH:mm:ss"
            style="width: 190px"
          ></el-date-picker>
        </div>
      </div>
      <p style="font-size:14px;font-weight:600">·交易数据</p>
      <div class="sytongji">
        <ul class="distUl">
          <li>交易金额</li>
          <li>
            <span>{{traList2.thisOrderPrice}}</span>元
          </li>
          <li>相较上一时段{{Math.round(traList2.thisOrderPrice-traList2.lastOrderPrice*100)/100 }}元</li>
          <li
            v-if="(traList2.thisOrderPrice-traList2.lastOrderPrice)/traList2.thisOrderPrice<0&&traList2.thisOrderPrice!=0"
          >下降{{Math.round([(traList2.thisOrderPrice-traList2.lastOrderPrice)/traList2.thisOrderPrice]*100)/100}}%</li>
          <li
            v-if="(traList2.thisOrderPrice-traList2.lastOrderPrice)/traList2.thisOrderPrice==0&&traList2.thisOrderPrice!=0"
          >持平</li>
          <li
            v-if="(traList2.thisOrderPrice-traList2.lastOrderPrice)/traList2.thisOrderPrice>0&&traList2.thisOrderPrice!=0"
          >上涨{{Math.round([(traList2.thisOrderPrice-traList2.lastOrderPrice)/traList2.thisOrderPrice]*100)/100}}%</li>
          <li v-if="traList2.thisOrderPrice==0">上涨</li>
        </ul>
        <ul class="distUl">
          <li>交易笔数</li>
          <li>
            <span>{{traList2.thisOrderAmount}}</span>笔
          </li>
          <li>相较上一时段{{Math.round(traList2.thisOrderAmount-traList2.lastOrderAmount*100)/100}}笔</li>
          <li
            v-if="(traList2.thisOrderAmount-traList2.lastOrderAmount)/traList2.thisOrderAmount<0&&traList2.thisOrderAmount!=0"
          >下降{{Math.round([(traList2.thisOrderAmount-traList2.lastOrderAmount)/traList2.thisOrderAmount]*100)/100}}%</li>
          <li
            v-if="(traList2.thisOrderAmount-traList2.lastOrderAmount)/traList2.thisOrderAmount==0&&traList2.thisOrderAmount!=0"
          >持平</li>
          <li
            v-if="(traList2.thisOrderAmount-traList2.lastOrderAmount)/traList2.thisOrderAmount>0&&traList2.thisOrderAmount!=0"
          >上涨{{Math.round([(traList2.thisOrderAmount-traList2.lastOrderAmount)/traList2.thisOrderAmount]*100)/100}}%</li>
          <li v-if="traList2.thisOrderAmount==0">上涨</li>
        </ul>
        <ul class="distUl">
          <li>退款金额</li>
          <li>
            <span>{{traList2.thisRefund}}</span>元
          </li>
          <li>相较上一时段{{Math.round(traList2.thisRefund-traList2.lastRefund*100)/100}}元</li>
          <li
            v-if="(traList2.thisRefund-traList2.lastRefund)/traList2.thisRefund<0&&traList2.thisRefund!=0"
          >下降{{Math.round([(traList2.thisRefund-traList2.lastRefund)/traList2.thisRefund]*100)/100}}%</li>
          <li
            v-if="(traList2.thisRefund-traList2.lastRefund)/traList2.thisRefund==0&&traList2.thisRefund!=0"
          >持平</li>
          <li
            v-if="(traList2.thisRefund-traList2.lastRefund)/traList2.thisRefund>0&&traList2.thisRefund!=0"
          >上涨{{Math.round([(traList2.thisRefund-traList2.lastRefund)/traList2.thisRefund]*100)/100}}%</li>
          <li v-if="traList2.thisRefund==0">上涨</li>
        </ul>
        <ul class="distUl">
          <li>退款笔数</li>
          <li>
            <span>{{traList2.thisRefundCount}}</span>笔
          </li>
          <li>相较上一时段{{Math.round(traList2.thisRefundCount-traList2.lastRefundCount*100)/100}}笔</li>
          <li
            v-if="(traList2.thisRefundCount-traList2.lastRefundCount)/traList2.thisRefundCount<0&&traList2.thisRefundCount!=0"
          >下降{{Math.round([(traList2.thisRefundCount-traList2.lastRefundCount)/traList2.thisRefundCount]*100)/100}}%</li>
          <li
            v-if="(traList2.thisRefundCount-traList2.lastRefundCount)/traList2.thisRefundCount==0&&traList2.thisRefundCount!=0"
          >持平</li>
          <li
            v-if="(traList2.thisRefundCount-traList2.lastRefundCount)/traList2.thisRefundCount>0&&traList2.thisRefundCount!=0"
          >上涨{{Math.round([(traList2.thisRefundCount-traList2.lastRefundCount)/traList2.thisRefundCount]*100)/100}}%</li>
          <li v-if="traList2.thisRefundCount==0">上涨</li>
        </ul>
        <ul class="distUl">
          <li>实际营收</li>
          <li>
            <span>{{traList2.thisTotal }}</span>元
          </li>
          <li>相较上一时段{{Math.round(traList2.thisTotal-traList2.lastTotal*100)/100}}元</li>
          <li
            v-if="(traList2.thisTotal-traList2.lastTotal)/traList2.thisTotal<0&&traList2.thisTotal!=0"
          >下降{{Math.round([(traList2.thisTotal-traList2.lastTotal)/traList2.thisTotal]*100)/100}}%</li>
          <li
            v-if="(traList2.thisTotal-traList2.lastTotal)/traList2.thisTotal==0&&traList2.thisTotal!=0"
          >持平</li>
          <li
            v-if="(traList2.thisTotal-traList2.lastTotal)/traList2.thisTotal>0&&traList2.thisTotal!=0"
          >上涨{{Math.round([(traList2.thisTotal-traList2.lastTotal)/traList2.thisTotal]*100)/100}}%</li>
          <li v-if="traList2.thisTotal==0">上涨</li>
        </ul>
        <!--  -->
      </div>
      <!-- 折线图 2-->
      <div id="myChart2" style="width: 100%;height: 400px;"></div>
      <!-- 支付通道 -->
      <div class="flex">
        <p style="font-size:14px;font-weight:600">·支付通道</p>
        <div class="tongji3">
          <el-radio-group v-model="isTotal2" size="mini" style="margin:20px;" @change="getisTotal2">
            <el-radio-button :label="true">交易金额</el-radio-button>
            <el-radio-button :label="false">交易笔数</el-radio-button>
          </el-radio-group>
        </div>
      </div>
      <div class="sytongji">
        <ul class="distUl" v-for="(item,index) in aisleList" :key="index">
          <li>{{item.payName}}</li>
          <li>
            <span>{{item.thisPayMoney}}</span>
            <span v-if="isTotal2" style="font-size:16px;font-weight: normal;">元</span>
            <span v-else style="font-size:16px;font-weight: normal;">笔</span>
          </li>
          <li v-if="isTotal2">相较上一时段{{Math.round((item.thisPayMoney-item.lastPayMoney)*100)/100}}元</li>
          <li v-else>相较上一时段{{Math.round((item.thisPayMoney-item.lastPayMoney)*100)/100}}笔</li>
          <li
            v-if="(traList2.thisPayMoney-traList2.lastPayMoney)/traList2.thisPayMoney<0&&traList2.thisPayMoney!=0"
          >下降{{Math.round([(traList2.thisPayMoney-traList2.lastPayMoney)/traList2.thisPayMoney]*100)/100}}%</li>
          <li
            v-if="(traList2.thisPayMoney-traList2.lastPayMoney)/traList2.thisPayMoney==0&&traList2.thisPayMoney!=0"
          >持平</li>
          <li
            v-if="(traList2.thisPayMoney-traList2.lastPayMoney)/traList2.thisPayMoney>0&&traList2.thisPayMoney!=0"
          >上涨{{Math.round([(traList2.thisPayMoney-traList2.lastPayMoney)/traList2.thisPayMoney]*100)/100}}%</li>
          <li v-if="traList2.thisPayMoney==0">上涨</li>
        </ul>
        <!--  -->
      </div>
      <!-- 饼状图 2-->
      <div id="myChartB" style="width: 100%;height: 400px;margin-top:50px"></div>
      <!-- 弹窗 -->
      <el-dialog title :visible.sync="detailDialog" width="30%" style="margin-top:24vh">
        <p class="detail_label">【{{detailList.payTime}}】交易数据详情</p>
        <div class="detail">
          <ul>
            <li>交易数据</li>
            <li>交易金额：{{detailList.total }}元</li>
            <li>交易笔数：{{detailList.totalCount}}笔</li>
            <li>退款金额：{{detailList.refund }}元</li>
            <li>退款笔数：{{detailList.refundCount}}笔</li>
            <li>实际营收：{{detailList.ActualMoney}}元</li>
          </ul>
          <ul>
            <li>支付通道</li>
            <li>
              微信支付：{{detailList.wxTotalPrice}}笔；
              {{[detailList.wxTotalPrice/(detailList.wxTotalPrice+detailList.zfbTotalPrice+detailList.bkTotalPrice+detailList.vipTotalPrice+detailList.ohterTotalPrice)]*100}}%
            </li>
            <li>
              支付宝支付：{{detailList.zfbTotalPrice}}笔；
              {{[detailList.zfbTotalPrice/(detailList.wxTotalPrice+detailList.zfbTotalPrice+detailList.bkTotalPrice+detailList.vipTotalPrice+detailList.ohterTotalPrice)]*100}}%
            </li>
            <li>
              银行卡支付：{{detailList.bkTotalPrice}}笔；
              {{[detailList.bkTotalPrice/(detailList.wxTotalPrice+detailList.zfbTotalPrice+detailList.bkTotalPrice+detailList.vipTotalPrice+detailList.ohterTotalPrice)]*100}}%
            </li>
            <li>
              会员卡支付：{{detailList.vipTotalPrice}}笔；
              {{[detailList.vipTotalPrice/(detailList.wxTotalPrice+detailList.zfbTotalPrice+detailList.bkTotalPrice+detailList.vipTotalPrice+detailList.ohterTotalPrice)]*100}}%
            </li>
            <li>
              未知：{{detailList.ohterTotalPrice}}笔；
              {{[detailList.ohterTotalPrice/(detailList.wxTotalPrice+detailList.zfbTotalPrice+detailList.bkTotalPrice+detailList.vipTotalPrice+detailList.ohterTotalPrice)]*100}}%
            </li>
          </ul>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import {
  tradingSituation,
  tradingSituationMap,
  tradeData,
  tradeDataMap,
  payAisle
} from "./api/index";
import pagination from "@/components/pagination/index";
export default {
  components: { pagination },
  data() {
    return {
      searchDays: "近7日",
      isTotal: true,
      isTotal2: true,
      startTime: "",
      endTime: "",
      startTime2: "",
      endTime2: "",
      value: "",
      userType:sessionStorage.getItem('userType'),
      peopleCountData: {},
      options: [
        {
          value: 0,
          label: "累计"
        },
        {
          value: 1,
          label: "分时"
        }
      ],
      type: 0,
      todaytotal: [],
      todaytotalCount: [],
      todaypayTime: [],
      yesterdayTotalFee: 0,
      actualMoneyList: [],
      payTimeList: [],
      traList: [],
      traList2: [],
      aisleList: [],
      dataList: [],
      detailList: [],
      detailDialog: false,
      pay_type: 1
    };
  },
  mounted() {
    this.startTime = this.getDay2(0);
    this.startTime2 = this.getDay(-6);
    this.endTime = this.getDay3(0);
    this.endTime2 = this.getDay(0);
    this.gettradingSituation();
    this.gettradingSituationMap();
    this.gettradeData();
    this.gettradeDataMap();
    this.getpayAisle();
  },
  methods: {
    to_more(){
      this.$router.push({path: '/pay/views/flowPreview'})
    },
    getstartTime2(val) {
      console.log(val);
      this.startTime2 = val;
      this.gettradeData();
      this.gettradeDataMap();
      this.getpayAisle();
    },
    getendTime2(val) {
      console.log(val);
      this.endTime2 = val;
      this.gettradeData();
      this.gettradeDataMap();
      this.getpayAisle();
    },
    getDates(val) {
      console.log(val, "val");
      switch (val) {
        case "昨日":
          this.startTime2 = this.getDay(-1);
          this.endTime2 = this.getDay(-1);
          break;
        case "近3日":
          this.startTime2 = this.getDay(-2);
          this.endTime2 = this.getDay(0);
          break;
        case "近7日":
          this.startTime2 = this.getDay(-6);
          this.endTime2 = this.getDay(0);
          break;
        case "近15日":
          this.startTime2 = this.getDay(-14);
          this.endTime2 = this.getDay(0);
          break;
        case "近30日":
          this.startTime2 = this.getDay(-29);
          this.endTime2 = this.getDay(0);
          break;
      }
      this.gettradeData();
      this.gettradeDataMap();
      this.getpayAisle();
    },
    // 当前
    getDay(day) {
      var today = new Date();
      var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      var tHours = today.getHours();
      var tMinutes = today.getMinutes();
      var tSeconds = today.getSeconds();
      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return (
        tYear +
        "-" +
        tMonth +
        "-" +
        tDate +
        " " +
        tHours +
        ":" +
        tMinutes +
        ":" +
        tSeconds
      );
    },
    //00:00:00
    getDay2(day) {
      var today = new Date();
      var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      var tHours = "00";
      var tMinutes = "00";
      var tSeconds = "00";
      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return (
        tYear +
        "-" +
        tMonth +
        "-" +
        tDate +
        " " +
        tHours +
        ":" +
        tMinutes +
        ":" +
        tSeconds
      );
    },
    //23:59:59
    getDay3(day) {
      var today = new Date();
      var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      var tHours = "23";
      var tMinutes = "59";
      var tSeconds = "59";
      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return (
        tYear +
        "-" +
        tMonth +
        "-" +
        tDate +
        " " +
        tHours +
        ":" +
        tMinutes +
        ":" +
        tSeconds
      );
    },
    doHandleMonth(month) {
      var m = month;
      if (month.toString().length == 1) {
        m = "0" + month;
      }
      return m;
    },
    getType(val) {
      if (val) {
        this.type = 1;
      } else {
        this.type = 0;
      }
      this.gettradingSituationMap();
    },
    //支付通道
    getpayAisle() {
      this.loading = true;
      payAisle(this.startTime2, this.endTime2, this.pay_type)
        .then(response => {
          console.log('前端测试    ',response.obj);
          this.aisleList = response.obj;
          this.drawLineB();
          setTimeout(() => {
            this.loading = false;
          }, 500);
        })
        .catch(() => {
          this.loading = false;
        });
    },
    //点击交易金额/交易笔数
    getisTotal(val) {
      console.log(val, "交易");
      this.isTotal = val;
      this.gettradingSituationMap();
    },
    //点击交易金额/交易笔数2
    getisTotal2(val) {
      console.log(val, "交易");
      this.isTotal2 = val;
      if (this.isTotal2) {
        this.pay_type = 1; //交易金额
      } else {
        this.pay_type = 0; //笔数
      }
      this.getpayAisle();
    },
    // 交易概况
    gettradingSituation() {
      this.loading = true;
      tradingSituation(this.startTime, this.endTime)
        .then(response => {
          this.traList = response.obj;
          this.startTime = this.getDay(0);
          this.endTime = this.getDay(0);
          setTimeout(() => {
            this.loading = false;
          }, 500);
        })
        .catch(() => {
          this.loading = false;
        });
    },
    //交易数据
    gettradeData() {
      this.loading = true;
      tradeData(this.startTime2, this.endTime2)
        .then(response => {
          this.traList2 = response.obj;
          setTimeout(() => {
            this.loading = false;
          }, 500);
        })
        .catch(() => {
          this.loading = false;
        });
    },

    //交易数据图
    gettradeDataMap() {
      this.loading = true;
      tradeDataMap(this.startTime2, this.endTime2) //级别，人数
        .then(response => {
          this.dataList = response.obj;

          this.actualMoneyList = [];
          this.payTimeList = [];

          this.dataList.forEach(item => {
            this.actualMoneyList.push(item.actualMoney);
            this.payTimeList.push(item.payTime);
          });
          this.drawLine2();
          setTimeout(() => {
            this.loading = false;
          }, 500);
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 交易数据折线图2
    drawLine2() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart2"));
      const option = {
        //提示框
        tooltip: {
          trigger: "axis",
          position: "top",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.payTimeList
        },
        yAxis: {
          type: "value",
          show: false
        },
        series: [
          {
            name: "实际营收",
            data: this.actualMoneyList,
            type: "line",
            areaStyle: {}
          }
        ],
        color: ["#5cd5d5"]
      };
      // 绘制图表
      myChart.setOption(option);

      myChart.on("click", params => {
        myChart.setOption(option);
        this.detailList = this.dataList[params.dataIndex];
        this.detailDialog = true;
      });
    },
    // 今日交易概况统计图
    gettradingSituationMap() {
      this.loading = true;
      tradingSituationMap(this.type) //级别，人数
        .then(response => {
          let todayList = response.obj.todayList;
          let yesterdayList = response.obj.yesterdayList;
          this.yesterdayTotalFee = response.obj.yesterdayTotalFee;
          this.todaytotal = [];
          this.todaytotalCount = [];
          this.todaypayTime = [];

          this.yesterdaytotal = [];
          this.yesterdaytotalCount = [];
          this.yesterdayTime = [];

          // total 交易金额  totalCount 交易笔数  //支付时间
          todayList.forEach(item => {
            this.todaytotal.push(item.total);
            this.todaytotalCount.push(item.totalCount);
            this.todaypayTime.push(item.payTime);
          });
          yesterdayList.forEach(item => {
            this.yesterdaytotal.push(item.total);
            this.yesterdaytotalCount.push(item.totalCount);
            this.yesterdayTime.push(item.payTime);
          });
          this.drawLine();
          setTimeout(() => {
            this.loading = false;
          }, 500);
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 交易概况 折线图
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart1"));
      const option = {
        //提示框
        tooltip: {
          trigger: "axis",
          position: "top",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.todaypayTime
        },
        yAxis: {
          type: "value",
          show: false
        },
        series: [
          {
            name: "今日",
            data: this.isTotal ? this.todaytotal : this.todaytotalCount,
            type: "line",
            areaStyle: {}
          },
          {
            name: "昨日",
            data: this.isTotal ? this.yesterdaytotal : this.yesterdaytotalCount,
            type: "line",
            areaStyle: {}
          }
        ],
        color: ["#f48194", "#fcdc5d"]
      };
      // 绘制图表
      myChart.setOption(option);
    },
    // 代理人数统计饼状图
    drawLineB() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChartB"));
      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          //图例
          orient: "horizontal", //图例的布局，vertical竖直    horizontal为水平
          x: "right", //图例显示在右边
          textStyle: {
            itemWidth: 1000
          },
          data: [
            {
              name: this.aisleList[0].payName
            },
            {
              name: this.aisleList[1].payName
            },
            {
              name: this.aisleList[2].payName
            },
            {
              name: this.aisleList[3].payName
            },
            {
              name: this.aisleList[4].payName
            }
          ],
          textStyle: {
            //图例文字的样式
            color: "#333", //文字颜色
            fontSize: 12 //文字大小
          }
        },
        color: ["#f48194", "#5cd5d5", "#70d58e", "#fcdc5d", "#b5b5b5"],
        series: [
          {
            name: "支付通道",
            type: "pie",
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: this.aisleList[0].thisPayMoney,
                name: this.aisleList[0].payName
              },
              {
                value: this.aisleList[1].thisPayMoney,
                name: this.aisleList[1].payName
              },
              {
                value: this.aisleList[2].thisPayMoney,
                name: this.aisleList[2].payName
              },
              {
                value: this.aisleList[3].thisPayMoney,
                name: this.aisleList[3].payName
              },
              {
                value: this.aisleList[4].thisPayMoney,
                name: this.aisleList[4].payName
              }
            ]
          }
        ]
      });
    }
  }
};
</script>
<style >
.active{
  background: #e1edff;
}
.detail_label {
  text-align: center;
  padding: 20px 0;
  border-bottom: 1px dashed #ccc;
  color: #000;
  font-size: 18px;
}
.detail {
  display: flex;
  justify-content: space-around;
  line-height: 30px;
  color: #101010;
  padding: 20px 0;
}
.detail ul li:first-of-type {
  font-weight: 600;
}
ul {
  margin: 0;
  padding: 0;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>
<style scoped>
.sytongji {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.tongji3 {
  /* border:1px solid red; */
  text-align: right;
}
.tongji2 {
  display: flex;
  align-items: center;
}
.tongji {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-bottom: 1px dashed #ccc;
  padding: 30px 0;
}
.distUl {
  text-align: center;
  line-height: 30px;
}

.distUl2 {
  line-height: 30px;
  font-size: 14px;
}
.distUl li:first-of-type {
  font-size: 16px;
}
.distUl li:nth-of-type(2) {
  font-size: 16px;
}
.distUl li:nth-of-type(2) span {
  font-size: 30px;
  font-weight: bold;
  margin: 0 5px;
}
.distUl li:nth-of-type(3) {
  font-size: 12px;
  color: #aaa;
}
.distUl li:last-of-type {
  font-size: 14px;
  color: #aaa;
}
i {
  margin: 0 5px;
  font-size: 20px;
}
p {
  margin: 0;
}
li {
  list-style: none;
}
</style>


