<template>
  <div class="memberLevel">
    <el-button @click="getvipList"
               size="small">
      <i class="el-icon-refresh"></i>刷新
    </el-button>
    <div class="level-ul justleft"
         style="flex-wrap: wrap;">
      <div class="level-li"
           v-for="(item,index) in vipList"
           :key="index">
        <div class="justleft vipCard">
          <label class>{{item.name}}</label>
          <p v-if="item.discount && item.discount!=1"
             class="cardbtn">{{item.discount*10}}折</p>
        </div>
        <el-tooltip class="item"
                    effect="dark"
                    placement="top">
          <div slot="content">
            <span v-if="item.memberLimitAmount">需充值到{{item.memberLimitAmount}}元;</span>
            <span v-if="item.discount && item.discount!=1">享受消费折扣{{item.discount*10}}折;</span>
            <span v-if="item.giftMoney">送金额{{item.giftMoney}}元;</span>
            <span v-if="item.giftScore">送积分{{item.giftScore}}积分;</span>
          </div>
          <p style="width:50%"
             class="upgradeRules">升级规则</p>
        </el-tooltip>
        <p class="rechargeTO">充值到{{item.memberLimitAmount}}元</p>
        <div class="seIcon">
          <span @click.stop="redactdata(item)">
            <i class="el-icon-edit"></i>
          </span>
          <span @click="deVipList(item)">
            <i class="el-icon-delete"></i>
          </span>
        </div>
      </div>
      <div class="level-li active justcenter"
           @click="addRating">
        <div>+添加等级</div>
      </div>
    </div>

    <!-- dialog -->
    <el-dialog title="添加等级"
               :visible.sync="dialogVisible"
               width="60%">
      <div class="levelDialog_con">
        <el-steps :active="active"
                  finish-status="success">
          <el-step title="新增会员等级"></el-step>
          <el-step title="会员权益"></el-step>
          <el-step title="升级礼包"></el-step>
        </el-steps>
      </div>
      <!--新增会员等级内容  -->
      <div v-if="active === 1">
        <div class="mt50"
             v-for="(item,index) in addList"
             :key="index"
             style="border-bottom: 1px solid #eee">
          <div class="leter">
            <span class="red">*</span>
            <span class="label">等级名称：</span>
            <el-input size="mini"
                      v-model="item.name"
                      class="ml20"
                      style="width:45%;"
                      placeholder="请输入等级名称"></el-input>
            <el-button @click="remove(index)"
                       size="small"
                       v-if="addList.length>1"
                       class="ml20"
                       type="danger">移除</el-button>
          </div>
          <div class="leter">
            <span class="red">*</span>
            <span class="label">等级图片：</span>
            <el-radio-group class="ml20"
                            v-model="item.radio"
                            @change="seImage(index)">
              <el-radio label="1">默认图片</el-radio>
              <el-radio label="2">自定义图片</el-radio>
            </el-radio-group>
          </div>
          <div class="leter"
               style="padding: 0 105px;"
               v-if="item.radioType">
            <el-upload class="avatar-uploader"
                       :headers="headers"
                       :action="importUrl"
                       :show-file-list="false"
                       name="files"
                       :on-success="handleAvatarSuccess.bind(null,{'index':index,'data':item})"
                       :before-upload="beforeAvatarUpload">
              <img v-if="item.photo2"
                   :src="item.photo2"
                   class="avatar" />
              <i v-else
                 class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
        </div>
        <div v-if="addList.length<5"
             @click="again"
             class="addlevel-btn">+继续添加等级</div>
      </div>
      <!-- 会员权益内容 -->
      <div v-if="active === 2">
        <div class="step-tips">
          <i class="el-icon-warning"></i>
          <span>会员将按照一次性充值金额升级</span>
          <p>如果会员注册了，但没有充值，系统会归入默认等级，默认等级的会员不能享受会员权益</p>
        </div>
        <div class="level-price-div justcenter"
             v-for="(item,index) in addList2"
             :key="index">
          <p class="center">
            <span class="red">*</span>
            <span style="margin-right:10px"
                  class="weight7">{{addList[index].name}}:</span>充值到
            <el-input size="mini"
                      v-model="item.memberLimitAmount"
                      style="width:30%;margin:0 10px"
                      placeholder="请输入"></el-input>元
          </p>
          <p class="center">
            <el-radio v-model="item.radio"
                      label="1">享受消费折扣</el-radio>
            <el-input :disabled="item.radio!=1?true:false"
                      size="mini"
                      v-model="item.discount"
                      style="width:30%"
                      placeholder="请输入"></el-input>折
            <el-radio class="ml20"
                      v-model="item.radio"
                      label="2">不打折</el-radio>
          </p>
        </div>
        <div class="team-price">
          <div>
            <p class="center justcenter"
               style="margin-top:10px;">
              <span class="red">*</span>
              <span style="min-width:105px;display: inline-block;"
                    class="weight7">会员特权说明</span>
            </p>
            <el-input :rows="5"
                      v-model="rightExplain"
                      class="ml20"
                      type="textarea"
                      :maxlength="500"
                      placeholder="请输入内容"
                      style="width:100%;"></el-input>
          </div>
          <p class="instructionNum">{{rightExplain.length}}/{{500}}</p>
        </div>
      </div>
      <!-- 升级礼包内容 -->
      <div v-if="active === 3">
        <div class="level-price-div justcenter"
             v-for="(item,index) in addList3"
             :key="index">
          <p class="center">
            <span class="red">*</span>
            <span style="margin-right:10px"
                  class="weight7">升级至{{addList[index].name}}:</span>
            <el-checkbox v-model="item.duoxuan">送金额</el-checkbox>
            <el-input :disabled="item.duoxuan==false?true:false"
                      size="mini"
                      v-model="item.giftMoney"
                      style="width:30%;margin:0 10px"
                      placeholder="请输入"></el-input>元
          </p>
          <p class="center">
            <el-checkbox v-model="item.duoxuan2">送积分</el-checkbox>
            <el-input :disabled="item.duoxuan2==false?true:false"
                      size="mini"
                      v-model="item.giftScore"
                      style="width:30%"
                      placeholder="请输入"
                      class="mr20"></el-input>积分
          </p>
        </div>
      </div>
      <!--新增会员等级内容 end -->
      <div class="deployBtn">
        <el-button size="small"
                   @click="dialogVisible=false">放弃</el-button>
        <el-button size="small"
                   @click="prev"
                   v-if="active==2 || active == 3">上一步</el-button>
        <el-button size="small"
                   type="primary"
                   @click="next"
                   v-if="active==1 || active == 2">下一步</el-button>
        <el-button size="small"
                   type="primary"
                   @click="vipSubmit"
                   v-if="active==3">提交</el-button>
      </div>
    </el-dialog>
    <el-dialog title="编辑等级"
               :visible.sync="redact_dialogVisible"
               width="60%">
      <div class="justleft">
        <p class="justleft right"
           style="min-width: 100px;">
          <span class="red">*</span>
          <span class="label weight7">等级名称：</span>
        </p>
        <el-input v-model="amendDialogData.name"
                  size="mini"
                  class="ml20"
                  style="width:45%;"
                  placeholder="请输入等级名称"></el-input>
      </div>
      <div class="justleft mt">
        <p class="justleft right"
           style="min-width: 100px;">
          <span class="red">*</span>
          <span class="label weight7">等级图片：</span>
        </p>
        <el-radio-group class="ml20"
                        v-model="imgRadio"
                        @change="seImage2()">
          <el-radio label="1">默认图片</el-radio>
          <el-radio label="2">自定义图片</el-radio>
        </el-radio-group>
      </div>
      <div class="justleft mt">
        <div class="leter"
             style="padding: 0 105px;width:100%"
             v-if="imgradioType">
          <el-upload class="avatar-uploader"
                     :headers="headers"
                     :action="importUrl"
                     :show-file-list="false"
                     name="files"
                     :on-success="handleAvatarSuccess2"
                     :before-upload="beforeAvatarUpload">
            <img v-if="amendDialogData.photo"
                 :src="amendDialogData.photo"
                 class="avatar" />
            <i v-else
               class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </div>
      </div>
      <div class="justleft mt"
           style="align-items: flex-end;">
        <div style="display: flex; justify-content: flex-start; align-items: flex-start;width:70%">
          <p class="justleft right"
             style="min-width: 100px;">
            <span class="red">*</span>
            <span class="weight7">会员特权说明</span>
          </p>
          <el-input :rows="5"
                    v-model="amendDialogData.rightExplain"
                    class="ml20"
                    type="textarea"
                    :maxlength="500"
                    placeholder="请输入内容"
                    style="width:100%;"></el-input>
        </div>
        <p class="instructionNum">{{rightExplain.length}}/{{500}}</p>
      </div>
      <div class="mt justleft">
        <p class="justleft right"
           style="min-width: 100px;">
          <span class="red">*</span>
          <span style="margin-right:10px"
                class="weight7">会员权益：</span>
        </p>
        <span class="ml20">充值到</span>
        <el-input v-model="amendDialogData.memberLimitAmount"
                  size="mini"
                  style="width:20%;margin:0 10px"
                  placeholder="请输入"></el-input>元
        <p class>
          <el-radio v-model="discountRaio"
                    label="1">享受消费折扣</el-radio>
          <el-input size="mini"
                    style="width:30%"
                    placeholder="请输入"
                    v-model="amendDialogData.discount "
                    :disabled="discountRaio!='1'?true:false"></el-input>折
          <el-radio v-model="discountRaio"
                    class="ml20"
                    label="2">不打折</el-radio>
        </p>
      </div>
      <div class="mt justleft">
        <p class>
          <span class="red">*</span>
          <span style="margin-right:10px"
                class="weight7">升级礼包：</span>
          <el-checkbox v-model="giftMoneybox"
                       class="ml20">送金额</el-checkbox>
          <el-input size="mini"
                    style="width:30%;margin:0 10px"
                    placeholder="请输入"
                    :disabled="!giftMoneybox?true:false"
                    v-model="amendDialogData.giftMoney"></el-input>元
        </p>
        <p class="center">
          <el-checkbox v-model="giftScoreBox">送积分</el-checkbox>
          <el-input size="mini"
                    :disabled="!giftScoreBox?true:false"
                    v-model="amendDialogData.giftScore"
                    style="width:30%"
                    placeholder="请输入"
                    class="mr20"></el-input>积分
        </p>
      </div>
      <span slot="footer"
            class="dialog-footer">
        <el-button size="small"
                   @click="redact_dialogVisible = false">取 消</el-button>
        <el-button size="small"
                   type="primary"
                   @click="saveUpdate">保 存</el-button>
      </span>
    </el-dialog>
    <!--  -->
  </div>
</template>
<script>
import {
  addactive1,
  addactive2,
  addactive3,
  vipList,
  deleteList,
  updateList
} from "../../api/memberLevel";
import { url } from "@/utils/request";
export default {
  name: "memberLevel",
  data() {
    return {
      active: 1,
      imgRadio: "1",
      index: 1,
      imgradioType: false,
      dialogVisible: false,
      vipList: [],
      photoUrl: url + "/fms/upload/resource/",
      importUrl: url + "/fms/upload/files_upload/file/user",
      addList: [
        { name: "", radio: "1", photo: "", photo2: "", radioType: false }
      ],
      addList2: [{ memberLimitAmount: 0, discount: "", radio: "1" }],
      addList3: [
        { giftMoney: "", giftScore: "", duoxuan: false, duoxuan2: false }
      ],
      name: "",
      headers: {
        authorized: sessionStorage.token
      },
      rightExplain: "",
      customImage: false,
      addbtn: true,
      //图片上传-------------------
      files: "",
      redact_dialogVisible: false,
      amendDialogData: "",
      discountRaio: "1",
      giftMoneybox: false,
      giftScoreBox: false,
      objImg: ""
    };
  },
  created() {
    this.getvipList();
  },
  methods: {
    getdiscountRaio(val) {
      if (val == 2) {

      }
    },
    //修改
    saveUpdate() {
      this.amendDialogData.photo = this.objImg;

      if (this.discountRaio == 2) {
        this.amendDialogData.discount = 1;
      } else {
        this.amendDialogData.discount = this.amendDialogData.discount / 10;
      }
      updateList(this.amendDialogData)
        .then(response => {
          this.$message({
            message: response.msg,
            type: "success"
          });
          this.redact_dialogVisible = false;
          this.getvipList();
        })
        .catch(() => { });
    },
    //删除
    deVipList(item) {
      console.log(item.id, "item");
      deleteList(item.id)
        .then(response => {
          this.$message({
            message: response.msg,
            type: "success"
          });
          this.getvipList();
        })
        .catch(() => { });
    },
    redactdata(item) {
      if (item.photo) {
        this.imgRadio = "2";
        this.imgradioType = true;
        if (item.photo.indexOf(this.photoUrl) == -1) {
          item.photo = this.photoUrl + item.photo;
        }
      } else {
        this.imgRadio = "1";
        this.imgradioType = false;
      }

      if (item.discount != 1) {
        this.discountRaio = "1";
      } else {
        this.discountRaio = "2";
        item.discount = "";
      }
      if (item.giftMoney) {
        this.giftMoneybox = true;
      } else {
        this.giftMoneybox = false;
      }
      if (item.giftScore) {
        this.giftScoreBox = true;
      } else {
        this.giftScoreBox = false;
      }
      this.amendDialogData = item;
      this.redact_dialogVisible = true;
    },
    //会员等级列表
    getvipList() {
      this.loading = true;
      vipList()
        .then(response => {
          this.vipList = response.obj.content;
          console.log(this.vipList, "vipList");
        })
        .catch(() => { });
    },
    //提交
    vipSubmit() {
      let isname3 = true;
      this.addList3.forEach(item3 => {
        if (item3.duoxuan == true && !item3.giftMoney) {
          isname3 = false;
        } else if (item3.duoxuan2 == true && !item3.giftScore) {
          isname3 = false;
        } else {
          isname3 = true;
        }
      });
      if (isname3) {
        //接口
        var newList = [];

        for (let i = 0; i < this.addList.length; i++) {
          let newObj = {};
          Object.assign(
            newObj,
            this.addList[i],
            this.addList2[i],
            this.addList3[i]
          );
          newList.push(newObj);
          newObj.rightExplain = this.rightExplain;
        }

        console.log(newList, "newList");
        // return
        addactive3(newList)
          .then(response => {
            console.log(response, "respone");
            this.$message({
              message: response.msg,
              type: "success"
            });
            this.dialogVisible = false;
            this.getvipList();
          })
          .catch(() => { });
      } else {
        this.$message({
          message: "请输入金额和积分",
          type: "warning"
        });
      }
    },
    //移除
    remove(index) {
      console.log(index, "下标");
      this.addList.splice(index, 1);
    },
    //上一步
    prev() {
      --this.active;
      if (this.active < 1) this.active = 1;
    },
    //下一步
    next() {
      let isname = true;
      let isname2 = true;

      if (this.active == 1) {
        this.addList.forEach(item => {
          // console.log(item, "item");
          // console.log(isname, "isname");
          if (!item.name) isname = false;
        });
        if (isname) {
          if (this.active++ > 3) this.active = 1;
          //请求接口
          var mapList = this.addList.map(item => {
            return item.name;
          });

          addactive1(mapList)
            .then(response => {
              console.log(response, "respone");
            })
            .catch(() => { });
        } else {
          this.$message({
            message: "请输入等级名称",
            type: "warning"
          });
        }
      } else if (this.active == 2) {
        this.addList2.forEach(item2 => {
          if (!item2.memberLimitAmount) {
            isname2 = false;
          } else if (item2.radio == 1 && !item2.discount) {
            isname2 = false;
          } else {
            isname2 = true;
          }
        });
        if (isname2) {
          if (this.rightExplain) {
            if (this.active++ > 3) this.active = 1;
            //请求接口
            var mapList2 = this.addList2.map(item => {
              return item.memberLimitAmount;
            });
            console.log(mapList2, "mapList2");
            addactive2(mapList2)
              .then(response => {
                console.log(response, "respone");
              })
              .catch(() => { });
          } else {
            this.$message({
              message: "请输入会员特权说明",
              type: "warning"
            });
          }
        } else {
          this.$message({
            message: "请输入充值金额和有效享受消费折扣(0.1~9.9)",
            type: "warning"
          });
        }
      }
    },
    //显示自定义图片
    seImage(index) {
      if (this.addList[index].radio == "2") {
        this.addList[index].radioType = true;
      } else {
        this.addList[index].radioType = false;
      }
    },
    seImage2() {
      if (this.imgRadio == "2") {
        this.imgradioType = true;
      } else {
        this.imgradioType = false;
      }
    },
    //添加
    addRating() {
      this.addList = [
        { name: "", radio: "1", photo: "", photo2: "", radioType: false }
      ];
      this.addList2 = [{ memberLimitAmount: 0, discount: "", radio: "1" }];
      this.addList3 = [
        { giftMoney: "", giftScore: "", duoxuan: false, duoxuan2: false }
      ];
      this.rightExplain = "";
      this.dialogVisible = true;
      this.active = 1;
    },
    again() {
      console.log(this.addList.length, "addList");
      if (this.addList.length < 5) {
        this.addList.push({
          name: "",
          radio: "1",
          photo: "",
          photo2: "",
          radioType: false
        });
        console.log(this.addList, "111111")
        this.addList2.push({
          memberLimitAmount: 0,
          discount: "",
          radio: "1",
          rightExplain: ""
        });
        this.addList3.push({
          giftMoney: "",
          giftScore: "",
          duoxuan: false,
          duoxuan2: false
        });
      }
    },
    handleAvatarSuccess(obj, res, file) {
      var index = obj.index;
      console.log(this.addList[index].photo2, "2222");
      this.addList[index].photo2 = URL.createObjectURL(file.raw);
      this.addList[index].photo = res.obj;
    },
    handleAvatarSuccess2(obj, file) {
      console.log(obj.obj, "file");
      this.objImg = obj.obj;
      this.amendDialogData.photo = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      // if (!isJPG) {
      //   this.$message.error("上传头像图片只能是 JPG 格式!");
      // }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isLt2M;
    }
  }
};
</script>
<style>
.el-popover {
  background: #303133 !important;
  color: #fff;
  height: 20px;
  line-height: 5px;
}
.el-dialog {
  background: #ffffff;
}
</style>
<style scoped>
.seIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f1f1f1;
  margin-top: 17px;
  height: 50px;
}
.seIcon span {
  width: 50%;
  text-align: center;
  font-size: 20px;
}
.seIcon span:first-of-type {
  border-right: 1px solid #ccc;
}
.upgradeRules {
  color: #8995a0;
  font-size: 14px;
  padding: 0 30px;
}
.rechargeTO {
  color: #57636f;
  font-size: 14px;
  padding: 0 30px;
}
.vipCard {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
}
.vipCard label {
  font-size: 16px;
  font-weight: inherit;
  margin-right: 10px;
}
.cardbtn {
  background: #1087C7;
  border-radius: 20px;
  color: #ffffff;
  font-size: 14px;
  width: 50px;
  height: 20px;
  text-align: center;
  line-height: 20px;
}
.avatar {
  width: 100%;
}
.label {
  min-width: 70px;
}
.team-price div {
  width: 70%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.team-price {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.instructionNum {
  padding: 10px;
}
.level-price-div {
  border-bottom: 1px dashed #ddd;
  padding: 20px;
}
.team-price {
  margin-top: 20px;
}
.step-tips {
  text-align: center;
  color: #1087C7;
  padding: 20px 0;
  background: #eaf2ee;
}
.step-tips span {
  font-weight: 700;
}
.step-tips p {
  color: #8995a0;
  font-size: 14px;
  margin: 0;
  padding-top: 20px;
}
.deployBtn {
  margin-top: 40px;
  text-align: center;
}
.memberLevel {
  padding: 20px;
}
.level-li {
  width: calc(25% - 20px);
  margin: 20px 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  height: 181px;
}
.level-li.active {
  border: 1px solid #1087C7;
  border-radius: 4px;
  color: #1087C7;
  cursor: pointer;
}
.addlevel-btn {
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 14px;
  font-weight: 400;
  color: #2c393f;
  border: 1px dashed #ddd;
  text-align: center;
  cursor: pointer;
}
.leter {
  width: 50%;
  margin: auto;
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.avatar-uploader {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px dashed #d9d9d9;
}
</style>


