<template>
  <div>
    <div class="horn-btns">
      <el-button type="primary" @click="addOrEdit(1)" size="small">新增</el-button>
    </div>
    <el-table :data="tableData"
              style="width: 100%">
      <el-table-column prop="id"
                       label="id"
                       min-width="125">
      </el-table-column>
      <el-table-column prop="deviceId"
                       label="设备id"
                       min-width="100">
      </el-table-column>
      <el-table-column prop="status"
                       label="状态"
                       min-width="80">
        <template slot-scope="scope">
          {{scope.row.status?'启用':'禁用'}}
        </template>
      </el-table-column>
      <el-table-column prop="remark"
                       label="备注"
                       min-width="180">
      </el-table-column>
      <el-table-column prop=""
                       label="操作"
                       width="180">
        <template slot-scope="scope">
          <el-button type="text" @click="addOrEdit(2, scope.row)">编辑</el-button>
          <el-button type="text" @click="testDevice(scope.row)">连接测试</el-button>
          <el-button type="text" @click="delDevice(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--云喇叭配置-->
    <el-dialog title="云喇叭配置"
               v-if="dialogHornConfig.show"
               :visible.sync="dialogHornConfig.show"
               width="490px" append-to-body>
      <HornConfig :propsInfo="dialogHornConfig" :storeId="params.storeId" @getTableList="getList"></HornConfig>
    </el-dialog>
  </div>
</template>

<script>
// import * as commissionApi from '../../../api/commission'
// import ImgUpload from '@/components/imgUpload/index'
import * as storeApi from '../../api/store'
import HornConfig from './cloudHornConfig.vue'
export default {
  mixins: [],
  components: {
    HornConfig
  },
  props: {
    propsInfo: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      params: {
        storeId: null
      },
      tableData: [],
      dialogHornConfig: {
        show: false
      }
    }
  },
  watch: {},
  computed: {},
  created () {
    this.params.storeId = this.propsInfo.data.id
    this.getList()
  },
  methods: {
    /**
     * 获取列表
     * */
    getList () {
      storeApi.hornList(this.params).then(res => {
        this.tableData = res.obj
      })
    },
    /**
     * 获取列表--新增
     * */
    addOrEdit (type, data) {
      this.dialogHornConfig = {
        show: true,
        type: type,
        data: data
      }
    },
    /**
     * 获取列表--新增
     * */
    testDevice (data) {
      let params = {
        deviceId: data.deviceId
      }
      storeApi.hornListTest(params).then(res => {
        this.$message.success(res.msg)
      })
    },
    /**
     * 获取列表--新增
     * */
    delDevice (data) {
      this.$confirm('确定删除该记录？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.confirmDel(data)
      }).catch(() => {
      })
    },
    confirmDel (data) {
      storeApi.hornListDel(data.id).then(res => {
        this.$message.success(res.msg)
        this.getList()
      })
    }
  }
}
</script>
<style scoped>
  .horn-btns{
    margin-bottom: 10px;
    margin-top: -20px;
    text-align: right;
  }
</style>



