<template>
  <div class="app-container"
       v-loading="loading">
    <div class="tab_header_bar">
      <div class="block">
        <el-input v-model="id"
                  size="small"
                  placeholder="授权单号"
                  type="text">
        </el-input>
      </div>
      <div class="block">
        <el-select size="small"
                   v-model="status"
                   placeholder="状态">
          <el-option v-for="item in options"
                     :key="item.value"
                     :label="item.name"
                     :value="item.value"></el-option>
        </el-select>
      </div>
      <div class="block">
        <el-date-picker v-model="startTime"
                        type="date"
                        size="small"
                        placeholder="开始日期"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd"
                        style="width: 190px"></el-date-picker>
        <span>至</span>
        <el-date-picker v-model="endTime"
                        type="date"
                        size="small"
                        placeholder="结束日期"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd"
                        style="width: 190px"></el-date-picker>
      </div>
      <div class="block"
           style="margin-left: 10px">
        <el-button size="small"
                   type="primary"
                   @click="search">查 询</el-button>
      </div>
    </div>
    <!--表格-->
    <el-table :data="tableData"
              style="width: 100%">
      <el-table-column type="index"
                       label="序号"
                       width="100"
                       :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="id"
                       label="授权单号"
                       :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="money"
                       label="冻结金额"
                       :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="status"
                       label="冻结状态"
                       :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="leftMoney"
                       label="剩余押金"
                       :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="orderId  "
                       label="消费单号"
                       :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <p v-if="scope.row.orderId">{{scope.row.orderId}}</p>
          <p v-else>--</p>
        </template>
      </el-table-column>
      <el-table-column prop="createTime"
                       label="冻结时间"
                       :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="remark"
                       label="备注"
                       :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-button v-if="scope.row.remark"
                     type="text"
                     size="small"
                     @click="openRem(scope.row)">{{scope.row.remark}}
          </el-button>
          <el-button v-else
                     type="text"
                     size="small"
                     @click.stop="openRem(scope.row)">备注
          </el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作"
                       :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-button :disabled="scope.row.status=='冻结成功'?false:true"
                     size="small"
                     type="text"
                     @click="openQuit(scope.row)">退押金
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination :total-elements="totalElements"
                :change-callback="getMoneyList"
                ref="page">
    </pagination>
    <el-dialog title="备注"
               :visible.sync="createRem"
               :before-close="closeRem"
               width="400px">
      <el-form :model="remForm"
               :label-position="labelPosition"
               label-width="110px"
               style="margin-top:10px 10px">
        <el-form-item v-if="status1"
                      label="官方订单号："
                      :rules="{required: true, message: '官方订单号不能为空', trigger: 'blur'}">
          <el-input type="text"
                    v-model="remForm.transactionId"
                    placeholder="请输入官方订单号"
                    style="width:230px;">
          </el-input>
        </el-form-item>
        <el-form-item label="备注信息：">
          <el-input type="textarea"
                    v-model="remForm.remark"
                    placeholder="如房号"
                    :rows="3"
                    maxlength="20"
                    show-word-limit
                    style="width:230px;">
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type=""
                     size="small"
                     @click="closeRem">取 消
          </el-button>
          <el-button type="primary"
                     size="small"
                     @click="remSub">确 定
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="退押金"
               :visible.sync="createQuit"
               :before-close="closeQuit"
               width="400px">
      <el-form :model="quitForm"
               :label-position="labelPosition"
               label-width="110px"
               style="margin-top:10px;">
        <el-form-item label="退押金金额：">
          <div>{{money}}</div>
        </el-form-item>
        <el-form-item label="消费："
                      :rules="{required: true, message: '消费金额不能为空', trigger: 'blur'}">
          <el-input size="small"
                    v-model="quitForm.usedFee"
                    placeholder="请输入消费金额"
                    style="width:230px;">
          </el-input>
          <p style="color: #ffac21;font-size:12px;margin-left:5px">官方要求最低消费金额不小于0.01元</p>
        </el-form-item>
        <el-form-item label="退押金：">
          <span v-if="!quitForm.usedFee">请先输入消费金额</span>
          <span v-else>{{money-quitForm.usedFee}}</span>
        </el-form-item>
        <el-form-item>
          <el-button type=""
                     size="small"
                     @click="closeQuit">取 消
          </el-button>
          <el-button type="primary"
                     size="small"
                     @click="quitSub">确 定
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import pagination from '@/components/pagination/index'
import { getMoney, editRem, quitMon } from '../api/deposit'
export default {
  name: 'depositManage',
  components: { pagination },
  data () {
    return {
      loading: false, // 页面加载
      tableData: [], // 表格数据
      totalElements: 0, // 表格总条数
      createRem: false,
      createQuit: false,
      labelPosition: 'right',
      // 搜索条件
      id: '',
      status: '',
      startTime: '',
      endTime: '',
      status1: false,
      status2: false,
      money: '',
      id1: '',
      remForm: {
        id: '',
        remark: '',
        transactionId: ''
      },
      quitForm: {
        usedFee: '',
        depositId: ''
      },
      options: [
        {
          value: 1,
          name: '冻结中'
        }, {
          value: 2,
          name: '冻结成功'
        }, {
          value: 3,
          name: '冻结失败'
        }, {
          value: 4,
          name: '已解冻'
        }
      ],
      dayRange: '0' // 日期范围
    }
  },
  mounted () {
    this.getMoneyList()
  },

  methods: {
    openQuit (row) {
      this.id1 = row.id
      this.money = row.money
      this.quitForm = {
        usedFee: '',
        depositId: ''
      }
      this.createQuit = true
    },
    closeQuit () {
      this.createQuit = false
      this.quitForm = {
        usedFee: '',
        depositId: ''
      }
    },
    quitSub () {
      this.createQuit = false
      this.quitForm.depositId = this.id1
      this.quitForm.usedFee = this.quitForm.usedFee * 100
      quitMon(this.quitForm).then(res => {
        this.$message({
          message: res.msg,
          type: 'success'
        })
        this.$refs.page.refresh()
      }).catch(() => { })
    },
    openRem (row) {
      this.createRem = true
      if (row.status === '冻结成功') {
        this.status1 = true
      } else {
        this.status1 = false
      }
      this.remForm.remark = row.remark
      this.remForm.id = row.id
    },
    closeRem () {
      this.createRem = false
    },
    remSub () {
      this.createRem = false
      editRem(this.remForm).then(res => {
        this.$message({
          message: res.msg,
          type: 'success'
        })
        this.$refs.page.refresh()
      }).catch(() => { })
    },
    search () {
      this.$refs.page.search()
    },
    // 获取押金管理列表
    getMoneyList (number, pageSize) {
      this.loading = true
      getMoney(number, pageSize, this.id, this.status, this.startTime, this.endTime).then(res => {
        let data = res.obj
        this.tableData = data.content
        this.totalElements = data.totalElements
        data.content.forEach(element => {
          switch (element.status) {
            case 1:
              element.status = '冻结中'
              break
            case 2:
              element.status = '冻结成功'
              break
            case 3:
              element.status = '冻结失败'
              break
            case 4:
              element.status = '已解冻'
              break
          }
        })
        setTimeout(() => {
          this.loading = false
        }, 500)
      }).catch(() => {
        this.loading = false
      })
    }
  }
}
</script>

<style scoped>
/*public*/
.status {
  display: flex;
  justify-content: flex-start;
  padding: 0 20px;
  color: #ffac21;
}
.nodata {
  position: relative;
  left: 0;
  top: -20px;
  width: 100%;
  height: 100%;
  background-color: #3a3a3a;
}

.block {
  display: inline-block;
  vertical-align: middle;
}
/*卡片*/
.card_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}

.card_item {
  padding: 0;
  width: 15%;
  max-width: 100%;
  margin: 20px 1% 0 1%;
}

.tools_box {
  padding: 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.tools_img {
  width: 30%;
  text-align: left;
}

.tools_text {
  width: 70%;
  text-align: right;
}

.tools_text p {
  margin: 0;
  padding: 0;
  cursor: default;
}

.tools_text p:nth-child(1) {
  font-size: 12px;
  color: #888;
}

.tools_text p:nth-child(2) {
  padding: 5px 0 0 0;
  font-size: 20px;
  color: #3f3f3f;
}

/*图表*/
.chart_img {
  width: 100%;
  height: 400px;
}

.lineTwo_chart_img {
  width: 50%;
  height: 400px;
}

.lineTwo_chart_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.analysisBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.analysis_table {
  border: 1px solid #cdcdcd;
  font-size: 14px;
}
.analysis_table thead tr {
  font-weight: bold;
  height: 24px;
  line-height: 28px;
  text-align: left;
}
.analysis_table tbody tr td {
  color: #718195;
}
.gender_bar_item {
  margin: 0 auto;
  width: 50%;
}

.bar_content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bar_title p {
  padding: 0;
  font-size: 16px;
  text-align: center;
}

.bar_icon_group {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.bar_icon {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.bar_proportion {
  height: 20px;
  width: 100%;
  background-color: #ffac21;
}

.girl_mark {
  position: relative;
  top: 0;
  left: 0;
  height: 20px;
  background-color: #81be94;
}
span {
  font-size: 14px;
}
</style>



