<template>
  <div class="login-container" v-bind:style="bgStyle">

    <div class="header-bar">
      <div>
        <img v-if="siteInfo.logo" :src="siteInfo.logo" class="header-logo-left" alt="">
      </div>
      <div class="service-info">
        <div><span>客服热线：{{info.servicePhone}}</span></div>
        <div class="wx-box"><img src="../../assets/images/login/weChat.png" class="wxlogo" alt=""><span>关注微信公众号</span>
        </div>
      </div>
    </div>

    <el-form class="login-form loginform">
      <!--<h3 class="title">脸付代理商登录</h3>-->
      <div class="header-logo">
        <!--<img v-if="siteInfo.logo" :src="siteInfo.logo" alt="">-->
        <!--<img  src="../../assets/images/login/logo.png" alt="">-->
        <span  class="nor-title">商户后台管理系统</span>
      </div>
      <div class="form-box">

        <h2 class="title">登录</h2>

        <el-form-item class="form-item-box">
          <p class="input-p">用户名：</p>
          <el-input v-model.trim="loginForm.username"></el-input>
        </el-form-item>

        <el-form-item>
          <p class="input-p">密&nbsp&nbsp&nbsp码：</p>
          <el-input v-model="loginForm.password" :type="pwdType"
                    @keyup.enter.native="handleLogin"></el-input>
          <!--<span class="show-pwd" @click="showPwd"><svg-icon icon-class="eye"/></span>-->
        </el-form-item>

        <div class="fogotBtn_box">
          <div>
            <el-button type="text" class="fogotBtn" @click="goFogotPw">&nbsp忘记密码</el-button>
          </div>
        </div>

        <el-form-item style="margin-top: 10px">
          <el-button type="primary" :loading="loading" @click="handleLogin" class="login-btn" style="">登录</el-button>
        </el-form-item>

      </div>
      <div class="null-box"></div>
    </el-form>

    <div class="copyright">
      <p v-show="info.techSupport">技术支持&nbsp{{info.techSupport||''}}</p>
      <p><span v-show="info.icpInfo">网站ICP备案号：{{info.icpInfo||''}}</span>
        <span v-show="info.copyrightInfo">Copyright&nbsp{{info.copyrightInfo||''}}</span></p>
    </div>
  </div>
</template>

<script>
import {getSiteInfo} from '@/modules/login/api/login'
import {returnThumbnailUrl, returnFilesUrl, uploadUrl} from '@/modules/file/api/upload' // 图片操作API

export default {
  name: 'login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      siteInfo: {
        bg: '',
        logo: ''
      },
      bgStyle: {
        background: '',
        backgroundSize: ''
      },
      loading: false,
      pwdType: 'password',
      info: {}
    }
  },
  mounted () {
    // 获取默认信息
    this.getInfo()
    this.getCookie()
  },
  methods: {
    showPwd () {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },

    handleLogin () {
      // 判断复选框是否被勾选 勾选则调用配置cookie方法
      if (this.checked) {
        // 传入账号名，密码，和保存天数3个参数
        this.setCookie(this.loginForm.username, this.loginForm.password, 7)
      } else {
        // 清空Cookie
        this.clearCookie()
      }
      this.loading = true
      this.$store.dispatch('login', this.loginForm).then(() => {
        this.$router.replace({path: '/'})
      }).catch(() => {
        this.loading = false
      })
    },

    /**
       * 获取信息
       */
    getInfo: function () {
      getSiteInfo().then(res => {
        console.log(res)
        let data = res.obj
        // 如果有背景图
        if (data.mctBgmPhoto !== '' && data.mctBgmPhoto != null) {
          this.siteInfo.bg = "url('" + this.getImg(data.mctBgmPhoto) + "') no-repeat"
          this.bgStyle.background = this.siteInfo.bg // 背景图片
          this.bgStyle.backgroundSize = 100 + '%' // 100%大小
        }
        // 如果有LOGO
        if (data.logo !== '' && data.logo != null) {
          this.siteInfo.logo = this.getImg(data.logo)
          sessionStorage.removeItem('logoImg')
          sessionStorage.setItem('logoImg', this.getImg(data.logo))
        }
        this.info = data
      })
    },

    /**
       * 换取图片
       */
    getImg: function (val) {
      let reg = /^[0-9]*$/
      if (reg.test(val)) {
        return returnFilesUrl(val)
      } else {
        let base64 = 'data:image/png;base64,'
        return base64
      }
    },

    /**
       * 忘记密码
       */
    goFogotPw () {
      console.log('go')
      this.$router.push({path: '/fogot'})
    },
    // 设置cookie
    setCookie (c_name, c_pwd, exdays) {
      var exdate = new Date() // 获取时间
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) // 保存的天数
      // 字符串拼接cookie
      window.document.cookie = 'userName' + '=' + c_name + ';path=/;expires=' + exdate.toGMTString()
      window.document.cookie = 'userPwd' + '=' + c_pwd + ';path=/;expires=' + exdate.toGMTString()
    },
    // 读取cookie
    getCookie () {
      if (document.cookie.length > 0) {
        var arr = document.cookie.split('; ') // 这里显示的格式需要切割一下自己可输出看下
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split('=') // 再次切割
          // 判断查找相对应的值
          if (arr2[0] == 'userName') {
            this.loginForm.username = arr2[1] // 保存到保存数据的地方
            this.checked = true
          } else if (arr2[0] == 'userPwd') {
            this.loginForm.password = arr2[1]
          }
        }
      }
    },
    // 清除cookie
    clearCookie: function () {
      this.setCookie('', '', -1) // 修改2值都为空，天数为负1天就好了
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss">
  $bg: #ffffff;
  $light_gray: rgba(204, 204, 204, 1);
  $text_gray: rgb(76, 76, 76);
  .loginform {
    .el-input {
      display: inline-block;
      height: 40px;
      width: 100%;

      input {
        background: transparent;
        border: 1px solid $light_gray;
        width: 300px;
        -webkit-appearance: none;
        border-radius: 0px;
        padding: 12px 5px 12px 15px;
        color: $text_gray;
        height: 40px;

        &:-webkit-autofill {
          -webkit-box-shadow: 0 0 0px 1000px $bg inset !important;
          -webkit-text-fill-color: $text_gray !important;
        }
      }
    }

    .el-form-item {
      border: 1px solid rgba(255, 255, 255, 0.1);
      /*background: rgba(0, 0, 0, 0.1);*/
      border-radius: 5px;
      color: #454545;
    }
  }

</style>

<style rel="stylesheet/scss" lang="scss" scoped>

  $bg: #2d3a4b;
  $dark_gray: #889aa4;
  $light_gray: #eee;

  .fogotBtn_box {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: -20px;

  }

  .login-btn {
    width: 100%;
    border: 0;
    background: rgba(255, 128, 62, 1);
  }

  .login-container {
    /*position: fixed;*/
    height: 100%;
    width: 100%;
    background: url("../../assets/images/login/bg.png") no-repeat center top;
    background-size: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    .header-logo {
      text-align: center;
    }
    .header-bar {
      height: 75px;
      width: 90%;
      border-bottom: 1px solid #efefef;

      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      font-size: 14px;
      color: white;
    }

    .service-info {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }

    .service-info > div {
      margin: 0 10px;
    }

    .wxlogo {
      width: 23px;
      height: 20px;
    }

    .header-logo {
      text-align: center;
    }

    .header-logo-left {
      height: 50px;
      width: auto;
    }

    .service-info span {
      line-height: 23px;
    }

    .wx-box {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
    .wxlogo{
      margin-right: 5px;
    }

    .copyright {
      font-size: 14px;
      color: #bababa;
      text-align: center;
    }

    .header-logo img {
      width: 260px;
      height: auto;
    }

    .form-box {
      margin-top: 50px;
      width: 380px;
      height: 365px;
      padding: 25px 40px 25px 40px;
      background-color: white;
    }

    .null-box {
      width: 380px;
      height: 110px;
      background-color: rgba(255, 128, 62, 0);
    }

    /*.login-form {*/
    /*position: absolute;*/
    /*left: 0;*/
    /*right: 0;*/
    /*!*width: 520px;*!*/
    /*padding: 35px 35px 15px 35px;*/
    /*margin: 120px auto;*/
    /*}*/

    .tips {
      font-size: 14px;
      color: #fff;
      margin-bottom: 10px;

      span {
        &:first-of-type {
          margin-right: 16px;
        }
      }
    }

    .svg-container {
      padding: 6px 5px 6px 15px;
      color: $dark_gray;
      vertical-align: middle;
      width: 30px;
      display: inline-block;

      &_login {
        font-size: 20px;
      }
    }

    .title {
      font-size: 20px;
      font-weight: bold;
      line-height: 25px;
      color: rgba(255, 128, 62, 1);
      opacity: 1;
      margin: 0;
      padding: 0;
    }

    .form-item-box {
      margin-top: 30px;
    }

    .input-p {
      margin: 0;
      padding: 0;
      font-size: 16px;
      font-weight: 400;
      line-height: 20px;
      color: rgba(112, 112, 112, 1);
      opacity: 1;
      margin-bottom: 3px;
    }

    .show-pwd {
      position: absolute;
      right: 10px;
      top: 7px;
      font-size: 16px;
      color: $dark_gray;
      cursor: pointer;
      user-select: none;
    }
  }

  @media (max-width: 1024px) {
    .null-box {
      display: none;
    }
  }

  .nor-title {
    font-size: 24px;
    line-height: 26px;
    color: white;
    font-weight: bold;
  }
</style>



