import request from '@/utils/request'
// 获取订单列表
export function getOrder (pageNumber, pageSize, searchForm) {
  return request({
    url: 'merchant/goods_order',
    method: 'get',
    params: {
      pageNumber: pageNumber,
      pageSize: pageSize,
      storeOrOrder: searchForm.keyWords,
      startTime: searchForm.startTime,
      endTime: searchForm.endTime,
      payType: searchForm.payType,
      industryCategory: 1
    }
  })
}


