<template>
  <div class="alipayMember">
    <el-tabs v-model="activeName"
             @tab-click="handleClick">
      <el-tab-pane label="会员卡设置"
                   name="first">
        <p class="alipayTitle">
          <i class="el-icon-bank-card"></i>会员卡信息设置
        </p>
        <div class="flex">
          <div class="left">
            <div class="flex mt20">
              <p>会员LOGO：</p>
              <!-- <img class="logo" :src="memberDetailData.logoId" /> -->
              <el-upload class="avatar-uploader"
                         :headers="headers"
                         :action="imageUrl"
                         :show-file-list="false"
                         name="file"
                         :on-success="handleAvatarSuccess"
                         :before-upload="beforeAvatarUpload">
                <img v-if="memberDetailData.logoUrl"
                     :src="memberDetailData.logoUrl"
                     class="avatar" />
                <i v-else
                   class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
            <p class="hint">如需修改，请在商户信息中更新，即可同步获取</p>
            <div class="flex mt20">
              <p>会员卡背景：</p>
              <el-color-picker @change="getRGB"
                               color-format="rgb"
                               class="ml20"
                               v-model="memberDetailData.color">
              </el-color-picker>
            </div>
            <div class="flex mt20"
                 style="margin-left:100px">
              <el-radio class="ml20"
                        v-model="radio"
                        label="1">自定义背景
              </el-radio>
              <el-upload class="avatar-uploader"
                         :headers="headers"
                         :action="imageUrl"
                         :show-file-list="false"
                         name="file"
                         :on-success="handleAvatarSuccess2"
                         :before-upload="beforeAvatarUpload2">
                <img v-if="memberDetailData.backgroundUrl"
                     :src="memberDetailData.backgroundUrl"
                     class="avatar" />
                <i v-else
                   class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
            <div class="flex mt20">
              <p>会员卡名称：</p>
              <el-input class="ml20"
                        size="small"
                        v-model="memberDetailData.name"
                        style="width:50%"
                        placeholder="请输入会员卡名称">
              </el-input>
            </div>
            <div class="flex mt20">
              <p>会员卡期限：</p>
              <el-radio class="ml20"
                        v-model="memberDetailData.term"
                        :label="1">永久有效
              </el-radio>
            </div>
            <div class="flex mt20">
              <p>&emsp;商家电话：</p>
              <el-input class="ml20"
                        size="small"
                        style="width:50%"
                        v-model="memberDetailData.phone"
                        placeholder="请输入电话号码">
              </el-input>
            </div>
            <div class="flex mt20">
              <p>&emsp;特权说明：</p>
              <el-input type="textarea"
                        class="ml20"
                        size="small"
                        v-model="memberDetailData.privilegeExplain"
                        style="width:50%"
                        placeholder="请输入特权说明">
              </el-input>
            </div>
            <div class="flex mt20">
              <p>&emsp;领卡方式：</p>
              <el-radio class="ml20"
                        v-model="memberDetailData.acceptWay"
                        :label="1">付款后激活领取
              </el-radio>
              <p v-if="fakaLink"
                 style="font-size:12px;color:#fbbf56"
                 @click="getLink">【获取发卡链接】</p>
            </div>
            <div class="flex mt20">
              <p>&emsp;领取赠送：</p>
              <el-radio class="ml20"
                        @change="getGive"
                        v-model="give"
                        :label="1">赠送
              </el-radio>
              <el-input size="small"
                        :disabled="!give?true:false"
                        v-model="memberDetailData.presentScores"
                        style="width:15%;margin:0 10px ">
              </el-input>
              <span>积分</span>
            </div>
            <div class="flex mt20">
              <p>&emsp;领取赠送：</p>
              <el-radio class="ml20"
                        @change="getGive"
                        v-model="give"
                        :label="0">赠送优惠券
              </el-radio>
              <p class="addquan"
                 @click="addquan">添加优惠券</p>
            </div>
            <p class="remark">注:优惠券过期则不赠送，请核对有效期</p>
          </div>
          <div class="right">
            <!-- <el-card> -->
            <p class="rightTitle">预览</p>
            <!-- <img src="../../../assets/images/member/alipay.png" /> -->
            <div class="cardCon">
              <img class="touxiang"
                   :src="memberDetailData.logoUrl" />
              <p class="pat_title">{{memberDetailData.name}}</p>
              <p class="pat_Con">会员卡号：{{memberDetailData.cardNumber}}</p>
            </div>
            <!-- </el-card> -->
          </div>
        </div>
        <div style="width:100%;text-align: center;">
          <el-button @click="saveUpdateDetail"
                     size="small"
                     type="primary"
                     class="mt20">修改
          </el-button>
        </div>
        <!-- 添加优惠券弹框 -->
        <el-dialog title="创建优惠券"
                   :visible.sync="dialogVisible"
                   width="70%">
          <p class="DTitle">基本信息：</p>
          <div class="flex">
            <p class="DT">卡券名称：</p>
            <el-input class="Dinput"
                      v-model="couponData.name"
                      placeholder="请输入卡券名称"
                      size="small">
            </el-input>
          </div>
          <div class="flex">
            <p class="DT">卡券面值：</p>
            <el-input class="Dinput"
                      v-model="couponData.money"
                      placeholder="请输入卡券面值">
            </el-input>元
          </div>
          <div class="flex">
            <p class="DT">卡券logo：</p>
            <el-upload class="avatar-uploader"
                       :headers="headers"
                       :action="imageUrl"
                       :show-file-list="false"
                       name="file"
                       :on-success="handleAvatarSuccess3"
                       :before-upload="beforeAvatarUpload3">
              <img v-if="couponData.photoUrl"
                   :src="couponData.photoUrl"
                   class="avatar" />
              <i v-else
                 class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
          <div class="flex">
            <p class="DT">有效期：</p>
            <el-radio @change="getvalidityRadio"
                      v-model="validityRadio"
                      :label="0">领取后立即生效
            </el-radio>
            有效期为
            <el-input :disabled="validityRadio?true:false"
                      class="Dinput"
                      v-model="couponData.claimedTime"
                      size="small"
                      style="width:10%">
            </el-input>
            天
          </div>
          <div class="flex"
               style="margin-left:120px">
            <el-radio @change="getvalidityRadio"
                      v-model="validityRadio"
                      :label="1">固定日期
            </el-radio>
            <el-date-picker :disabled="!validityRadio?true:false"
                            v-model="couponData.validTimeStart"
                            size="small"
                            type="date"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            placeholder="选择日期">
            </el-date-picker>
            <span>--</span>
            <el-date-picker :disabled="!validityRadio?true:false"
                            v-model="couponData.validTimeEnd"
                            size="small"
                            type="date"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            placeholder="选择日期">
            </el-date-picker>
          </div>
          <div class="flex">
            <p class="DT">发放总量：</p>
            <el-radio v-model="couponData.sendType"
                      :label="1">不限
            </el-radio>
          </div>
          <div class="flex"
               style="margin-left:120px">
            <el-radio v-model="couponData.sendType"
                      :label="2">设置库存
            </el-radio>
            <el-input :disabled="couponData.sendType==1?true:false"
                      size="small"
                      v-model="couponData.claimUpperLimit"
                      style="width:10%;margin:0 5px">
            </el-input>
            张
          </div>
          <div class="flex">
            <p class="DT">活动有效时间：</p>
            <el-date-picker v-model="couponData.actTimeStart"
                            size="small"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            type="date"
                            placeholder="选择日期">
            </el-date-picker>
            <span>--</span>
            <el-date-picker value-format="yyyy-MM-dd HH:mm:ss"
                            v-model="couponData.actTimeEnd"
                            size="small"
                            type="date"
                            placeholder="选择日期">
            </el-date-picker>
          </div>
          <div class="flex">
            <p class="DT">卡券说明：</p>
            <el-input class="Dinput"
                      v-model="couponData.remark"
                      size="small"
                      type="textarea">
            </el-input>
          </div>
          <p class="DTitle">规则配置：</p>
          <div class="flex">
            <p class="DT">最低消费：</p>
            <el-select @change="getLowest"
                       size="small"
                       v-model="lowSelect"
                       placeholder="请选择">
              <el-option v-for="item in options"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
            <el-input v-if="LowestInput"
                      v-model="couponData.miniExpendLimit"
                      size="small"
                      style="width:5%;margin:0 5px">
            </el-input>
            <span v-if="LowestInput">元</span>
          </div>
          <div class="flex mt20">
            <p class="DT">使用时段：</p>
            <el-select @change="gettimeFrame"
                       size="small"
                       v-model="timeFrame"
                       placeholder="请选择">
              <el-option v-for="item in options2"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
            <div v-if="timeFrameInput"
                 style="margin:0 5px;">
              <el-checkbox-group @change="getWeek"
                                 v-model="useTimeWeekList">
                <el-checkbox v-for="(item,index) in Weekitems"
                             :key="item.id"
                             :label="item.id"
                             :checked="item.checked">{{item.content}}
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
          <div v-if="timeFrameInput"
               class="flex"
               style="margin:0px 0 20px 112px">
            <el-time-select v-model="sTimeDay"
                            @change="getsTimeDay"
                            size="small"
                            format="HH:mm"
                            :picker-options="{  start: '00:00', step: '00:15',end: '23:59' }"
                            placeholder="选择时间">
            </el-time-select>
            <span>--</span>
            <el-time-select v-model="eTimeDay"
                            @change="geteTimeDay"
                            size="small"
                            format="HH:mm"
                            :picker-options="{ start: '00:00', step: '00:15',end: '23:59' }"
                            placeholder="选择时间">
            </el-time-select>
          </div>
          <div class="flex">
            <p class="DT">使用门店：</p>
            <el-select @change="getsemend"
                       size="small"
                       v-model="couponData.storeIds"
                       placeholder="请选择">
              <el-option v-for="item in options3"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
            <p class="addquan"
               v-if="issemend && !shopIdNumber"
               @click="semendian">选择门店</p>
            <p style="margin:0 10px;"
               v-if="shopIdNumber>0">
              已选择{{shopIdNumber}}家，
              <span style="color:#1087C7"
                    @click="semendian">查看或修改</span>
            </p>
          </div>
          <span slot="footer"
                class="dialog-footer">
            <el-button @click="dialogVisible = false"
                       size="small">取 消
            </el-button>
            <el-button type="primary"
                       @click="addcoupon"
                       size="small">确 定
            </el-button>
          </span>
        </el-dialog>
        <el-dialog title="选择门店"
                   :visible.sync="dialogVisible2"
                   width="50%">
          <div>
            <el-input v-model="shopName"
                      style="width:50%"
                      placeholder="输入门店名"
                      size="small">
            </el-input>
            <el-button type="primary"
                       size="small"
                       @click="getshopTable">搜索
            </el-button>
            <el-table ref="multipleTable"
                      :data="shopList"
                      tooltip-effect="dark"
                      style="width: 100%;margin-top:20px"
                      @selection-change="handleSelectionChange">
              <el-table-column type="selection"
                               label="选择"
                               width="55">
              </el-table-column>
              <el-table-column prop="name"
                               label="门店"
                               show-overflow-tooltip>
              </el-table-column>
              <el-table-column prop="address"
                               label="地址"
                               show-overflow-tooltip>
              </el-table-column>
            </el-table>
          </div>
          <span slot="footer"
                class="dialog-footer">
            <el-button size="small"
                       @click="toggleSelection()">取 消
            </el-button>
            <el-button size="small"
                       type="primary"
                       @click="getshopList">确 定
            </el-button>
          </span>
        </el-dialog>
        <!-- 链接弹框 -->
        <el-dialog title="获取链接"
                   :visible.sync="linkDiaog"
                   width="70%">
          <p>{{applyCardUrl}}</p>
        </el-dialog>
      </el-tab-pane>
      <el-tab-pane label="会员支付设置"
                   name="second">
        <div class="memberPaySetting">
          <div class="container">
            <p class="title">充值赠送的金额</p>
            <el-radio v-model="rechargeUse"
                      :label="1">可立即使用
            </el-radio>
            <el-radio v-model="rechargeUse"
                      :label="2">下次消费才可使用
            </el-radio>
          </div>
          <div class="container">
            <p class="title">积分是否可抵扣金额</p>
            <el-radio class="mt"
                      v-model="integralUse"
                      :label="0">不抵扣
            </el-radio>
            <el-radio v-model="integralUse"
                      :label="1">抵扣
            </el-radio>
            <el-input size="small"
                      v-model="integralRule.integralNum"
                      class="input">
            </el-input>
            <span>积分</span>
            <span>抵扣</span>
            <el-input size="small"
                      v-model="integralRule.deduction"
                      class="input">
            </el-input>
            <span>元</span>
            <span>最多抵扣</span>
            <el-input size="small"
                      v-model="integralRule.deductionMax"
                      class="input">
            </el-input>
            <span>积分</span>
            <br />
          </div>
          <div class="container">
            <p class="title">折扣/优惠券/满减优惠是否同享</p>
            <el-checkbox v-model="simultaneouslyUse1"
                         :true-label="1"
                         :false-label="2"
                         @change>展示满减
            </el-checkbox>
            <el-checkbox v-model="simultaneouslyUse2"
                         :true-label="1"
                         :false-label="2"
                         @change>展示优惠
            </el-checkbox>
          </div>
          <div class="container mt tip">
            <p>提示：</p>
            <p>1.单独选择满减/优惠券时，设备上仅展示满减或优惠券信息；</p>
            <p>2.满减和优惠券都选择时，设备上展示两样优惠信息，但是用户只能选择一个优惠；</p>
            <p>3.满减和优惠券都不选择时，有优惠信息也不展示</p>
          </div>
          <el-button class="operate-btn"
                     type="primary"
                     size="small"
                     @click="saveMembersPay">保存
          </el-button>
        </div>
      </el-tab-pane>
      <el-tab-pane label="会员等级"
                   name="third">
        <div class="flex2">
          <div class="box"
               v-for="(item,index) in GradeList"
               :key="index">
            <p>
              <span class="Btitle">{{item.name}}</span>
              <span class="discount">{{item.discount}}折</span>
            </p>
            <p style="margin:10px 0">
              <span>条件：</span>
              <span class="huise">充值到{{item.memberLimitAmount}}元</span>
            </p>
            <p>
              <span>权益：</span>
              <span class="huise">享受消费折扣{{item.memberLimitAmount}}折；
                送金额{{item.giftMoney}}元；
                送积分{{item.giftScore}}积分；</span>
            </p>
            <p class="seIcon">
              <span @click="addGrade(1,item)">
                <i class="el-icon-edit"></i>
              </span>
              <span @click="delMemberGrade(item)">
                <i class="el-icon-delete"></i>
              </span>
            </p>
          </div>
          <div class="add"
               @click="addGrade(0)">
            <p>+添加等级</p>
          </div>
        </div>
        <el-dialog title="添加等级"
                   :visible.sync="dialogGrade"
                   width="60%">
          <div class="grade">
            <p>等级名称：</p>
            <el-input v-model="addGradeData.name"
                      size="small"
                      style="width:50%">
            </el-input>
          </div>
          <div class="grade">
            <p>等级图片：</p>
            <el-color-picker color-format="rgb"
                             v-model="addGradeData.color">
            </el-color-picker>
            <el-radio class="ml10"
                      v-model="radio">自定义背景
            </el-radio>
            <el-upload class="avatar-uploader"
                       :headers="headers"
                       :action="imageUrl"
                       :show-file-list="false"
                       name="file"
                       :on-success="handleAvatarSuccess4"
                       :before-upload="beforeAvatarUpload4">
              <img v-if="addGradeData.background"
                   :src="addGradeData.background"
                   class="avatar" />
              <i v-else
                 class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
          <div class="grade">
            <p>等级门槛：</p>充值到：
            <el-input size="small"
                      v-model="addGradeData.memberLimitAmount"
                      style="width:30%">
            </el-input>
            元
          </div>
          <div class="grade">
            <p>消费权益：</p>
            <el-radio v-model="addGradeData.discountType"
                      :label="1">享受消费折扣
            </el-radio>
            <el-input size="small"
                      v-model="addGradeData.discount"
                      style="width:30%;margin-right:10px;">
            </el-input>
            折
            <el-radio v-model="addGradeData.discountType"
                      :label="2"
                      class="ml10">不打折
            </el-radio>
          </div>
          <div class="grade">
            <p>升级礼包：</p>
            <el-radio v-model="addGradeData.giftType"
                      :label="1">送积分
            </el-radio>
            <el-input size="small"
                      v-model="addGradeData.giftScore"
                      style="width:30%;margin-right:10px">
            </el-input>
            分
            <el-radio v-model="addGradeData.giftType"
                      :label="2"
                      class="ml10">送金额
            </el-radio>
            <el-input v-model="addGradeData.giftMoney"
                      size="small"
                      style="width:30%;margin-right:10px">
            </el-input>
            元
          </div>
          <div class="grade">
            <p>特权说明：</p>
            <el-input v-model="addGradeData.rightExplain"
                      type="textarea"
                      size="small"
                      style="width:50%">
            </el-input>
          </div>
          <span slot="footer"
                class="dialog-footer">
            <el-button size="small"
                       @click="dialogGrade = false">取 消
            </el-button>
            <el-button size="small"
                       type="primary"
                       @click="addMemberGrade">保存
            </el-button>
          </span>
        </el-dialog>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import {
  memberDetail,
  UpdateDetail,
  setTemplateForm,
  applyActivateUrl,
  coupon,
  shopTable,
  MembersPay,
  saveMembersPay,
  MemberGrade,
  addMemberGrade,
  delMemberGrade
} from '../api/alipayMember'
import { url } from '@/utils/request'
export default {
  data () {
    return {
      sTimeDay: '',
      shopIdNumber: 0,
      timeFrame: 0,
      eTimeDay: '',
      activeName: 'first',
      color1: '#1087C7',
      tableData: [],
      imageUrl: url + '/member/sem/upload/imageOrVideo',
      value1: '',
      file: '',
      radio: '1',
      photo: '',
      shopName: '',
      headers: {
        authorized: sessionStorage.token
      },
      checked: '',
      dialogVisible: false,
      dialogVisible2: false,
      options: [
        {
          value: 0,
          label: '不限制'
        },
        {
          value: 1,
          label: '设置最低消费'
        }
      ],
      options2: [
        {
          value: 0,
          label: '不限制'
        },
        {
          value: 1,
          label: '设置使用时段'
        }
      ],
      options3: [
        {
          value: 0,
          label: '全部门店'
        },
        {
          value: 1,
          label: '指定门店'
        }
      ],
      value: '',
      give: 0,
      dialogGrade: false,
      fakaLink: false,
      linkDiaog: false,
      applyCardUrl: '',
      backgroundUrl: '',
      logoUrl: '',
      useTimeWeekList: [],
      couponData: {
        name: '',
        money: '',
        photoUrl: '',
        claimedTime: '',
        validTimeStart: '',
        validTimeEnd: '',
        sendType: 1,
        claimUpperLimit: '',
        actTimeStart: '',
        actTimeEnd: '',
        remark: '',
        miniExpendLimit: 0,
        useTimeWeek: [],
        useTimeDay: '',
        storeIds: 0,
        logoId: '',
        tempateId: '',
        merchantId: '',
        privilegeExplain: '',
        backgroundUrl: '',
        backgroudId: '',
        type: 2,
        cardType: 2
      },
      memberDetailData: {
        acceptWay: 1,
        backgroudId: '',
        backgroundUrl: '',
        bgColor: '',
        color: '',
        couponId: '',
        logoId: '',
        logoUrl: '',
        merchantId: '',
        name: '',
        phone: '',
        presentScores: 0,
        privilegeExplain: '',
        tempateId: '',
        term: 1,
        tip: '',
        photoUrl: '',
        userID: sessionStorage.getItem('userId')
      },
      shopList: [],
      shopId: '',
      issemend: false,
      LowestInput: false,
      timeFrameInput: false,
      quanUrl: '',
      validityRadio: 0,
      lowSelect: 0,
      addGradeData: {
        name: '',
        color: '',
        background: '',
        memberLimitAmount: '',
        discountType: 2,
        giftType: 0,
        discount: '',
        giftScore: '',
        giftMoney: '',
        rightExplain: '',
        logoId: '',
        privilegeExplain: ''
      },
      Weekitems: [
        { id: 1, content: '周一' },
        { id: 2, content: '周二' },
        { id: 3, content: '周三' },
        { id: 4, content: '周四' },
        { id: 5, content: '周五' },
        { id: 6, content: '周六' },
        { id: 7, content: '周日' }
      ],
      rechargeUse: 1,
      integralUse: 0,
      simultaneouslyUse1: '',
       simultaneouslyUse2: '',
      gradebackground: '',
      GradeList: [],
      integralRule: { integralNum: 0, deduction: 0, deductionMax: 0 }
    }
  },
  created () {
    // this.getmemberCard();
    this.getshopTable()
    this.getMembersPay()
    this.getMemberGrade()
  },
  methods: {
    // 确定选择门店
    getshopList () {
      let shopID = this.shopId.split(',')
      this.shopIdNumber = shopID.length
      this.dialogVisible2 = false
    },
    // 取消选择门店
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
        this.shopIdNumber = 0
        console.log(this.shopIdNumber, 'shopid')
      }
      this.dialogVisible2 = false
    },
    delMemberGrade (item) {
      let ids = item.id
      delMemberGrade(ids)
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.getMemberGrade()
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 添加等级
    addMemberGrade () {
      if (this.gradebackground) {
        this.addGradeData.background = this.gradebackground
      }
      this.addGradeData.privilegeExplain = this.memberDetailData.privilegeExplain
      this.addGradeData.logoId = this.memberDetailData.logoId
      this.addGradeData.backgroudId = this.memberDetailData.backgroudId
      addMemberGrade(this.addGradeData)
        .then(response => {
          this.$message({
            message: '操作成功',
            type: 'success'
          })
          this.dialogGrade = false
          this.getMemberGrade()
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取会员等级
    getMemberGrade () {
      MemberGrade()
        .then(response => {
          this.GradeList = response.obj.content
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 保存会员支付设置
    saveMembersPay () {
      console.log(this.simultaneouslyUse1,this.simultaneouslyUse2)
      this.payData.integralRule = JSON.stringify(this.integralRule)
      if(this.simultaneouslyUse1 == 1 && this.simultaneouslyUse2 == 1) {
         this.payData.simultaneouslyUse = 3
      }else if(this.simultaneouslyUse1 == 1){
        this.payData.simultaneouslyUse = 1
      }else if(this.simultaneouslyUse2 == 1) {
        this.payData.simultaneouslyUse = 2
      }else {
        this.payData.simultaneouslyUse = 0
      }
      saveMembersPay(this.payData)
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.getMembersPay()
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取会员支付设置详情
    getMembersPay () {
      MembersPay()
        .then(response => {
          this.payData = response.obj
          console.log(this.payData.simultaneouslyUse)
           if(this.payData.simultaneouslyUse == 3) {
               this.simultaneouslyUse1 = 1
               this.simultaneouslyUse2 = 1
            }else if(this.payData.simultaneouslyUse == 1){
               this.simultaneouslyUse1 = 1
               this.simultaneouslyUse2 = 2
            }else if(this.payData.simultaneouslyUse == 2) {
              this.simultaneouslyUse1 = 2
              this.simultaneouslyUse2 = 1
            }else {
              console.log(22222)
              this.simultaneouslyUse1 = 2
              this.simultaneouslyUse2 = 2
            }
          if (this.payData.integralRule) {
            this.integralRule = JSON.parse(this.payData.integralRule)
          }
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取validityRadio
    getvalidityRadio (val) {
      console.log(val, 'getvalidityRadio')
    },
    // 获取指定门店
    getsemend (val) {
      if (val) {
        this.issemend = true
      } else {
        this.issemend = false
      }
    },
    // 使用开始时段
    getsTimeDay (val) {
      console.log(val, 'stime')
    },
    // 使用结束时段
    geteTimeDay (val) {
      console.log(val, 'etime')
    },
    // 获取星期几
    getWeek (val) {
      this.couponData.useTimeWeek = val.join(',')
    },
    // 获取最低消费
    getLowest (val) {
      if (val) {
        this.LowestInput = true
      } else {
        this.LowestInput = false
      }
    },
    // 获取时段
    gettimeFrame (val) {
      if (val) {
        this.timeFrameInput = true
      } else {
        this.timeFrameInput = false
      }
    },
    // 多选
    handleSelectionChange (val) {
      this.shopId = ''
      for (var i = 0; i < val.length; i++) {
        if (!this.shopId) {
          this.shopId = val[i].id
        } else {
          this.shopId += ',' + val[i].id
        }
        console.log(this.shopId)
      }
    },
    getshopTable () {
      shopTable(this.shopName)
        .then(response => {
          this.shopList = response.obj.content
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    addcoupon () {
      if (this.quanUrl) {
        this.couponData.photoUrl = this.quanUrl
      }
      this.couponData.tempateId = this.memberDetailData.tempateId
      this.couponData.merchantId = this.memberDetailData.merchantId
      this.couponData.privilegeExplain = this.memberDetailData.privilegeExplain
      this.couponData.logoId = this.memberDetailData.logoId
      this.couponData.backgroundUrl = this.memberDetailData.backgroundUrl
      this.couponData.backgroudId = this.memberDetailData.backgroudId
      this.couponData.useTimeDay = this.sTimeDay + '-' + this.eTimeDay
      this.couponData.type = 2
      coupon(this.couponData)
        .then(response => {
          this.$message({
            message: response.msg,
            type: 'success'
          })
          this.dialogVisible = false
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    handleAvatarSuccess (res, file) {
      this.memberDetailData.logoId = res.obj.imageId
      this.logoUrl = res.obj.imageUrl
      this.memberDetailData.logoUrl = URL.createObjectURL(file.raw)
    },
    handleAvatarSuccess2 (res, file) {
      this.memberDetailData.backgroudId = res.obj.imageId
      this.backgroundUrl = res.obj.imageUrl
      this.memberDetailData.backgroundUrl = URL.createObjectURL(file.raw)
    },
    handleAvatarSuccess3 (res, file) {
      this.quanUrl = res.obj.imageUrl
      this.couponData.photoUrl = URL.createObjectURL(file.raw)
    },
    handleAvatarSuccess4 (res, file) {
      this.gradebackground = res.obj.imageUrl
      this.addGradeData.background = URL.createObjectURL(file.raw)
    },
    getGive (val) {
      if (val) {
        delete this.memberDetailData.couponId
      } else {
        delete this.memberDetailData.presentScores
      }
      console.log(this.memberDetailData, 'memberDetailData')
    },
    saveUpdateDetail () {
      if (this.logoUrl) {
        this.memberDetailData.logoUrl = this.logoUrl
      }
      if (this.backgroundUrl) {
        this.memberDetailData.backgroundUrl = this.backgroundUrl
      }
      this.memberDetailData.merchantId = this.payData.merchantId
      UpdateDetail(this.memberDetailData)
        .then(response => {
          this.getmemberCard()
          this.$message({
            message: response.msg,
            type: 'success'
          })

          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    getRGB (val) {
      console.log(val)
    },
    getLink () {
      this.linkDiaog = true
    },
    to_setTemplateForm () {
      setTemplateForm(this.memberDetailData.tempateId)
        .then(response => {
          this.fakaLink = true
          this.to_applyActivateUrl()
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    to_applyActivateUrl () {
      applyActivateUrl(this.memberDetailData.tempateId)
        .then(response => {
          this.applyCardUrl = response.obj.applyCardUrl
          console.log(this.applyCardUrl, 'url')
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    getmemberCard () {
      memberDetail()
        .then(response => {
          this.memberDetailData = response.obj
          if (this.memberDetailData.presentScores) {
            console.log(this.memberDetailData.presentScores, '234234')
            this.give = 1
          } else {
            this.give = 0
          }
          this.to_setTemplateForm()
          setTimeout(() => {
            this.loading = false
          }, 500)
        })
        .catch(() => {
          this.loading = false
        })
    },
    addGrade (index, item) {
      this.dialogGrade = true
      if (!index) {
        this.addGradeData = {
          name: '',
          color: '',
          background: '',
          memberLimitAmount: '',
          discountType: 2,
          giftType: 0,
          discount: '',
          giftScore: '',
          giftMoney: '',
          rightExplain: '',
          logoId: '',
          privilegeExplain: ''
        }
      } else {
        this.addGradeData = item
        console.log(item, 'item')
      }
    },
    semendian () {
      this.dialogVisible2 = true
    },
    handleClick (tab, event) {
      console.log(tab, event)
    },
    addquan () {
      this.dialogVisible = true
      this.couponData = {
        name: '',
        money: '',
        photoUrl: '',
        claimedTime: '',
        validTimeStart: '',
        validTimeEnd: '',
        sendType: 1,
        claimUpperLimit: '',
        actTimeStart: '',
        actTimeEnd: '',
        remark: '',
        miniExpendLimit: 0,
        useTimeWeek: [],
        useTimeDay: '',
        storeIds: 0,
        logoId: '',
        tempateId: '',
        merchantId: '',
        privilegeExplain: '',
        backgroundUrl: '',
        backgroudId: '',
        type: 2,
        cardType: 2
      }
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    beforeAvatarUpload2 (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    beforeAvatarUpload3 (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    beforeAvatarUpload4 (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    }
  }
}
</script>

<style scoped>
.ml10 {
  margin-left: 10px;
}
.grade {
  display: flex;
  align-items: center;
  margin: 20px 0;
}
.grade p {
  width: 100px;
}
.box {
  width: calc(25% - 20px);
  border: 1px dashed #bbb;
  border-radius: 5px;
  padding: 20px;
  padding-bottom: 0px;
  margin-top: 20px;
  height: 220px;
  display: flex;
  flex-direction: column;
  align-content: flex-end;
  margin-right: 20px;
}
.add {
  text-align: center;
  color: #1087C7;
  width: calc(25% - 20px);
  border: 1px dashed #1087C7;
  border-radius: 5px;
  padding: 20px;
  margin-top: 20px;
  height: 180px;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.seIcon {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 50px;
}
.seIcon span {
  width: 50%;
  text-align: center;
  font-size: 20px;
  margin-bottom: 5px;
}
.seIcon span:first-of-type {
  border-right: 1px solid #ccc;
}
.flex2 {
  display: flex;
  flex-wrap: wrap;
}
.Btitle {
  font-weight: bold;
}
.huise {
  color: #a2a2a2;
}
.discount {
  border-radius: 10px;
  background: #1087C7;
  display: inline-block;
  color: #fff;
  padding: 3px 5px;
}

.tip {
  font-size: 14px;
  line-height: 24px;
}
.input {
  width: 10%;
}
span {
  font-size: 14px;
}
.operate-btn {
  margin-top: 50px;
}
.memberPaySetting {
  padding: 0 100px;
}
.title {
  font-weight: 700;
  font-size: 16px;
  margin: 50px 0 30px;
}
.avatar-uploader {
  width: 100px;
  height: 100px;
  border: 1px dashed #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
}
.avatar {
  width: 98%;
}
.Dinput {
  width: 30%;
  margin: 10px 5px;
}
.DT {
  width: 120px;
  text-align: right;
  line-height: 50px;
}
.DTitle {
  margin: 20px 0;
  padding-left: 10px;
  font-weight: bold;
  border-left: 2px solid #333;
}
.addquan {
  border: 1px solid #1087C7;
  padding: 5px 10px;
  color: #1087C7;
  border-radius: 5px;
  font-size: 14px;
  margin: 0 5px;
}
.remark {
  color: #ccc;
  font-size: 14px;
  margin-top: 10px;
  margin-left: 120px;
}
.left {
  width: 50%;
}
.pat_Con {
  color: #fff;
  position: absolute;
  top: 270px;
  left: 20px;
  z-index: 99;
}
.touxiang {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  top: 130px;
  left: 20px;
  z-index: 99;
}
.pat_title {
  color: #fff;
  position: absolute;
  top: 130px;
  left: 80px;
  z-index: 99;
}
.right {
  position: relative;
}

.rightTitle {
  padding: 20px;
  text-align: left;
  border: 1px solid #eee;
}

p {
  font-size: 16px;
}
.alipayTitle {
  margin-bottom: 20px;
  font-weight: bold;
}
.ml20 {
  margin-left: 20px;
}
.hint {
  font-size: 12px;
  color: #b9b9b9;
  margin-left: 80px;
  margin-top: 10px;
}
.logo {
  width: 130px;
  height: 130px;
  border: 1px solid #ccc;
  margin-left: 10px;
}
.mt20 {
  margin-top: 20px;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.alipayMember {
  padding: 20px;
}
</style>



