import request from '@/utils/request'
import qs from 'qs'
// 获取商品销量列表
export function salesList(startTime, endTime,salesNumType,salesCountNumType,tradingVolumeType,pageNum,pageSize,) {
  return request({
    url: '/merchant/goods_order/get_goods_order_sales',
    method: 'get',
    params: {
      startTime, endTime,
      salesNumType,salesCountNumType,tradingVolumeType,
      pageNum,pageSize,

    }
  })
}

// 获取图表
export function salesMap(goodsName,startTime,endTime,type) {
  return request({
    url: '/merchant/goods_order/statistics',
    method: 'get',
    params: {
      goodsName,startTime,endTime,type
    }
  })
}




