<template>
  <div class="app-container" v-loading="loading">
    <div class="filter-container">
      <el-input style="width: 200px"
                class="filter-item"
                placeholder="姓名"
                v-model="queryList.name" @keyup.enter.native="search" size="small"></el-input>
      <el-input style="width: 200px"
                type="tel"
                class="filter-item"
                placeholder="手机"
                v-model="queryList.phone" @keyup.enter.native="search" size="small"></el-input>
      <el-button type="primary" icon="el-icon-search" class="filter-item" @click="search" size="small">搜索</el-button>
      <el-button class="filter-item" @click="reset" size="small">重置</el-button>
    </div>
    <div class="action-container">
      <el-button type="primary" icon="el-icon-plus" size="small" @click="openAddDialog" style="margin-right:20px;">添加员工</el-button>
      <el-button icon="el-icon-refresh" size="small" @click="refresh">刷新</el-button>
    </div>
    <el-table :data="tableData"
              style="width: 100%"
              @selection-change="handleSelectionChange">
      <el-table-column type="selection"
                       width="55">
      </el-table-column>
      <el-table-column prop="name"
                       label="姓名">
      </el-table-column>
      <el-table-column prop="username"
                       label="用户名">
      </el-table-column>
      <el-table-column prop="phone"
                       label="手机">
      </el-table-column>
      <el-table-column prop="sex"
                       label="性别">
      <template slot-scope="scope">
        <template v-if="scope.row.sex===1">男</template>
        <template v-else-if="scope.row.sex===2">女</template>
      </template>
      </el-table-column>
      <el-table-column prop="storeName"
                       label="所属门店">
      </el-table-column>
      <el-table-column prop="userType" 
                       label="角色">
      <template slot-scope="scope">
        <template v-if="scope.row.userType==='1'">商户</template>
        <template v-else-if="scope.row.userType==='2'">店长</template>
        <template v-else-if="scope.row.userType==='3'">店员</template>
      </template>
      </el-table-column>
      <el-table-column prop="status" 
                       label="状态">
      <template slot-scope="scope">
        <template v-if="scope.row.status===1">启用</template>
        <template v-else-if="scope.row.status===2">禁用</template>
      </template>
      </el-table-column>
      <el-table-column label="操作"
                       width="270">
        <template slot-scope="scope">
          <el-button
            type="text"
            @click="openEditDialog(scope.row)">编辑
          </el-button>
          <el-button
            type="text"
            @click="resetPasswordDialogOpen(scope.row)">重置密码
          </el-button>
          <el-button type="text" @click="rooterDeatil(scope.row)">详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination :total-elements="totalElements"
                :change-callback="getUserList"
                ref="page"></pagination>
    <el-dialog title="添加员工"
               :visible.sync="addDialog"
               v-if="addDialog"
               :before-close="closeAddDialog">
      <el-form :model="form" :label-width="formLabelWidth"  :rules="formRules" ref="formRef">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username" clearable></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="form.password" clearable></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name" clearable></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="phone">
          <el-input v-model="form.phone" type="tel" clearable></el-input>
        </el-form-item>
        <el-form-item label="选择性别" prop="sex">
          <el-select v-model="form.sex"
                     collapse-tags
                     clearable
                     placeholder="请选择">
            <el-option v-for="item in sexOption"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="userType=='1'" label="选择门店" prop="storeId">
          <el-select v-model="form.storeId"
                     collapse-tags
                     clearable
                     placeholder="请选择">
            <el-option v-for="item in storeOptions"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="userType=='1'" label="选择角色" prop="userType">
          <el-select v-model="form.userType"
                     collapse-tags
                     clearable
                     placeholder="请选择">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择状态" prop="status">
          <el-select v-model="form.status"
                     collapse-tags
                     clearable
                     placeholder="请选择">
            <el-option v-for="item in statusOption"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="图片" prop="photoId">
          <el-upload class="logo-uploader"
                     :action="url"
                     :data="filesData"
                     name="files"
                     :show-file-list="false"
                     :on-success="createLogoSuccess"
                     :on-error="handleLogoFailed"
                     :before-upload="beforeLogoUpload"
                     :headers="headers">
            <img v-if="createdSrc" :src="createdSrc" class="logo">
            <i class="el-icon-plus logo-uploader-icon" v-else></i>
          </el-upload>
          <el-input v-model="form.photoId" style="display: none;"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeAddDialog">取 消</el-button>
        <el-button type="primary" @click="addUser('formRef')" :loading="btnLoading">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="编辑用户"
               :visible.sync="editDialog"
               :before-close="closeEditDialog">
      <el-form :model="currentRow" :label-width="formLabelWidth"  :rules="formRules" ref="formRefEdit">
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
          <el-input v-model="currentRow.name"></el-input>
        </el-form-item>
        <el-form-item label="手机" :label-width="formLabelWidth" prop="phone">
          <el-input v-model="currentRow.phone" type="tel"></el-input>
        </el-form-item>
        <el-form-item label="选择性别" :label-width="formLabelWidth" prop="sex">
          <el-select v-model="currentRow.sex"
                     collapse-tags
                     placeholder="请选择">
            <el-option v-for="item in sexOption"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="userType=='1'" label="选择门店" :label-width="formLabelWidth" prop="storeId">
          <el-select v-model="currentRow.storeId"
                     collapse-tags
                     placeholder="请选择">
            <el-option v-for="item in storeOptions"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-if="userType=='1'" label="选择角色" :label-width="formLabelWidth" prop="userType">
          <el-select v-model="currentRow.userType"
                     collapse-tags
                     placeholder="请选择">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择状态" :label-width="formLabelWidth" prop="status">
          <el-select v-model="currentRow.status"
                     collapse-tags
                     placeholder="请选择">
            <el-option v-for="item in statusOption"
                       :key="item.value"
                       :label="item.name"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="图片" :label-width="formLabelWidth" prop="photoId">
          <el-upload class="logo-uploader"
                     :action="url"
                     :data="filesData"
                     name="files"
                     :show-file-list="false"
                     :on-success="editLogoSuccess"
                     :on-error="handleLogoFailed"
                     :before-upload="beforeLogoUpload"
                     :headers="headers">
            <img v-if="createdSrc" :src="createdSrc" class="logo">
            <i v-else class="el-icon-plus logo-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeEditDialog">取 消</el-button>
        <el-button type="primary" @click="editUser('formRefEdit')" :loading="btnLoading">确 定</el-button>
      </div>
    </el-dialog>

    <!--重置密码对话框 -->
    <el-dialog title="重置业务员密码"
               :visible.sync="resetPasswordDialog"
               width="30%"
               :before-close="resetPasswordDialogClose">
      <el-form :model="resetForm" :rules="rules" ref="resetForm" label-width="100px">
        <el-form-item label="新密码" prop="password">
          <el-input v-model="resetForm.password" type="password"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetPasswordDialogClose">取 消</el-button>
        <el-button type="primary" @click="saveNewPssword">提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {getUser, addUser, editUser, resetPassword, storeListByMctId} from '../api/user'
import {returnThumbnailUrl, uploadUrl} from '@/modules/file/api/upload'
import pagination from '@/components/pagination/index'
import {getExcel} from '@/utils/exportExcel'
import {url} from '@/utils/request'

export default {
  components: {pagination},
  name: 'user',
  data () {
    const validPhone = (rule, value, callback, source, options) => {
      let errors = []
      let regRule = /^1\d{10}$/
      let valid = regRule.test(value)
      if (!valid) {
        errors.push('请输入有效手机号')
      }
      callback(errors)
    }
    return {
      filesData: {
        module: 'user'
      }, // 携带参数
      createdSrc: '',
      editSrc: '',
      headers: {
        authorized: sessionStorage.token
      },
      url: uploadUrl + '/user',
      resetPasswordDialog: false, // 重置密码对话框
      resetPWid: '',
      resetForm: {
        password: ''
      },
      formRules: {
        username: [
          {required: true, message: '请输入用户名'},
          {min: 1, max: 20, message: '长度在 1 到 20 个字符'}
        ],
        password: [
          {required: true, message: '请输入密码'},
          {min: 6, max: 20, message: '长度在 6 到 20 个字符'}
        ],
        name: [
          {required: true, message: '请输入姓名'},
          {min: 1, max: 20, message: '长度在 1 到 20 个字符'}
        ],
        phone: [
          {required: true, message: '请输入手机号'},
          { required: true, validator: validPhone }
        ],
        sex: [
          {required: true, message: '请选择性别'}
        ],
        storeId: [
          {required: true, message: '请选择门店'}
        ],
        userType: [
          {required: true, message: '请选择角色'}
        ],
        status: [
          {required: true, message: '请选择状态'}
        ],
        photoId: [
          {required: true, message: '请选择图片'}
        ]
      },
      rules: {
        password: [
          {required: true, message: '请输入新的密码', trigger: 'blur'},
          {min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur'}
        ]
      },
      loading: false,
      btnLoading: false,
      multipleSelection: [], // 选中数据-多选
      totalElements: 0, // 数据条数
      queryList: {
        name: '',
        phone: ''
      }, // 搜素数据
      tableData: [], // 表格数据
      addDialog: false, // 添加角色模态框
      form: {
        username: '',
        password: '',
        name: '',
        phone: '',
        sex: 1,
        storeId: '',
        status: 1,
        userType: '', // 下拉框多选值
        photoId: ''
      }, // 添加用户数据
      formLabelWidth: '120px',
      options: [{value: '2', name: '店长'}, {value: '3', name: '店员'}],
      sexOption: [{value: 1, name: '男'}, {value: 2, name: '女'}],
      statusOption: [{value: 1, name: '启用'}, {value: 2, name: '注销'}],
      storeOptions: [],
      editDialog: false, // 编辑角色模态框
      currentRow: {
        id: '',
        name: '',
        phone: '',
        sex: '',
        storeId: '',
        status: '',
        photoId: '',
        userType: '' // 下拉框多选值
      }, // 当前表格对象
      userType: '1'
    }
  },
  mounted () {
    this.getUserList()
  },
  methods: {
    //导出的方法
    exportExcel() {
      this.$confirm('是否导出用户列表?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const tHeaders = ['姓名', '用户名','手机', '所属门店','性别', '状态', '角色']
        const filterVals = ['name', 'username', 'phone', 'storeName', 'sex', 'status', 'userType']
        const datas = this.tableData
        console.log(datas)
        const jsons = this.formatJson(filterVals, datas);
        getExcel(tHeaders, jsons)
      }) 
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    handleDelete (row) {
      this.multipleSelection.push(row)
      this.deleteRoles()
    },
    /*
       * 获取用户列表
       * */
    getUserList (number, pageSize) {
      this.loading = true
      getUser(number, pageSize, this.queryList).then(response => {
        let data = response.obj
        console.log(data.content)
        this.totalElements = data.totalElements
        data.content.forEach(element => {
          // switch (element.sex) {
          //   case 1:
          //     element.sex = '男'
          //     break;
          //   case 2:
          //     element.sex = '女'
          //     break;
          // }
          // switch (parseInt(element.userType)) {
          //   case 1:
          //     element.userType = '商户'
          //     break;
          //   case 2:
          //     element.userType = '店长'
          //     break;
          //   case 3:
          //     element.userType = '店员'
          //     break;
          // }
          // switch (element.status) {
          //   case 1:
          //     element.status = '启用'
          //     break;
          //   case 2:
          //     element.status = '禁用'
          //     break;
          // }
        });
        this.tableData = data.content
        setTimeout(() => {
          this.loading = false
        }, 500)
      }).catch(() => {
        this.loading = false
      })
    },

    // 重置密码
    resetPasswordDialogOpen: function (data) {
      // 获取被修改的ID
      this.resetPWid = data.id
      this.resetPasswordDialog = true
    },
    resetPasswordDialogClose: function () {
      this.resetForm.password = ''
      this.resetPasswordDialog = false
    },
    saveNewPssword: function () {
      console.log('表单密码:' + this.resetForm.password)
      this.$refs.resetForm.validate().then(valid => {
        if (valid) {
          // 保存密码
          resetPassword(this.resetPWid, this.resetForm.password).then(response => {
            this.$message({
              message: response.msg,
              type: 'success'
            })
            this.resetForm.password = ''
            this.resetPasswordDialog = false
          }).catch(() => {
            this.loading = false
          })
        }
      })
    },

    /*
       * 添加用户
       * */
    /*
       * 打开添加用户模态框
       * */
    openAddDialog () {
      let userType = sessionStorage.getItem('userType')
      this.userType = userType
      this.getStores()
      this.createdSrc = ''
      this.addDialog = true
    },
    /*
       * 关闭添加用户模态框
       * */
    closeAddDialog () {
      this.addDialog = false
      this.form = {
        username: '',
        password: '',
        name: '',
        phone: '',
        sex: 1,
        storeId: '',
        status: 1,
        userType: '', // 下拉框多选值
        photoId: ''
      }
      this.createdSrc = ''
    },
    /*
       * 添加用户
       * */
    addUser (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          this.btnLoading = true
          addUser(this.form).then(response => {
            this.$message({
              message: response.msg,
              type: 'success'
            })
            this.btnLoading = false
            this.closeAddDialog()
            this.$refs.page.refresh()
          }).catch(() => {
            this.btnLoading = false
            this.loading = false
          })
        } else {
          return false
        }
      })
    },
    /*
       * 编辑用户
       * */
    /*
       * 打开编辑用户模态框
       * */
    openEditDialog (row) {
      console.log(row)
      this.currentRow.id = row.id
      this.currentRow.phone = row.phone
      this.currentRow.name = row.name
      this.currentRow.sex = row.sex
      this.currentRow.storeId = row.storeId
      this.currentRow.status = row.status
      this.currentRow.photoId = row.photoId
      this.currentRow.userType = row.userType
      this.getStores()
      this.createdSrc = this.currentRow.photoId ? this.getPicUrl(this.currentRow.photoId) : ''
      this.editDialog = true
    },

    getPicUrl (picid) {
      console.log(returnThumbnailUrl(picid))
      return returnThumbnailUrl(picid)
    },
    /*
       * 关闭编辑用户模态框
       * */
    closeEditDialog (row) {
      this.currentRow = {
        id: '',
        name: '',
        phone: '',
        sex: '1',
        storeId: '',
        status: '1',
        userType: '' // 下拉框多选值
      }
      this.editDialog = false
    },
    /*
       * 编辑用户
       * */
    editUser (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          this.btnLoading = true
          editUser(this.currentRow).then(response => {
            this.$message({
              message: response.msg,
              type: 'success'
            })
            this.btnLoading = false
            this.closeEditDialog()
            this.$refs.page.refresh()
          }).catch(() => {
            this.btnLoading = false
            this.loading = false
          })
        } else {
          return false
        }
      })
    },

    /*
    * 获取门店列表
    * */
    getStores () {
      if (!this.storeOptions.length) {
        storeListByMctId().then(response => {
          this.storeOptions = response.obj
        }).catch(() => {
        })
      }
    },

    // 跳转详情页
    rooterDeatil (data) {
      // console.log(data)
      // query发送ID到详情
      let id = data.id
      this.$router.push({path: '/user/views/userDetail', query: {id: id}})
    },

    // 创建模态框 上传
    createLogoSuccess (response, file) {
      // 保存图片ID
      this.photoId = response.obj
      console.log(response)
      this.$message({
        message: response.msg,
        type: 'success'
      })
      this.form.photoId = response.obj
      this.createdSrc = returnThumbnailUrl(response.obj)
    },
    // 编辑模态框 上传
    editLogoSuccess (response, file) {
      this.$message({
        message: response.msg,
        type: 'success'
      })
      this.currentRow.photoId = response.obj
      this.createdSrc = returnThumbnailUrl(response.obj)
    },
    // 图片上传失败
    handleLogoFailed () {
      this.$message({
        message: '图片上传失败',
        type: 'error'
      })
    },
    // 对上传图片做限制
    beforeLogoUpload (file) {
      let isImage = file.type.substring(0, 5) === 'image'
      if (!isImage) {
        this.$message.error('只能上传图片')
        return false
      }
      return true
    },

    /*
     * 刷新
     * */
    refresh () {
      this.$refs.page.refresh()
    },
    /*
      * 搜索
      * */
    search () {
      this.$refs.page.search()
    },
    /*
      * 重置
      * */
    reset () {
      this.queryList = {
        name: '',
        email: '',
        phone: ''
      }
      this.$refs.page.search()
    }
  }
}
</script>

<style scoped>
  body .el-table th.gutter {
    display: table-cell !important;
  }
  .logo-uploader {
    display: inline-block;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .logo-uploader:hover {
    border-color: #409EFF;
  }

  .logo-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .logo {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



