import request from '@/utils/request'
import qs from 'qs'
// 优惠券列表
export function couponList (
  actStatus,
  pageSize,
  pageNumber,
  actTimeStart,
  actTimeEnd,
  type
) {
  return request({
    url: '/member/coupon/list_re',
    method: 'get',
    jsonHeader: true,
    params: {
      actStatus,
      pageSize,
      pageNumber,
      actTimeStart,
      actTimeEnd,
      type
    }
  })
}
// 满减活动
export function activityList (
  status,
  startTime,
  endTime,
  pageNumber,
  pageSize
) {
  return request({
    url: '/merchant/member/rule',
    method: 'get',
    jsonHeader: true,
    params: {
      status,
      startTime,
      endTime,
      pageNumber,
      pageSize
    }
  })
}
// 上传
export function uploadIMG (file, userID) {
  return request({
    url: '/member/sem/upload/imageOrVideo',
    method: 'POST',
    params: {
      file, userID
    }
  })
}
// 新增规则
export function addRule (ruleData) {
  return request({
    // url: '/member/sem/rule/save',
    url: '/merchant/member/rule',
    method: 'post',
    data: ruleData,
    jsonHeader: true
  })
}
// 新增优惠券
export function coupon (couponData) {
  return request({
    url: '/member/coupon/save_re',
    method: 'post',
    data: qs.stringify(
      couponData
    )
  })
}
// 删除
export function delRule (ids) {
  return request({
    url: '/member/coupon/logical_del',
    method: 'post',
    data: qs.stringify({
      ids
    })
  })
}
// 改变状态
export function changeStatus (ruleData) {
  return request({
    url: '/member/sem/rule/changeStatus',
    method: 'post',
    data: ruleData,
    jsonHeader: true
  })
}
// 查找店铺 门店列表
export function shopTable (name) {
  console.log(name, 'name')
  return request({
    url: '/member/sem/coupon/findStore',
    method: 'get',
    params: {
      name
    }
  })
}



