<template>
  <div class="newCommission">
    <el-card class="card-box">
      <div class="tab_header_bar">
        <div class="block">
          <el-input size="small"
                    v-model="name"
                    placeholder="请输入手机号或用户名"></el-input>
        </div>
        <div class="block">
          按首笔交易：
          <el-date-picker type="date"
                          size="small"
                          v-model="startTime"
                          placeholder="开始日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd"
                          style="width: 190px"></el-date-picker>
          <span>--</span>
          <el-date-picker type="date"
                          size="small"
                          v-model="endTime"
                          placeholder="结束日期"
                          format="yyyy 年 MM 月 dd 日"
                          value-format="yyyy-MM-dd"
                          style="width: 190px"></el-date-picker>
        </div>
        <div class="block"
             style="margin-left: 10px">
          <el-button size="mini"
                     type="primary">查 询</el-button>
        </div>
        <div class="block"
             style="margin-left: 10px">
          <el-button size="mini"
                     type="primary">重 置</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <el-table :data="tableData"
                style="width: 100%">
        <el-table-column prop="time"
                         label="手笔交易时间"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="orderNo"
                         label="用户名"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="payTime"
                         label="手机号"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="pay"
                         sortable
                         label="首笔消费"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="pay"
                         sortable
                         label="其他商户消费"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="pay"
                         sortable
                         label="返佣金额"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="操作"
                         :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <el-button @click="toDetail(scope.row.id)"
                       type="text"
                       size="small">详情<i class="el-icon-arrow-right"></i></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- <pagination :total-elements="tableTotalElements" :change-callback="getOrderList" ref="page"></pagination> -->
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      name: '',
      startTime: '',
      endTime: '',
      tableData: [{
        time: '2019-10-19'
      }],
      tableTotalElements: 0
    }
  },
  methods: {
    toDetail (id) {
      this.$router.push({ path: '/merCommission/views/commissionDetail', query: { id: id } })
    }
  }
}
</script>
<style scoped>
.newCommission {
  padding: 30px;
}
.tab_header_bar {
  margin-bottom: 10px;
}

.block {
  display: inline-block;
  vertical-align: middle;
}
</style>



