import request from '@/utils/request'
import qs from 'qs'

export function addactive1(names) {
  console.log(names)
  return request({
    url: '/member/member/level/name_valid',
    method: 'post',
    data: names,
    jsonHeader: true
  })
}

export function addactive2(names) {
  console.log(names)
  return request({
    url: '/member/member/level/amount_valid',
    method: 'post',
    data: names,
    jsonHeader: true
  })
}

export function addactive3(names) {
  console.log(names)
  return request({
    url: '/member/member/level/save_rewrite',
    method: 'post',
    data: names,
    jsonHeader: true
  })
}

export function vipList() {
  return request({
    url: '/member/member/level/list',
    method: 'get',
    params: {

    }
  })
}

// 删除
export function deleteList(ids) {
  console.log(ids, "ids")
  return request({
    url: '/member/member/level/delete',
    method: 'post',
    data: qs.stringify({
      ids: ids
    }
    )

  })
}
// 修改
export function updateList(names) {

  return request({
    url: '/member/member/level/update',
    method: 'post',
    jsonHeader: true,
    data: names

  })
}
// // // // // // // // // // // // // 

export function payruleList() {
  return request({
    url: '/merchant/member/payrule',
    method: 'get',
    params: {

    }
  })
}
// 保存
export function setpayrule(integralRule, integralUse, rechargeUse, simultaneouslyUse) {

  return request({
    url: '/merchant/member/payrule',
    method: 'post',
    data: {
      integralRule, integralUse, rechargeUse, simultaneouslyUse
    },
    jsonHeader: true
  })
}


