import request from '@/utils/request'
// 获取设备列表
export function getDevice (pageNumber, pageSize, queryList) {
  return request({
    url: '/auth/equipment',
    method: 'get',
    params: {
      pageNumber: pageNumber,
      pageSize: pageSize,
      keyWord: queryList.keyWords,
      type: queryList.type
    }
  })
}
// 获取流水清单的笔数
export function getDeviceDetail (pageNumber, pageSize, searchData, id) {
  return request({
    url: '/auth/equipment/summary',
    method: 'get',
    params: {
      pageNumber: pageNumber,
      pageSize: pageSize,
      orderNum: searchData.orderNum,
      staffName: searchData.staffName,
      beginTime: searchData.beginTime,
      endTime: searchData.endTime,
      payType: searchData.payType,
      equipmentId: id
    }
  })
}
export function getDeviceDetailList (pageNumber, pageSize, searchData, id) {
  return request({
    url: '/auth/equipment/detail',
    method: 'get',
    params: {
      pageNumber: pageNumber,
      pageSize: pageSize,
      orderNum: searchData.orderNum,
      staffName: searchData.staffName,
      beginTime: searchData.beginTime,
      endTime: searchData.endTime,
      payType: searchData.payType,
      equipmentId: id
    }
  })
}
export function getDeviceDetailListExport (pageNumber, pageSize, searchData, id) {
  return request({
    url: '/auth/equipment/export',
    method: 'get',
    params: {
      pageNumber: pageNumber,
      pageSize: pageSize,
      orderNum: searchData.orderNum,
      staffName: searchData.staffName,
      beginTime: searchData.beginTime,
      endTime: searchData.endTime,
      payType: searchData.payType,
      equipmentId: id

    }
  })
}
export function geteditModeSubmit (mode, id) {
  return request({
    url: '/auth/equipment/mode',
    method: 'put',
    params: {
      mode: mode,
      id: id
    }
  })
}



